cc.Class({
    extends: require('SlotGameWriter'),

    makeScriptResume() {
        const { normalGameMatrix, normalGamePaylines, freeGameRemain,
            freeGameMatrix, winAmountPS, betId } = this.node.gSlotDataStore.playSession;
        const { TABLE_FORMAT, TABLE_FORMAT_FREE } = this.node.config;
        const normalSpinMatrix = this.node.gSlotDataStore.convertSlotMatrix(normalGameMatrix, TABLE_FORMAT);
        const normalPayLines = this.node.gSlotDataStore.convertPayLineAll(normalGamePaylines);
        const {steps} = this.node.gSlotDataStore.slotBetDataStore.data;
        const listBet = String(betId).split('');
        const betIndex = listBet[0];
        const betValue = steps[betIndex];
        const isFreeGame = freeGameRemain && freeGameRemain > 0;
        const { promotion, promotionRemain, promotionTotal } = this.node.gSlotDataStore;

        let listScript = [];

        listScript.push({
            command: "_updateBet",
            data: betValue
        });
        listScript.push({
            command: "_disableBet",
        });
        listScript.push({
            command: "_showTrialButtons",
            data: false
        });
        listScript.push({
            command: "_updateBet",
            data: betValue
        });
        listScript.push({
            command: "_updateMatrix",
            data: {matrix: normalSpinMatrix},
        });
        listScript.push({
            command: "_setUpPaylines",
            data: {matrix: normalSpinMatrix, payLines: normalPayLines},
        });
        if (winAmountPS && winAmountPS > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: {winAmount: winAmountPS, time: 0}
            });
        }
        if (isFreeGame) {
            let freeSpinMatrix = normalSpinMatrix;
            if (freeGameMatrix) {
                freeSpinMatrix = this.node.gSlotDataStore.convertSlotMatrix(freeGameMatrix, TABLE_FORMAT_FREE);
            }
            listScript.push({
                command: "_showWildPayLine",
            });
            listScript.push({
                command: "_showIntroFreeGame",
                data: {
                    name: "IntroFreeGame",
                    content: {
                        isResume: true
                    }
                }
            });
            listScript.push({
                command: "_newGameMode",
                data: {name: "freeGame", data: freeSpinMatrix,},
            });  
            listScript.push({
                command: "_resumeGameMode",
                data: {name: "normalGame",},
            });
        }
        if (normalPayLines && normalPayLines.length > 0) {
            listScript.push({
                command: "_showNormalPayline",
            });
        } else {
            listScript.push({
                command: "_clearPaylines",
            });
        }
        listScript.push({
            command: "_gameFinish"
        });
        listScript.push({
            command: "_gameRestart"
        });

        if(promotion === true && promotionRemain && promotionTotal && promotionRemain >0) {
            listScript.push({
                command: "_showPromotionPopup",
            });
        }

        return listScript;
    },

    makeScriptResultReceive() {
        const {matrix, jackpot, neuro, normalSubSymbol: subSymbol, sureWin} = this.node.gSlotDataStore.playSession;
        let listScript = [];
        if (jackpot) {
            const jpInfo = jackpot[jackpot.length - 1].split(';');
            const jpType = jpInfo[0].split('_')[2];
            listScript.push({
                command: "_updateValueJP",
                data: {
                    jpValue: Number(jpInfo[1]),
                    jpType: jpType.includes('GRAND') ? 0 : 1,
                }
            });
            listScript.push({
                command: "_pauseUpdateJP"
            });
        }
        if (sureWin) {
            listScript.push({
                command: "_playAnimSureWin",
            });
        }
        if (neuro) {
            listScript.push({
                command: "_setupEuroBanner",
                data: neuro
            });
        }
        listScript.push({
            command: "_resultReceive",
            data: { matrix, subSymbol }
        });

        listScript.push({
            command: "_showResult",
            data: matrix,
        });

        return listScript;
    },

    makeScriptShowResults() {
        const {  matrix, winAmount, payLines, freeGame, winAmountPS, jackpot, sureWin, neuro, normalSubSymbol, isFinished } = this.node.gSlotDataStore.playSession;
        const {currentBetData} = this.node.gSlotDataStore.slotBetDataStore.data;
        const listScript = [];
        const isBigwin = winAmount && winAmount >= currentBetData * 20 && !jackpot;
        this.isFastResult = false;

        listScript.push({
            command: "_setUpPaylines",
            data: {matrix, payLines},
        });
        listScript.push({
            command: "_pauseWallet"
        });

        if (neuro) {
            if (jackpot) {
                listScript.push({
                    command: "_openAllBannersSync",
                });
            } else {
                listScript.push({
                    command: "_openAllBanners",
                });
            }
        }
        
        const rate = winAmount ? winAmount / currentBetData : 0;
        if (rate >= 1 && !sureWin) {
            listScript.push({
                command: "_playLightWin",
            });
        }

        if (jackpot || freeGame || rate >= 5) {
            listScript.push({
                command: "_playAnimGirls",
            });
        }
        //TODO: jackpot
        if (jackpot) {
            const jpInfo = jackpot[jackpot.length - 1].split(';');
            const jpType = jpInfo[0].split('_')[2];
            listScript.push({
                command: "_showIntroJackpot",
                data: normalSubSymbol
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data:{
                    name: "JackpotWin",
                    content: {
                        winAmount: Number(jpInfo[1]),
                        jackpotType: jpType.includes('GRAND') ? 0 : 1,
                        currentBetData
                    }
                }
            });
            listScript.push({
                command: "_showAllSubSymbol",
            });
            listScript.push({
                command: "_resumeUpdateJP",
            });
        } else if (isBigwin) {
            listScript.push({
                command: "_blinkAllPaylineSync",
            });
            listScript.push({
                command: "_showCutscene",
                data: {
                    name: "WinEffect",
                    content: {
                        winAmount,
                        currentBetData
                    }
                }
            });
        }

        listScript.push({
            command: "_scaleSubSymbol",
        });

        if (freeGame && freeGame > 0) {
            const {spinTimes} = this.node.gSlotDataStore;
            if (winAmountPS && winAmountPS > 0) {
                listScript.push({
                    command: '_updateWinningAmount',
                    data: {winAmount: winAmountPS, time: 10}
                });
            } else {
                listScript.push({
                    command: '_clearWinAmount'
                });
            }
            listScript.push({
                command: "_showWildPayLine",
            });
            listScript.push({
                command: "_showIntroFreeGame",
                data: {
                    name: "IntroFreeGame",
                    content: {
                        isResume: false
                    }
                }
            });
            
            listScript.push({
                command: "_newGameMode",
                data: {name: "freeGame", data: matrix},
            });
            listScript.push({
                command: "_resumeGameMode",
                data: {name: "normalGame",},
            });
            if (spinTimes && spinTimes > 0) {
                listScript.push({
                    command: "_resumeSpinTime",
                    data: spinTimes,
                });
            }
        }
        if (payLines && payLines.length > 0) {
            if (!isBigwin) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
            }
            listScript.push({
                command: "_showNormalPayline",
            });
        } else {
            listScript.push({
                command: "_clearPaylines",
            });
        }
        listScript.push({
            command: "_gameEnd"
        });
        listScript.push({
            command: "_gameFinish"
        });
        listScript.push({
            command: "_gameRestart"
        });
        return listScript;
    },

    makeScriptGameRestart() {
        const listScript = [];
        const {freeGameRemain, winAmountPS} = this.node.gSlotDataStore.playSession;
        const {spinTimes, promotion, promotionRemain} = this.node.gSlotDataStore;

        this.scriptUpdateWinAmount(listScript);
        if (promotion && promotion > 0) {
            listScript.push({
                command: "_showTrialButtons",
                data: false,
            });
            listScript.push({
                command: "_resetPromotionButtons"
            });
        }

        if (spinTimes && spinTimes > 0) {
            if(freeGameRemain && freeGameRemain > 0){
                listScript.push({
                    command: "_runAutoSpin"
                });
            }else if(!promotion){
                listScript.push({
                    command: "_runAutoSpin"
                });
            }
        } else {
            if (!promotionRemain || promotionRemain <= 0) {
                listScript.push({
                    command: '_enableBet'
                });
                listScript.push({
                    command: "_exitPromotionMode"
                });
                listScript.push({
                    command: "_showTrialButtons",
                    data: true
                });
            }
        }
        if(this.node.mainDirector.trialMode && winAmountPS){
            listScript.push({
                command: '_updateTrialWallet',
                data: winAmountPS,
            });
        }
        listScript.push({
            command: "_runAsyncScript",
        });
        return listScript;
    },

    scriptUpdateWinAmount(listScript) {
        const {winAmountPS, winAmount} = this.node.gSlotDataStore.playSession;
        const {gameSpeed, modeTurbo} = this.node.gSlotDataStore;
        const isFTR = gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        if (winAmount && winAmount > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: {winAmount: winAmountPS, time: isFTR || modeTurbo ? 1 : 50, isFTR, rate: this._checkRateSoundWin()}
            });
        }
        listScript.push({
            command: "_resumeWallet",
        });
    },

    _checkRateSoundWin() {
        const { winAmount } = this.node.gSlotDataStore.playSession;
        const { currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        if (winAmount < currentBetData) return 0;
        if (winAmount <= currentBetData * 5) return 1;
        if (winAmount < currentBetData * 20) return 2;
    },

    makeScriptGameFinish() {
        return [];
    },


    makeScriptSpinByTimes(times) {
        this.node.gSlotDataStore.isAutoSpin = true;
        this.node.gSlotDataStore.spinTimes = times;
        this.node.gSlotDataStore.firstAutoSpin = true;
        return [
            {
                command: "_runAutoSpin",
            },
        ];
    },

    makeScriptUpdateWalletData(wallet) {
        const listScript = [];
        const {wallet: currentWallet} = this.node.gSlotDataStore.slotBetDataStore.data;
        if (currentWallet !== wallet && !this.node.gSlotDataStore.isUpdateWinAmount) {
            listScript.push({
                command: "_updateWallet",
            });
            this.node.gSlotDataStore.slotBetDataStore.updateWallet(wallet);
        }
        return listScript;
    },
});