const { formatMoney } = require('utils');
const { reverseEasing } = require('globalAnimationLibrary');
cc.Class({
    extends: require('JackpotWinv2'),
    properties: {
        animation: sp.Skeleton,
        animationFrame: sp.Skeleton,
    },
    onLoad() {
        this._super();
        /*//!test
        window._testJackPot = () => {
            const content = {
                winAmount: 1000000000,
            }
            this.play(content);
        };
        //*/
    },
    enter() {
        this.animation.node.active = true;
        this.animation.setAnimation(0, 'Appear', false);
        this.animation.addAnimation(0, 'Idle', true)
        this.animationFrame.node.active = true;
        this.animationFrame.setAnimation(0, 'Appear_TextOnly', false);
        this.animationFrame.addAnimation(0, 'Idle_TextOnly', true)
        this.initValue();
        this.label.string = '';
        this.label.node.opacity = 0;
        this.skippable = false;
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = cc.tween(this.node)
            .delay(1)
            .call(() => {
                this.label.node.opacity = 255;
                this.playSoundStart();
                const scaleTime = 0.2;
                this.winAmount.runAction(cc.repeatForever(cc.sequence(
                    cc.scaleTo(scaleTime, 1.2),
                    cc.scaleTo(scaleTime, 1),
                )));
                this.startParticle();
                this.startUpdateWinAmount();
                this.skippable = true;
            })
            .delay(20)
            .call(() => {
                this.finish();
                this.tweenShow = null;
            })
            .start();
    },
    finish() {
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = null;
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmount.stopAllActions();
        this.playSoundEnd();
        this.stopParticle();
        this.winInfo.runAction(cc.sequence(
            cc.delayTime(this.delayShowTime),
            cc.callFunc(() => {
                this.animation.setAnimation(0, 'Disappear', false);
                this.animationFrame.setAnimation(0, 'Disappear_TextOnly', false);
            }),
            cc.scaleTo(this.hideTime, 0),
            cc.callFunc(() => {
                this.label.string = '';
                if (this.node.soundPlayer) {
                    this.node.soundPlayer.stopAllAudio();
                    this.node.soundPlayer.playMainBGM();
                }
                this.exit();
            })
        ));
    },
    playSoundStart() {
        this.node.soundPlayer.stopAllAudio();
        this.node.soundPlayer.playSfxByID('9881_sfx_jp');
        this.node.soundPlayer.playSoundByID('9881_sfx_counting', true);
    },
    playSoundEnd() {
        this.node.soundPlayer.stopSoundByID('9881_sfx_jp');
        this.node.soundPlayer.stopSoundByID('9881_sfx_counting');
        this.node.soundPlayer.playSfxByID('9881_sfx_jp_end');
    },

    startUpdateWinAmount() {
        const speedUpEasing = t => t * t; // constant accelerated
        const slowDownEasing = reverseEasing(t => t * t);
        const halfAmount = 0.5 * this.content.winAmount;
        const extendFinishDelayTime = this.node.gSlotDataStore && this.node.gSlotDataStore.isAutoSpin ? this.extendFinishDelayTime : 0;
        this.currentTween = cc.tween(this);
        this.currentTween
            .to(0.5 * this.animDuration, { currentValue: halfAmount }, { easing: speedUpEasing })
            .to(0.5 * this.animDuration, { currentValue: this.content.winAmount }, { easing: slowDownEasing })
            .call(() => {
                this.node.soundPlayer.stopSoundByID('9881_sfx_counting');
                this.node.soundPlayer.playSfxByID('9881_sfx_counting_end');
            })
            .delay(extendFinishDelayTime)
            .call(() => {
                if (this.node.gSlotDataStore && this.node.gSlotDataStore.isAutoSpin) {
                    this.skippable = false;
                    this.currentTween = null;
                    this.finish();
                }
            });
        this.currentTween.start();
    },

    onClick() {
        if (!this.isUpdating || this.speedUp) return;
        if (!this.skippable) return;

        this.skippable = false;
        this.speedUp = true;
        this.winAmount.stopAllActions();
        const scaleTime = 0.13;
        this.winAmount.runAction(cc.repeatForever(cc.sequence(
            cc.scaleTo(scaleTime, 1.2),
            cc.scaleTo(scaleTime, 1),
        )));
        this.currentTween && this.currentTween.stop();
        this.currentTween = null;
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = cc.tween(this)
            .to(1, { currentValue: this.content.winAmount })
            .call(() => {
                this.finish();
                this.tweenShow = null;
            })
            .start();
    },
    bindQuickShow() { },
});