const StateGameMode = {
    NormalGame: 1,
    FreeGame: 2,
    FreespinOption: 3,
    BonusGame: 4,
    Others: 9,
};

cc.Class({
    extends: require('SlotGameWriter'),

    makeScriptResultReceive() {
        const { matrix, jackpot, state } = this.node.gSlotDataStore.playSession;

        let listScript = [];

        if (jackpot && state !== StateGameMode.FreespinOption) {
            const jpInfo = jackpot[jackpot.length - 1].split(';');
            const jpType = jpInfo[0].split('_')[2];
            listScript.push({
                command: "_updateValueJP",
                data: {
                    jpValue: Number(jpInfo[1]),
                    jpType: jpType,
                }
            });
            listScript.push({
                command: "_pauseUpdateJP"
            });
        }

        listScript.push({
            command: "_resultReceive",
            data: matrix,
        });

        listScript.push({
            command: "_showResult",
            data: matrix,
        });

        return listScript;
    },

    makeScriptShowResults() {
        const { state, matrix, winAmount, payLines, payLineJackPot, freeSpinOption, jackpot,
            bonusGame, freeGame, winAmountPS, freeGameRemain,
        } = this.node.gSlotDataStore.playSession;

        const { currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        const listScript = [];
        const isBigwin = winAmount && winAmount >= currentBetData * 20 && !isJackpotWin;
        const isJackpotWin = jackpot && jackpot.length > 0;
        this.isFastResult = false;

        if (state === StateGameMode.FreespinOption) {
            listScript.push({
                command: "_hideCutscene",
                data: {
                    name: "FreeGameOption",
                }
            });
        } else {
            listScript.push({
                command: "_setUpPaylines",
                data: { matrix, payLines },
            });
        }

        if (isJackpotWin) {
            listScript.push({
                command: "_showJackpotPayLine",
                data: payLineJackPot,
            });
            const jpInfo = jackpot[jackpot.length - 1].split(';');
            const jpType = jpInfo[0].split('_')[2];
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "JackpotWin",
                    content: {
                        winAmount: Number(jpInfo[1]),
                        jackpotType: jpType
                    }
                }
            });
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: Number(jpInfo[1]), time: 100 }
            });
            listScript.push({
                command: "_resumeUpdateJP",
            });
        } else if (isBigwin) {
            listScript.push({
                command: "_blinkAllPaylines",
            });
            listScript.push({
                command: "_showCutscene",
                data: {
                    name: "WinEffect",
                    content: {
                        winAmount,
                        currentBetData
                    }
                }
            });
        }
        if (state == StateGameMode.NormalGame) {
            const { spinTimes } = this.node.gSlotDataStore;
            if (bonusGame && bonusGame > 0) {
                listScript.push({ command: '_updateLastWin', data: false });
                if (winAmount && winAmount > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                } else {
                    listScript.push({
                        command: '_clearWinAmount'
                    });
                }
                listScript.push({
                    command: "_showBonusPayLine",
                });
                listScript.push({
                    command: "_newGameMode",
                    data: { name: "bonusGame", },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: "normalGame", },
                });
                if (!freeGame && spinTimes && spinTimes > 0) {
                    listScript.push({
                        command: "_resumeSpinTime",
                        data: spinTimes,
                    });
                }
            }

            if ((freeSpinOption && freeSpinOption > 0) || (freeGame && freeGame > 0)) {
                const { spinTimes } = this.node.gSlotDataStore;
                listScript.push({
                    command: '_updateLastWin',
                    data: false
                });
                if (!bonusGame) {
                    if (winAmountPS && winAmountPS > 0) {
                        listScript.push({
                            command: '_updateWinningAmount',
                            data: { winAmount: winAmountPS, time: 10 }
                        });
                    } else {
                        listScript.push({
                            command: '_clearWinAmount'
                        });
                    }
                }
                listScript.push({
                    command: "_showScatterPayLine",
                });
                if (freeSpinOption && freeSpinOption > 0) {
                    listScript.push({
                        command: "_showCutscene",
                        data: {
                            name: "FreeGameOption"
                        }
                    });
                }
                listScript.push({
                    command: "_newGameMode",
                    data: { name: "freeGame", data: matrix },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: "normalGame", },
                });

                if (spinTimes && spinTimes > 0) {
                    listScript.push({
                        command: "_resumeSpinTime",
                        data: spinTimes,
                    });
                }
            }
            if (payLines && payLines.length > 0) {
                if (!isBigwin) {
                    listScript.push({
                        command: "_blinkAllPaylines",
                    });
                }
                listScript.push({
                    command: "_showNormalPayline",
                });
            }
            else {
                listScript.push({
                    command: "_clearPaylines",
                });
            }
            listScript.push({
                command: "_gameEnd"
            });
            listScript.push({
                command: "_gameFinish"
            });
            listScript.push({
                command: "_gameRestart"
            });
        } else if (state == StateGameMode.FreeGame) {
            if (bonusGame && bonusGame > 0) {
                if (winAmount && winAmount > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                }
                listScript.push({
                    command: "_showBonusPayLine",
                });
                listScript.push({
                    command: "_newGameMode",
                    data: { name: "bonusGame", },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: "freeGame", },
                });
            }

            if (payLines && payLines.length > 0) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
                listScript.push({
                    command: "_showFreePayline",
                });
            }
            else {
                listScript.push({
                    command: "_clearPaylines",
                });
            }
            listScript.push({
                command: "_gameEnd"
            });

            if (!freeGameRemain || freeGameRemain <= 0) {
                if (winAmountPS && winAmountPS > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                }
                listScript.push({
                    command: "_delayTimeScript",
                    data: 0.3
                });
                listScript.push({
                    command: "_showUnskippedCutscene",
                    data: {
                        name: "TotalWinPanel",
                        content: {}
                    }
                });
                listScript.push({
                    command: "_gameExit",
                });
            } else {
                listScript.push({
                    command: "_gameRestart"
                });
            }
        }
        return listScript;
    },

    makeScriptGameRestart() {
        const listScript = [];
        const {freeGameRemain} = this.node.gSlotDataStore.playSession;
        const {spinTimes, promotion, promotionRemain} = this.node.gSlotDataStore;

        this.scriptUpdateWinAmount(listScript);
        if (promotion && promotion > 0) {
            listScript.push({
                command: "_showTrialButtons",
                data: false,
            });
            listScript.push({
                command: "_resetPromotionButtons"
            });
        }

        if (spinTimes && spinTimes > 0) {
            if(freeGameRemain && freeGameRemain > 0){
                listScript.push({
                    command: "_runAutoSpin"
                });
            }else if(!promotion){
                listScript.push({
                    command: "_runAutoSpin"
                });
            }
        } else {
            if (!promotionRemain || promotionRemain <= 0) {
                listScript.push({
                    command: '_enableBet'
                });
                listScript.push({
                    command: "_exitPromotionMode"
                });
                listScript.push({
                    command: "_showTrialButtons",
                    data: true
                });
            }
        }
        listScript.push({
            command: "_runAsyncScript",
        });
        return listScript;
    },

    scriptUpdateWinAmount(listScript) {
        const { winAmount, winAmountPS } = this.node.gSlotDataStore.playSession;
        if (winAmount && winAmount > 0) {
            if (winAmountPS == winAmount) {
                listScript.push({
                    command: "_clearWinAmount"
                });
            }
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: 300 }
            });
        }
    },
});
