const { randRange, getRandomElement } = require('utils');
const fakeFormats = [3, 9, 4, 9, 5, 9, 3, 4, 5];
cc.Class({
    extends: require('SlotTablev2'),

    onLoad() {
        this._super();
        this.init();
        this.showNumber = this.node.config.TABLE_FORMAT[0];
        this.stackSize = 6;
        this._testIndex = 0;
    },
    /**
     * @public 
     */
    startSpinning() {
        this._super();
        this._testResultReceive();
    },
    setupStack(stackFormats = null) {
        this.stackFormats = stackFormats;
        this.node.reels.forEach((reel, col) => reel.processStack(this.stackFormats[col]));
    },
    setMode(evt, mode) {
        this.node.mode = mode || evt;
    },
    fastToResult() {
        if (this.stopSpinningCallbackCount >= 5) return;
        if (!this.stackFormats) return;
        if (this.isFastToResult) return;
        this.isFastToResult = true;
        const remainSteps = this.stackFormats.map(format => format.step);
        const maxStep = Math.max.apply(null, remainSteps);
        this.node.reels.forEach(slotReel => slotReel.fastStopSpinning(maxStep));
    },
    /**
     * @private 
     */
    _processStack(formats) {
        const stackFormats = formats.map((format) => {
            if (format === 9)
                format = getRandomElement(fakeFormats);
            // format = 0 // !hard code test
            const step = this._getStep(format);
            return { format, step };
        });
        // console.table(stackFormats);
        return stackFormats;
    },
    _getStep(format) {
        if (format === 9) return null;
        if (format > 0) return format + this.stackSize;
        if (format < 0) return this.showNumber + format;
        if (format === 0) return randRange(this.showNumber, this.stackSize);
    },
    /**
     * @TEST 
     * @fakeData
     */
    _testResultReceive() {
        this.scheduleOnce(() => {
            this.setupStack(this._getTestFormats());
            const matrix = this._getTestMatrix();
            this.stopSpinning(matrix, () => { });
        }, 0.2);
    },
    _getTestFormats() {
        const testFormats = [
            [9, 9, 9, 9, 9],
            [9, 9, 9, 9, 9],
        ];
        let format = testFormats[this._testIndex %= testFormats.length];
        this._testIndex++;
        return this._processStack(format);
    },

    _getTestMatrix() {
        return [
            ["3", "A", "R"],
            ["A", "3", "3"],
            ["A", "2", "4"],
            ["3", "2", "3"],
            ["2", "3", "7"]
        ];
    },


});
