cc.Class({
    extends: require('SlotGameWriter'),


    makeScriptResume() {
        const { normalGameMatrix, normalGamePaylines, freeGameRemain,
            freeGameMatrix, winAmountPS, betId, multipleFree, freeMatrix0, bigWild } = this.node.gSlotDataStore.playSession;
        const normalPayLines = this.node.gSlotDataStore.convertPayLine(normalGamePaylines);
        const { steps } = this.node.gSlotDataStore.slotBetDataStore.data;
        const listBet = String(betId).split('');
        const betIndex = listBet[0];
        const betValue = steps[betIndex];
        const isFreeGame = (freeGameRemain && freeGameRemain > 0);
        const { promotion, promotionRemain, promotionTotal } = this.node.gSlotDataStore;

        let listScript = [];
        listScript.push({
            command: "_updateBet",
            data: betValue
        });
        listScript.push({
            command: "_disableBet",
        });
        listScript.push({
            command: "_showTrialButtons",
            data: false
        });
        listScript.push({
            command: "_updateBet",
            data: betValue
        });
        if (winAmountPS && winAmountPS > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: 300 }
            });
        }
        listScript.push({
            command: "_updateMatrix",
            data: { matrix: normalGameMatrix },
        });
        listScript.push({
            command: "_setUpPaylines",
            data: { matrix: normalGameMatrix, payLines: normalPayLines, rate: 0, isResume: true },
        });
        if (isFreeGame) {
            listScript.push({
                command: "_pauseWallet",
                data: true
            });
            let freeSpinMatrix = normalGameMatrix;
            if (freeGameMatrix) {
                freeSpinMatrix = freeGameMatrix;
            }
            listScript.push({
                command: "_showScatterPayLine",
            });
            if (freeGameRemain && freeGameRemain > 0)
                listScript.push({
                    command: "_newGameMode",
                    data: {
                        name: "freeGame",
                        data: {
                            matrix: freeSpinMatrix,
                            isResume: true,
                            multipleFree,
                            freeMatrix0,
                            freeGameRemain,
                            bigWild
                        }
                    },
                });
            listScript.push({
                command: "_resumeGameMode",
                data: { name: "normalGame", },
            });
            listScript.push({
                command: "_pauseWallet",
                data: false
            });
            listScript.push({
                command: "_updateWallet"
            });
        }
        if (normalPayLines && normalPayLines.length > 0) {
            listScript.push({
                command: "_showNormalSymbolPayLine",
            });
        } else {
            listScript.push({
                command: "_clearPaylines",
            });
        }
        listScript.push({
            command: "_gameFinish"
        });
        listScript.push({
            command: "_gameRestart"
        });
        if (promotion === true && promotionRemain && promotionTotal && promotionRemain > 0) {
            listScript.push({
                command: "_showPromotionPopup",
            });
        }
        return listScript;
    },
    makeScriptResultReceive() {
        const { matrix, jpInfo, fishInfo, bellProgress } = this.node.gSlotDataStore.playSession;

        let listScript = [];
        if (jpInfo) {
            listScript.push({
                command: "_pauseUpdateJP"
            });
        }
        listScript.push({
            command: "_movingWild",
            data: fishInfo,
        });
        listScript.push({
            command: "_updateBellProgress",
            data: bellProgress,
        });
        listScript.push({
            command: "_resultReceive",
            data: { matrix },
        });
        listScript.push({
            command: "_showResult",
            data: matrix,
        });

        return listScript;
    },
    makeScriptShowResults() {
        const { matrix, winAmount, payLines, freeGame, isLastStepTutorial,
            winAmountPS, multipleFree } = this.node.gSlotDataStore.playSession;
        const { currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        const totalBet = this._getTotalBetAmount();
        const isSessionEnded = !freeGame;
        const isBigWin = winAmount && winAmount >= currentBetData * 20;
        const { isAutoSpin, modeTurbo, canCountTrial } = this.node.gSlotDataStore;
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        const rate = winAmount / totalBet;
        const timeUpdateWinAmt = isFTR || freeGame ? 0 : this._getTimeUpdateWinAmount(rate) * 1000;
        let hadGameRestart = false;
        let hadGameFinish = false;

        const listScript = [];
        listScript.push({
            command: "_pauseWallet",
            data: true
        });
        listScript.push({
            command: "_setUpPaylines",
            data: { matrix, payLines, rate, isResume: false },
        });
        if (isBigWin) {
            if (modeTurbo || isFTR) {
                listScript.push({
                    command: "_updateWinningAmount",
                    data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
                });
            }
            listScript.push({
                command: "_blinkAllPaylines",
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "WinEffect",
                    content: {
                        winAmount,
                        currentBetData,
                        matrix,
                        rate,
                    }
                }
            });
            if (!(modeTurbo || isFTR)) {
                listScript.push({
                    command: "_updateWinningAmount",
                    data: { winAmount: winAmountPS, time: 300, rate }
                });
            }
            listScript.push({
                command: "_pauseWallet",
                data: false
            });
            listScript.push({
                command: "_updateWallet"
            });
        }
        if (!isBigWin && payLines) {
            if (!freeGame) {
                listScript.push({
                    command: "_playSoundWinLine",
                    data: rate
                });
                listScript.push({
                    command: "_updateWinningAmount",
                    data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
                });
                listScript.push({
                    command: "_blinkAllPaylines",
                });
                listScript.push({
                    command: "_pauseWallet",
                    data: false
                });
                listScript.push({
                    command: "_updateWallet"
                });
            }
            if (isSessionEnded && !isAutoSpin && !hadGameRestart) {
                listScript.push({
                    command: "_gameRestart"
                });
                hadGameRestart = true;
            }
        }
        if (freeGame) {
            const { spinTimes } = this.node.gSlotDataStore;
            listScript.push({
                command: "_playSoundWinLine",
                data: 2
            });
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: 0, rate: 2 }
            });
            if (isFTR || modeTurbo) {
                listScript.push({
                    command: "_delayTimeScript",
                    data: 1
                });
            }
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "IntroFreeGame",
                    content: {}
                }
            });
            listScript.push({
                command: "_newGameMode",
                data: { name: "freeGame", data: { matrix: matrix, multipleFree, isResume: false } },
            });
            listScript.push({
                command: "_resumeGameMode",
                data: { name: "normalGame", },
            });
            if (spinTimes && spinTimes > 0) {
                listScript.push({
                    command: "_resumeSpinTime",
                    data: spinTimes,
                });
            }
            if (!isAutoSpin && !hadGameRestart) {
                if (!modeTurbo) {
                    listScript.push({
                        command: "_gameRestart"
                    });
                    hadGameRestart = true;
                }
            }
            listScript.push({
                command: "_pauseWallet",
                data: false
            });
            listScript.push({
                command: "_updateWallet"
            });
            if (this.node.mainDirector.trialMode) {
                listScript.push({
                    command: '_gameFinish',
                });
                hadGameFinish = true;
            }
        }
        if ((!isAutoSpin && !hadGameRestart)) {
            listScript.push({
                command: "_gameRestart"
            });
            hadGameRestart = true;
        }
        if (isLastStepTutorial) {
            listScript.push({
                command: '_stopStepTrialMode',
            });
            if (!hadGameRestart) {
                listScript.push({
                    command: "_gameRestart"
                });
                hadGameRestart = true;
            }
        }
        if (canCountTrial && this.node.gSlotDataStore.countTrialSpinTime >= 20) {
            listScript.push({
                command: '_showNoticeBackToReal',
            });
            if (!hadGameRestart) {
                listScript.push({
                    command: "_gameRestart"
                });
                hadGameRestart = true;
            }
        }

        if (freeGame && isAutoSpin) {
            listScript.push({
                command: '_showBtnStopAuto',
            });
        }
        if (payLines) {
            if (freeGame) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
            }
            listScript.push({
                command: "_showNormalPayline",
            });
        }
        listScript.push({
            command: "_gameEnd"
        });
        if (!hadGameFinish) {
            listScript.push({
                command: "_gameFinish"
            });
        }
        listScript.push({
            command: "_pauseWallet",
            data: false
        });
        listScript.push({
            command: "_updateWallet"
        });
        if (!hadGameRestart) {
            listScript.push({
                command: "_gameRestart"
            });
        }
        return listScript;
    },

    makeScriptGameRestart() {
        const listScript = [];
        const { freeGameRemain } = this.node.gSlotDataStore.playSession;
        const { spinTimes, promotion, promotionRemain } = this.node.gSlotDataStore;

        this.scriptUpdateWinAmount(listScript);
        if (promotion && promotion > 0) {
            listScript.push({
                command: "_showTrialButtons",
                data: false,
            });
            listScript.push({
                command: "_resetPromotionButtons"
            });
        }

        if (spinTimes && spinTimes > 0) {
            if (freeGameRemain && freeGameRemain > 0) {
                listScript.push({
                    command: "_runAutoSpin"
                });
            } else if (!promotion) {
                listScript.push({
                    command: "_runAutoSpin"
                });
            }
        } else {
            if (!promotionRemain || promotionRemain <= 0) {
                listScript.push({
                    command: '_enableBet'
                });
                listScript.push({
                    command: "_exitPromotionMode"
                });
                listScript.push({
                    command: "_showTrialButtons",
                    data: true
                });
            }

        }
        return listScript;
    },
    makeScriptGameFinish() {
        const listScript = [];
        if (this.node.mainDirector.trialMode) {
            listScript.push({
                command: '_updateTrialWallet',
            });
        }
        return listScript;
    },
    _getTimeUpdateWinAmount(rate) {
        let time = 0;
        if (rate > 0 && rate < 1) time = 0;
        if (rate >= 1 && rate < 3) time = 0;
        if (rate >= 3 && rate < 10) time = 2.2;
        if (rate >= 10 && rate < 20) time = 4.16;
        return time
    },
    _getTimeUpdateWinAmount(rate) {
        let time = 0;
        if (rate > 0 && rate < 1) time = 0;
        if (rate >= 1 && rate < 3) time = 0;
        if (rate >= 3 && rate < 10) time = 2.2;
        if (rate >= 10 && rate < 20) time = 4.16;
        return time
    },
    scriptUpdateWinAmount(listScript) { },
});