
const { ccclass, property } = cc._decorator;
const serviceRest = require('serviceRest');
const loadConfigAsync = require('loadConfigAsync');

@ccclass
export class LoginDev extends cc.Component {
    @property({
        displayName: "List User",
        type: cc.Node,
    })
    listUser = null;

    onLoad() {
        this._bindEvents();
    }

    start () {
        // [3]
    }

    // update (deltaTime: number) {
    //     // [4]
    // }

    _bindEvents() {
        let startIdx = 100;
        let arrView = this.listUser.children;
        let length = arrView.length;
        for (let i = 0; i < length; i++) {
            let view = arrView[i];
            let userId =  arrView[i].getChildByName("Label").getComponent(cc.Label);
            const currentIndex = i + 1;
            userId.string = "User " + currentIndex;
            view.off("click");
            view.on("click", event => {
                let indexUser = startIdx + currentIndex;
                let userName = 'user' + indexUser;
                let password = 'pwduser' + indexUser;
                this.loginSuccess({userText: userName, pwText: password})
            });
        }
    }

    loginSuccess({userText, pwText}) {
        const {IS_FINISHED_REMOTE, LOBBY_SCENE_NAME, API_URL, USER_TOKEN} = loadConfigAsync.getConfig();
        if (!IS_FINISHED_REMOTE) {
            setTimeout(() => {
                this.loginSuccess({userText, pwText});
            }, 100);
            return;
        }
        const dataPost = {
            userName: userText,
            password: pwText,
            fingerPrint: 'test'
        };
        serviceRest.post({
            url: 'auth/login',
            data: dataPost,
            callback: (res) => {
                const {data: {data, error}} = res;
                if (data) {
                    cc.director.loadScene(LOBBY_SCENE_NAME);
                    cc.sys.localStorage.setItem(USER_TOKEN, data.token);
                } else if (error) {

                }
            },
            callbackErr: ()=>{

            },
            apiUrl: API_URL
        });
    }
}
