cc.Class({
    extends: require('Director'),

    newGameMode({ name, data }, callback) {
        if (this[name]) {
            this.stopJackpotWin();
            this.currentGameMode.hide();
            this.node.gSlotDataStore.currentGameMode = name;
            if (name === 'normalGame') {
                this.gui.emit('SHOW_GUI_NORMAL_GAME_MODE');
            } else {
                this.gui.emit('SHOW_GUI_FREE_GAME_MODE');
            }
            this.currentGameMode = this[this.node.gSlotDataStore.currentGameMode];
            this.currentGameMode.enter(data, callback);
        }
    },
    resumeGameMode({ name }, callback) {
        if (this[name]) {
            this.node.gSlotDataStore.currentGameMode = name;
            if (name === 'normalGame') {
                this.gui.emit('SHOW_GUI_NORMAL_GAME_MODE');
            } else {
                this.gui.emit('SHOW_GUI_FREE_GAME_MODE');
            }
            this.currentGameMode = this[this.node.gSlotDataStore.currentGameMode];
            this.currentGameMode.show(callback);
        }
    },
    
    switchMode() {
        this.gui.emit('PLAY_GIRL_IDLE');
        this._super();
    },

    switchToRealMode() {
        this._super();
        const { currentGameMode } = this.node.gSlotDataStore;
        const isFreeGame = currentGameMode === "freeGame";
        const isNormalGame = currentGameMode === "normalGame";
        if (isNormalGame) return;

        this.forceExitNormalGame();
        if (isFreeGame && !this.freeGame.director.beginToExit) {
            this.forceExitFreeGame();
        }
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playMainBGM();
        }
        this.showTrialButtons(false);
        this.tutorialMgr && this.tutorialMgr.trigger("GAME_RESET_SESSION");
    },

    forceExitFreeGame() {
        let exitScript = [];
        exitScript.push({
            command: "_clearPaylines",
        });
        exitScript.push({
            command: "_gameExit",
        });
        this.freeGame.emit("FORCE_TO_EXIT", exitScript);
    },

    forceExitNormalGame() {
        let normalExitScript = [];
        normalExitScript.push({
            command: "_resumeGameMode",
            data: { name: "normalGame", },
        });
        normalExitScript.push({
            command: "_resetSpinButton"
        });
        normalExitScript.push({
            command: "_gameEnd"
        });
        normalExitScript.push({
            command: "_gameFinish"
        });
        normalExitScript.push({
            command: "_gameRestart"
        });
        normalExitScript.push({
            command: "_clearWinAmount"
        });
        this.normalGame.director.stopAutoSpinClick();
        this.normalGame.director.autoSpin = false;
        this.normalGame.emit("FORCE_TO_EXIT", normalExitScript);
    },
});