const {getRandomInt} = require('utils');
cc.Class({
    extends: require('SlotReelv2'),

    properties: {
        bannerPrefab: cc.Prefab,
        bannerPrefabName: 'BannerEuro',
    },

    onLoad() {
        this._super();
        this.subSymbol = null;
        this._symbols = [];
        this.bufferDistance = 0;
        this.fakeBufferDistance = 0;
        this.bannerLength = 8;
        this.isSureWin = false;
    },

    init(showNumber, gameConfig, col, symbolPrefab, isFreeMode = false) {
        this.col = col;
        this.config = gameConfig;
        if(isFreeMode) {
            this.symbolList = this.config.SYMBOL_NAME_LIST_FREE[col];
        } else {
            this.symbolList = this.config.SYMBOL_NAME_LIST[col];
        }
        this.symbolPrefab = symbolPrefab;
        this.showNumber = showNumber;
        this.showSymbols = [];
        this.totalNumber = this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP + this.config.TABLE_SYMBOL_BUFFER.BOT;
        this.isFreeMode = isFreeMode;
        this.symbolStartY = - (this.config.TABLE_FORMAT[col]/2 + this.config.TABLE_SYMBOL_BUFFER.BOT - 0.5) * this.config.SYMBOL_HEIGHT;
        for (let i = 0; i < this.totalNumber; ++i) {          
            const symbol = cc.instantiate(this.symbolPrefab);
            symbol.name = "Symbol_" + i;
            symbol.parent = this.reel;
            symbol.setPosition(0, this.symbolStartY + i*this.config.SYMBOL_HEIGHT);
            symbol.changeToSymbol(this.getRandomSymbolNameWithException('K'));
            if (i >= this.config.TABLE_SYMBOL_BUFFER.BOT && this.showSymbols.length < this.showNumber) {
                this.showSymbols.unshift(symbol);
            }
            this._symbols.push(symbol);
        }

        this.mode = 'FAST';
        this.curentConfig = this.config.STATS[this.mode];
        this.index = 0;
        this.reset();
    },

    initPoolFactory(poolFactory) {
        this.poolFactory = poolFactory;
    },

    startSpinningWithDelay(delay) {
        this._symbols.forEach((child) => {
            child.opacity = 255;
            child.changeToBlurSymbol(child.symbol);
        });
        this.resetReelPosition();
        this._super(delay);
    },

    stopSpinningWithDelay(delay, matrix, data, callback) {
        const { subSymbol, isShowingBigWild, isSureWin } = data;
        this.delayIndex = delay;
        this.showSymbols = [];
        this.matrix = [...matrix];
        this.subSymbol = subSymbol && subSymbol.length ? [...subSymbol] : null;
        this.callbackStopReel = callback ? callback : () => {};
        let reelDelayStop = delay * this.curentConfig.REEL_DELAY_STOP;
        if (isSureWin) {
            reelDelayStop = this.curentConfig.REEL_DELAY_SURE_WIN;
        } else if (isShowingBigWild) {
            reelDelayStop += this.curentConfig.DELAY_BIG_WILD;
        }
        this.isShowingBigWild = isShowingBigWild;
        this.isSureWin = isSureWin;
        this.isNearWin = false;
        this.delay = delay;
        cc.director.getScheduler().schedule(this.setStepToStop, this, 0, 0, reelDelayStop, false);

        //Add 2 more symbol to apply near miss
        this.matrix.unshift(this.getRandomSymbolNameWithException('K'));
        this.matrix.push(this.getRandomSymbolNameWithException('K'));
    },

    runSpinning() {
        this.runSpinningAnimation(() => {
            if (this.isShowingResult && this.canShowBanner) {
                this.canShowBanner = false;
                if(this.poolFactory){
                    const banner = this.poolFactory.getObject(this.bannerPrefabName);
                    banner.active = true;
                    banner.setPosition(this.reel.x, -this.reel.y + this.bufferDistance);
                    banner.emit('PLAY_ANIM_IDLE');
                    this.bannerObject = banner;
                    this.reel.addChild(banner);
                }
            }
            if (this.canShowFakeBanner && this.isShowingResult) {
                this.canShowFakeBanner = false;
                if(this.poolFactory){
                    const fakeBanner = this.poolFactory.getObject(this.bannerPrefabName);
                    fakeBanner.active = true;
                    fakeBanner.setPosition(this.reel.x, -this.reel.y + this.fakeBufferDistance);
                    fakeBanner.emit('PLAY_ANIM_IDLE');
                    this.fakeBannerObject = fakeBanner;
                    this.reel.addChild(fakeBanner);
                }
            }
            if (this.step > this.showNumber) {
                this.runSpinning();
                this.step--;
                if (this.step < this.totalNumber) {
                    this.showResult = 1;
                }
            } else if (this.step == this.showNumber) {
                // check last reel, near win and not fast to result
                if (this.delayIndex === (this.config.TABLE_FORMAT.length - 1) && this.isNearWin && !this.isFastToResult) {
                    this.runStopAnimation(50, 0.2);
                } else {
                    this.runStopAnimation(this.curentConfig.REEL_EASING_DISTANCE, this.curentConfig.REEL_EASING_TIME);
                }
            }
        });
    },

    circularSymbols() {
        let lastSymbol = null;
        const reelIndex = this.index % (this.totalNumber);
        lastSymbol = this._symbols[reelIndex];
        if (!this.showResult) {
            lastSymbol.changeToBlurSymbol(this.getRandomSymbolName());
            lastSymbol.removeSubSymbol();
        } else if (this.stop < this.totalNumber) {
            let isRealSymbol = this.stop >= this.config.TABLE_SYMBOL_BUFFER.TOP && this.stop < this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP;
            let symbolValue = this.matrix[this.stop];
            this.step = this.totalNumber + this.showNumber - (this.stop + this.config.TABLE_SYMBOL_BUFFER.BOT);
            if (isRealSymbol) {
                lastSymbol.changeToSymbol(symbolValue);
                this.showSymbols.unshift(lastSymbol);
            } else {
                lastSymbol.changeToBlurSymbol(symbolValue);
            }
            if (this.isFreeMode && isRealSymbol && symbolValue === "K") {
                lastSymbol.opacity = 0;
            }
            const subSymbolIndex = this.showNumber - this.stop + this.showNumber * this.delayIndex;
            if (this.subSymbol && isRealSymbol && this.subSymbol.indexOf(subSymbolIndex) >= 0) {
                lastSymbol.addSubSymbol();
            } else {
                lastSymbol.removeSubSymbol();
            }
            this.stop++;
        }
        lastSymbol.y = lastSymbol.y + this.config.SYMBOL_HEIGHT*(this.totalNumber);
        this.index++;
    },

    setStepToStop() {
        this.step = this.curentConfig.STEP_STOP*2 - this.totalNumber;
        this.isShowingResult = true;
        if (this.curentConfig.MODE === 'NORMAL') {
            this.setUpFakeBanner();
        }
    },

    resetReelPosition() {
        this.resetBanner();
        this._symbols.forEach((child) => {
            child.y += this.reel.y;
        });
        this.reel.y = 0;
    },

    resetBanner() {
        if (this.bannerObject) {
            this.bannerObject.emit('RESET_BANNER');
            this.poolFactory.removeObject(this.bannerObject);
            this.bannerObject = null;
        }
        this._symbols.forEach((child) => {
            child.opacity = 255;
        });
        this.isShowingResult = false;
        this.canShowBanner = false;
        this.canShowFakeBanner = false;
    },

    reset() {
        this.index = this.index%(this.totalNumber);
        this.stop = 0;
        this.step = this.MAX_STEP;
        this.showResult = 0;
        this.matrix = [];
        this.bannerFormat = 9;
        if (this.fakeBannerObject) {
            this.poolFactory.removeObject(this.fakeBannerObject);
            this.fakeBannerObject = null;
        }
    },

    onReelStop() {
        this._symbols.forEach((child) => {
            child.changeToSymbol(child.symbol);
        });
    },

    playAnimOpenBanner() {
        if (this.bannerObject) {
            this.bannerObject.emit('PLAY_ANIM_OPEN', () => {
                if (this.bannerObject) {
                    this.poolFactory.removeObject(this.bannerObject);
                    this.bannerObject = null;
                }
            });
        }
    },

    fastStopSpinning() {
        if (!this.isSureWin) {
            if (this.step === this.MAX_STEP) return;
            this.isFastToResult = true;
            cc.director.getScheduler().unschedule(this.setStepToStop,this);
            if (!this.isShowingResult) {
                this.setStepToStop();
            }
            this.currentSpeed = this.currentSpeed/4;
        }
    },

    setUpEuroBanner(format) {
        this.canShowBanner = true;
        this.bannerFormat = format;
        switch (this.bannerFormat) {
            case -2:
            case -1:
                this.bufferDistance = this.calculateDistance(this.bannerFormat);
                break;
            case 0:
                this.bufferDistance = this.calculateDistance(getRandomInt(0, this.bannerLength - this.showNumber));
                break;
            case 1:
            case 2:
                this.bufferDistance = this.calculateDistance(this.bannerLength - (this.showNumber - this.bannerFormat));
                break;
            default:
                this.canShowBanner = false;
                break;
        }
    },

    calculateDistance(offset = 0) {
        const availableSteps = this.curentConfig.STEP_STOP*2 - this.totalNumber;
        return (availableSteps - offset + 0.5) * this.config.SYMBOL_HEIGHT + this.symbolStartY;
    },

    setUpFakeBanner() {
        const { SYMBOL_HEIGHT } = this.config;
        let availableSteps = this.step - this.bannerLength;
        switch (this.bannerFormat) {
            case 0:
                availableSteps -= (this.bannerLength - this.showNumber);
                this.fakeBufferDistance = this.calculateDistance(getRandomInt(this.showNumber, availableSteps), true);
                break;
            case 1:
            case 2:
                availableSteps -= (this.bannerLength - (this.showNumber - this.bannerFormat));
                this.fakeBufferDistance = this.calculateDistance(getRandomInt(this.showNumber, availableSteps), true);
                break;
            default:
                break;
        }
        const randomOffset = getRandomInt(this.showNumber, availableSteps);
        this.fakeBufferDistance = (randomOffset + 0.5) * SYMBOL_HEIGHT + this.symbolStartY;
        this.canShowFakeBanner = Math.random() < 5;
    },

    scaleSubSymbol() {
        this._symbols.forEach(symbol => {
            symbol.scaleSubSymbol();
        });
    },
});