
cc.Class({
    extends: cc.Component,
    properties: {
        smallWilds: [cc.Node],
        bigWilds: [cc.Node],
    },

    onEnable() {
        const fadeTime = 0.5;
        const breakTime = 1.5;
        const scaleTime = 0.5;
        cc.tween(this.node)
            .call(() => {
                this.bigWilds.forEach(node => {
                    cc.tween(node).to(fadeTime, { opacity: 0 }, { easing: "sineInOut" }).start();
                })
                this.smallWilds.forEach(node => {
                    node.opacity = 0;
                    node.scale = 1;
                    cc.tween(node)
                        .to(fadeTime, { opacity: 255 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1.1 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1 }, { easing: "sineInOut" })
                        .start();
                })
            })
            .delay(fadeTime + scaleTime * 3)
            .call(() => {
                this.smallWilds.forEach(node => {
                    cc.tween(node).to(fadeTime, { opacity: 0 }, { easing: "sineInOut" }).start()
                });
                this.bigWilds.forEach(node => {
                    cc.tween(node)
                        .to(fadeTime, { opacity: 255 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1.1 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1.1 }, { easing: "sineInOut" })
                        .to(scaleTime, { scale: 1 }, { easing: "sineInOut" })
                        .start();
                })
            })
            .delay(fadeTime + scaleTime * 4)
            .delay(breakTime)
            .union()
            .repeatForever()
            .start();
       
    },
    onDisable() {
        this.node.stopAllActions();
        this.smallWilds.forEach(node => {
            node.stopAllActions();
            node.opacity = 255;
            node.scale = 1;
        });
        this.bigWilds.forEach(node => {
            node.stopAllActions();
            node.opacity = 0;
            node.scale = 1;
        });
    },
  
   
});
