cc.Class({
    extends: cc.Component,

    properties: {
        reel: cc.Node,
    },

    onLoad() {
        this.node.mainComponent = this;
        this.MAX_STEP = Number.MAX_SAFE_INTEGER;
        this._symbols = [];
    },

    init(showNumber, gameConfig, col, symbolPrefab) {
        const { CIRCLE_ANGLE, SYMBOLS_PER_REEL, SYMBOL_HEIGHT, SYMBOL_NAME_LIST } = gameConfig;
        this.col = col;
        this.config = gameConfig;
        this.symbolList = SYMBOL_NAME_LIST[col];
        this.symbolPrefab = symbolPrefab;
        this.showNumber = showNumber;
        this.showSymbols = [];
        const bufferSymbols = this.config.TABLE_SYMBOL_BUFFER.TOP + this.config.TABLE_SYMBOL_BUFFER.BOT;
        this.totalNumber = this.showNumber + bufferSymbols;
        this.symbolStartY = - (this.config.TABLE_FORMAT[col] / 2 + this.config.TABLE_SYMBOL_BUFFER.BOT - 0.5) * SYMBOL_HEIGHT;
        this.slotAngle = CIRCLE_ANGLE / SYMBOLS_PER_REEL;
        this.startAngle = -this.slotAngle * bufferSymbols;
        this.symbolsPerReel = SYMBOLS_PER_REEL;
        // calculate reel radius
        this.reelRadius = Math.round((SYMBOL_HEIGHT / 2) / Math.tan(Math.PI / this.symbolsPerReel));
        for (let index = 0; index < this.totalNumber; ++index) {
            const symbol = cc.instantiate(this.symbolPrefab);
            symbol.name = "Symbol_" + index;
            symbol.parent = this.reel;

            this.setSymbolValue(symbol, index);
            symbol.changeToSymbol(this.getRandomSymbolName());
            this._symbols.unshift(symbol);
        }

        this.mode = 'FAST';
        this.curentConfig = this.config.STATS[this.mode];
        this.index = 0;
        this.reset();
    },

    setSymbolValue(symbol, index) {
        let angle = this.getAngle(index);
        symbol.y = this.getCircleY(-angle, this.reelRadius);
        symbol.z = this.getCircleZ(-angle, this.reelRadius);
        symbol.eulerAngles = cc.v3(angle, 0, 0);
        symbol.eulerAngles = cc.v3(angle, 0, 0);
    },

    getCircleZ(angle, radius) {
        const radian = (angle * Math.PI) / 180;
        return Math.cos(radian) * radius;
    },

    getCircleY(angle, radius) {
        const radian = (angle * Math.PI) / 180;
        return Math.sin(radian) * radius;
    },

    getAngle(index) {
        let angle = this.startAngle + (this.slotAngle * index);
        return angle;
    },

    getRandomSymbolName() {
        return this.symbolList[Math.floor(Math.random() * this.symbolList.length)];
    },

    reset() {
        this.stop = 0;
        this.step = this.MAX_STEP;
        this.showResult = 0;
        this.matrix = [];
    },

    setMode(mode) {
        this.mode = mode;
    },

    calculateAngle(pixel) {
        return (pixel / this.config.SYMBOL_HEIGHT) * this.slotAngle;
    },

    startSpinningWithDelay(delay) {
        this.step = this.MAX_STEP - 1;
        this.isFastToResult = false;
        this.curentConfig = this.config.STATS[this.mode];
        this.currentSpeed = this.curentConfig.TIME;

        const angle = this.calculateAngle(25);
        this.tweenSpinning = cc.tween(this.reel)
            .delay(delay * this.curentConfig.REEL_DELAY_START)
            .by(this.currentSpeed, { eulerAngles: cc.v3(-angle, 0, 0) })
            .by(this.currentSpeed, { eulerAngles: cc.v3(angle, 0, 0) })
            .call(() => {
                this.runSpinning();
            })
            .start();
    },

    runSpinning() {
        this.runSpinningAnimation(() => {
            if (this.step > this.showNumber) {
                this.runSpinning();
                this.step--;
                if (this.step < this.totalNumber) {
                    this.showResult = 1;
                }
            } else if (this.step == this.showNumber) {
                // check last reel, near win and not fast to result
                if (this.delayIndex === (this.config.TABLE_FORMAT.length - 1) && this.isNearWin && !this.isFastToResult) {
                    this.runStopAnimation(50, 0.2);
                } else {
                    this.runStopAnimation(this.curentConfig.REEL_EASING_DISTANCE, this.curentConfig.REEL_EASING_TIME);
                }
            }
        });
    },

    stopSpinningWithDelay(delay, matrix = [], callback) {
        this.delayIndex = delay;
        this.showSymbols = [];
        this.matrix = matrix;
        this.callbackStopReel = callback ? callback : () => { };
        let reelDelayStop = delay * this.curentConfig.REEL_DELAY_STOP;
        this.isNearWin = false;

        this.delay = delay;
        cc.director.getScheduler().schedule(this.setStepToStop, this, 0, 0, reelDelayStop, false);

        this.matrix.unshift(this.getRandomSymbolNameWithException('2'));

        if (this.config.TABLE_SYMBOL_BUFFER.BOT > 0) {
            this.matrix.push(this.getRandomSymbolNameWithException('3'));
        }
    },

    setStepToStop() {
        this.step = this.curentConfig.STEP_STOP * 2 - this.totalNumber;
    },

    fastStopSpinning() {
        if (this.step === this.MAX_STEP) return;
        this.isFastToResult = true;
        cc.director.getScheduler().unschedule(this.setStepToStop, this);
        this.showResult = 1;
        this.currentSpeed = this.currentSpeed / 3;
    },

    runStopAnimation(indexNearWin, time) {
        const angle = this.calculateAngle(indexNearWin);
        const timer = time ? time : this.curentConfig.TIME;
        this.onReelStop();
        this.tweenSpinning = cc.tween(this.reel)
            .by(timer, { eulerAngles: cc.v3(angle, 0, 0) })
            .by(timer, { eulerAngles: cc.v3(-angle, 0, 0) })
            .call(() => {
                this.reset();
                this.callbackStopReel();
                cc.director.getScheduler().unschedule(this.setStepToStop, this);
                this.currentSpeed = this.curentConfig.TIME;
            })
            .start();
    },

    onReelStop() { },

    runSpinningAnimation(callback) {
        let time = this.currentSpeed + this.currentSpeed * this.stop / 4;
        this.tweenSpinning = cc.tween(this.reel)
            .by(time, { eulerAngles: cc.v3(this.slotAngle, 0, 0) })
            .call(() => {
                this.circularSymbols();
                callback && callback();
            })
            .start();
    },

    circularSymbols() {
        const lastSymbol = this._symbols[this.index % (this.totalNumber)];
        if (!this.showResult) {
            lastSymbol.changeToBlurSymbol(this.getRandomSymbolName());
        } else if (this.stop < this.totalNumber) {
            let isRealSymbol = this.stop >= this.config.TABLE_SYMBOL_BUFFER.TOP && this.stop < this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP;
            let symbolValue = this.matrix[this.stop];
            this.step = this.totalNumber + this.showNumber - (this.stop + this.config.TABLE_SYMBOL_BUFFER.BOT);
            if (isRealSymbol) {
                lastSymbol.changeToBlurSymbol(this.getRandomSymbolName());
                lastSymbol.changeToSymbol(symbolValue);
                this.showSymbols.unshift(lastSymbol);
            } else {
                lastSymbol.changeToBlurSymbol(symbolValue);
            }
            this.stop++;
        }
        this.setSymbolValue(lastSymbol, this.symbolsPerReel - this.index - 1);
        this.index++;
    },

    getShowSymbol(index) {
        return this.showSymbols[index];
    },

    getRandomSymbolNameWithException(exceptionSymbol) {
        let symbol = this.symbolList[Math.floor(Math.random() * this.symbolList.length)];
        if (symbol == exceptionSymbol) {
            symbol = this.getRandomSymbolNameWithException(exceptionSymbol);
        }

        return symbol;
    },

    getRandomSymbolNameWithExceptions(exceptionSymbols) {
        const remainSymbols = [];
        const defaultSymbol = '3';
        if (!this.symbolList) return defaultSymbol; //case haven't init;
        for (let i = 0; i < this.symbolList.length; i++) {
            const symbol = this.symbolList[i];
            let res = true;
            for (let j = 0; j < exceptionSymbols.length; j++) {
                const exception = exceptionSymbols[j];
                if (symbol == exception) {
                    res = false;
                    break;
                }
            }
            if (res) {
                remainSymbols.push(symbol);
            }
        }
        let symbol = remainSymbols[Math.floor(Math.random() * remainSymbols.length)];
        return symbol;
    },
});
