
cc.Class({
    extends: cc.Component,
    properties: {
        fishK1: cc.Node,
        fishK2: cc.Node,
    },
    onLoad() {
        this.node.on("INIT", this.init, this);
        this.node.on("PLAY_ANIM_FLY", this.playAnimFly, this);
        this.tweenFlying = null;
    },
    init(symbol) {
        this.tweenFlying && this.tweenFlying.stop();
        this.tweenFlying = null;
        this.node.symbol = symbol;
        if (symbol == 'K1') {
            this.fishK1.active = true;
            this.fishK2.active = false;
        } else {
            this.fishK1.active = false;
            this.fishK2.active = true;
        }
    },
    playAnimFly(targetPos, timeFly, callback) {
        this.tweenFlying && this.tweenFlying.stop();
        this.tweenFlying = cc.tween(this.node)
            .call(() => {
                const distanceX = Math.abs(this.node.x - targetPos.x);
                let ranPos1 = cc.v2(Math.random() * distanceX + Math.min(targetPos.x, this.node.x), this.node.y);
                let ranPos2 = cc.v2(Math.random() * distanceX + Math.min(targetPos.x, this.node.x), targetPos.y);
                const randomBezier = [ranPos1, ranPos2, targetPos];
                this.node.runAction(cc.bezierTo(timeFly, randomBezier));
            })
            .delay(timeFly)
            .call(() => {
                callback && callback();
                this.tweenFlying = null;
            })
            .start();
    },

});
