
cc.Class({
    extends: cc.Component,

    properties: {
        animNode: cc.Node,
    },

    onLoad() {
        this.node.on("RESET", this.reset, this);
        this.node.on("PLAY_ANIMATION", this.playAnimation, this);
        this.node.on("STOP_ANIMATION", this.stopAnimation, this);

        this.animWinLine = this.animNode.getComponent(sp.Skeleton);
    },
    playAnimation() {
        this.animNode.active = true;
        this.animWinLine.setAnimation(0, 'Win', false);
        this.animWinLine.addAnimation(0, 'Idle', true);
        this.isDisable = false;
    },
    stopAnimation() {
        if (this.isDisable) return;
        this.animWinLine.setAnimation(0, 'Disappear', false);
        this.isDisable = true;
    },
    reset() {
        this.animNode.active = false;
    }

});
