const CHEER_LEADER = {
    IDLE: 1,
    DANCE: 2
};

cc.Class({
    extends: require('GameModeBasic'),

    properties: {
        mainBG: cc.Node,
        freeBG: cc.Node,
        spinTimeBG: cc.Node,
        lightBGMain: cc.Node,
        lightBGFree: cc.Node,
        cheerLeader: sp.Skeleton,
    },

    onLoad() {
        this._super();
        this.node.on("SHOW_GUI_NORMAL_GAME_MODE", this.showNormalGame, this);
        this.node.on("SHOW_GUI_FREE_GAME_MODE", this.showFreeGame, this);
        this.node.on("PLAY_GIRL_IDLE", this.playGirlIdle, this);
        this.node.on("PLAY_GIRL_DANCE", this.playGirlDance, this);
        this.freeBG.active = false;
        // this.cheerLeader.setAnimation(0, 'Idle', true);
        this.spineState = CHEER_LEADER.IDLE;
    },

    hideAll() {
        this.freeBG.active = false;
        this.mainBG.active = false;
        this.spinTimeBG.active = false;
        this.lightBGMain.opacity = 0;
        this.lightBGFree.opacity = 0;
    },

    showNormalGame() {
        this.hideAll();
        this.mainBG.active = true;
        this.lightBGMain.opacity = 255;
    },

    showFreeGame() {
        this.hideAll();
        this.freeBG.active = true;
        this.spinTimeBG.active = true;
        this.lightBGFree.opacity = 255;
    },

    playAnimGirl(updatedState, isForce = false) {
        // if (this.spineState === updatedState && isForce === false) return;
        // this.spineState = updatedState;

        // if (this.spineState === CHEER_LEADER.IDLE) {
        //     this.cheerLeader.setAnimation(0, 'Idle', true);
        // } else {
        //     this.cheerLeader.setAnimation(0, 'Dance', false);
        //     this.cheerLeader.setCompleteListener((entry) => {
        //         if (entry.animation.name === 'Dance') {
        //             this.spineState = CHEER_LEADER.IDLE;
        //             this.cheerLeader.setAnimation(0, 'Idle', true);
        //         }
        //     });
        // }
    },

    playGirlIdle(isForce) {
        this.playAnimGirl(CHEER_LEADER.IDLE, isForce);
    },
    playGirlDance(isForce) {
        this.playAnimGirl(CHEER_LEADER.DANCE, isForce);
    },
});