cc.Class({
    extends: require('SlotTablev2'),

    onLoad() {
        this._super();
        this.node.on("ON_BIG_WILD_SHOW", this.onBigWildShow, this);

        this._delayStepsStart = [0, 0, 0, 0, 0];
        this._delayStepsEnd = [0, 1, 2, 1, 0];
        this._delayStepsFree = [0, 1, 1, 1, 0];
        this._countReelSpin = 0;
    },
    init() {
        this._super()
        if (!this.isFreeMode) this.changeMatrix(this.node.gSlotDataStore.getDefaultMatrix());
    },
    stopReeWithRandomMatrix(callback) {
        this.matrixRandom = [];
        for (let col = 0; col < this.tableFormat.length; ++col) {
            this.matrixRandom[col] = [];
            for (let row = this.tableFormat[col] - 1; row >= 0; --row) {
                const reel = this.node.reels[col];
                if (reel && reel.getRandomSymbolNameWithExceptions) {
                    this.matrixRandom[col][row] = reel.getRandomSymbolNameWithExceptions(['K1', 'K2', 'K3']);
                } else {
                    this.matrixRandom[col][row] = "3";
                }
            }
        }
        const matrix = this.matrixRandom;
        const matrix0 = this.matrixRandom;
        this.stopSpinning({ matrix, matrix0 }, () => {
            callback && callback();
        });
    },
    onBigWildShow(event) {
        event.stopPropagation();
        const data = event.getUserData();
        const col = data.col;
        this.node.reels[col].node.emit("HIDE_SYMBOL_BIG_WILD");
    },
    changeMatrix(data) {
        const { matrix, bigWild, rowOffset = 0 } = data;
        this.matrix = matrix;
        if (bigWild && bigWild.length) {
            this.node.emit("SET_UP_BIG_WILD", data)
        }
        for (let col = 0; col < this.tableFormat.length; ++col) {
            const reel = this.node.reels[col];
            for (let row = 0; row < this.tableFormat[col]; ++row) {
                if (reel) {
                    const symbol = reel.showSymbols[row + rowOffset];
                    if (symbol) symbol.changeToSymbol(this.matrix[col][row]);
                }
            }
            if (!this.isFreeMode) this.node.emit('SET_UP_PAYLINES_AT_REEL', col);
        }
        if (this.isFreeMode) {
            if (this.stickyWild) this.stickyWild.emit("CHANGE_MATRIX", matrix);
            this.node.reels[2].node.emit("HIDE_SYMBOL_BIG_WILD");
        }
    },

    startSpinning() {
        this.isFastToResult = false;
        this.node.curentConfig = this.node.config.STATS[this.node.mode];
        this._countReelSpin = 0;
        for (let col = 0; col < this.node.reels.length; ++col) {
            if (!(this.node.isFreeMode && col == 2)) {
                const delay = this._delayStepsStart[col];
                this.node.reels[col].node.emit("RESET_SYMBOL_BIG_WILD");
                this.node.reels[col].setMode(this.node.mode);
                this.node.reels[col].startSpinningWithDelay(delay);
                this._countReelSpin++;
            }
        }
        this.node.emit('TABLE_START_NEARWIN');
        this.node.emit('TABLE_START_SOUND');
        this.node.emit('TABLE_START_EFFECT');
        this.node.emit('TABLE_START_MISC');
    },
    stopSpinning({ matrix, matrix0 }, callback) {
        this.matrix = matrix0;
        this.stopSpinningCallbackCount = 0;
        let delaySteps = this.node.isFreeMode ? this._delayStepsFree : this._delayStepsEnd;
        if (this.isTurbo) { delaySteps = [0, 0, 0, 0, 0]; }
        for (let col = 0; col < this.tableFormat.length; ++col) {
            const delay = delaySteps[col];
            const matrix = this.matrix[col].slice().reverse();
            const slotReel = this.node.reels[col];
            if (!(this.node.isFreeMode && col == 2)) {
                slotReel.stopSpinningWithDelay(delay, matrix, this.checkStopSpinningCallback.bind(this, matrix, callback, col));
            }
        }
        this.node.emit('REEL_ABOUT_TO_STOP_NEARWIN', { reels: this.node.reels, data: matrix, context: this, });
        this.node.emit('REEL_ABOUT_TO_STOP_SOUND', { reels: this.node.reels, data: matrix, context: this, });
        this.node.emit('REEL_ABOUT_TO_STOP_EFFECT', { reels: this.node.reels, data: matrix, context: this, });
        this.node.emit('REEL_ABOUT_TO_STOP_MISC', { reels: this.node.reels, data: matrix, context: this, });
        if (this.stickyWild) {
            this.stickyWild.emit("UPDATE_MATRIX", this.matrix);
        }
    },

    checkStopSpinningCallback(matrix, callback, col) {
        this.stopSpinningCallbackCount++;
        const count = this.stopSpinningCallbackCount;

        this.node.emit("SET_UP_PAYLINES_AT_REEL", col);
        this.node.emit("SHOW_BIG_WILD", col);
        this.node.emit('REEL_STOP_NEARWIN', { matrix, count, col, context: this });
        this.node.emit('REEL_STOP_SOUND', { matrix, count, col, context: this });
        this.node.emit('REEL_STOP_EFFECT', { matrix, count, col, context: this });
        this.node.emit('REEL_STOP_MISC', { matrix, count, col, context: this });
        if (this.isFreeMode && col == 3) this.node.emit("SET_UP_PAYLINES_AT_REEL", 2)
        if (count >= this._countReelSpin) {
            this._countReelSpin = 0;
            callback && callback();
        }


    },
});