const loadConfigAsync = require('loadConfigAsync');
const { ccclass, property } = cc._decorator;
 
@ccclass
export class LobbyDev extends cc.Component {
    @property({
        displayName: "List Game",
        type: cc.Node,
    })
    listGame = null;

    @property({
        displayName: "Login",
        type: cc.Node,
    })
    btnLogin = null;

    onLoad() {
        this._bindEvents();
    }

    start () {
        // [3]
    }

    // update (deltaTime: number) {
    //     // [4]
    // }

    _bindEvents() {
        let arrView = this.listGame.children;
        let length = arrView.length;
        for (let i = 0; i < length; i++) {
            let view = arrView[i];
            view.off("click");
            view.on("click", event => {
                let sceneName = view.name;
                cc.director.loadScene(sceneName);
            });
        }

        if (this.btnLogin) {
            this.btnLogin.off('click');
            this.btnLogin.on('click', () => {
                const {LOGIN_SCENE_NAME} = loadConfigAsync.getConfig();
                if (LOGIN_SCENE_NAME) {
                    cc.director.loadScene(LOGIN_SCENE_NAME);
                }
            });
        }
    }
}
