
const { ccclass, property } = cc._decorator;

@ccclass
export class ItemLobby extends cc.Component {

    gameId = null;

    @property({type: cc.Sprite})
    icon = null;

    @property({type: cc.Button})
    button = null;
    config = null;

    @property({type: cc.Node})
    jackpot = null; 

    @property
    dimColor: cc.Color = new cc.Color();

    onLoad() {
        this.node.on('ENABLE_SELECTED_GAME', this.enableGame.bind(this));
        this.node.on('DISABLE_SELECTED_GAME', this.disableGame.bind(this));
        this.node.on('JACKPOT_UPDATE', this.jackpotUpdate, this);
    }

    setJackpotY(posY) {
        this.jackpot.setPosition(cc.v3(0, posY,0));
    }

    jackpotUpdate(data) {
        // console.log(data);
        let listJP = Object.keys(data);
        let jpName = listJP.filter(it => {return it.indexOf(this.gameId)>0})[0];
        if (jpName) {
            let jpData = data[jpName];
            this.jackpot.emit('UPDATE_JACKPOT', {
                JP3: jpData[3],
                JP2: jpData[2],
                JP1: jpData[1],
            });
        }
    }

    setSprite(spriteFrame) {
        this.icon.spriteFrame = spriteFrame;
    }

    setGameConfig(id, config) {
        this.gameId = id;
        this.config = config;
    }

    disableGame() {
        this.icon.active = false;
        // this.icon.getComponent(Sprite).color = this.dimColor;
    };

    enableGame() {
        this.icon.active = true;
        // this.icon.getComponent(Sprite).color = Color.WHITE;
    }

    onClick() {
        let ev = new cc.Event('RQ_PLAY_GAME', true);
        ev['gameId'] = this.gameId;
        ev['config'] = this.config;
        this.node.dispatchEvent(ev);
    }
}