const { formatMoney } = require('utils');
cc.Class({
    extends: require('TotalWinPanelv2'),

    enter() {
        this.canClose = false;
        this.winAmount.getComponent(cc.Label).string = '';
        //Overwrite this when extends
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playTotalWin();
        }
        this.node.runAction(cc.sequence(
            cc.callFunc(() => {
                const {winAmountPS} = this.node.gSlotDataStore.playSession;
                this.winAmount.getComponent(cc.Label).string = formatMoney(winAmountPS);
                this.callback&&this.callback();
            }),
            cc.delayTime(0.5),
            cc.callFunc(() => {
                this.canClose = true;
                this.closeBtn.getComponent(cc.Button).interactable = true;
            }),
            cc.delayTime(3.5),
            cc.callFunc(() => {
                this.exit();
            }),
        ));
    },

    exit() {
        this.node.mainDirector.onIngameEvent("HIDE_TOTAL_WIN_PANEL");
        this.node.soundPlayer.stopTotalWin(); 
        this._super();
    },
});
