cc.Class({
    extends: require('SlotSymbol'),

    properties: {
        subSymbol: cc.Node,
        dimColor: cc.Color,
        blurNamePrefix: 'blur_'
    },

    onLoad () {
        this._super();
        this.node.addSubSymbol = this.addSubSymbol.bind(this); 
        this.node.removeSubSymbol = this.removeSubSymbol.bind(this); 
        this.node.scaleSubSymbol = this.scaleSubSymbol.bind(this); 
        this.node.showSubSymbol = this.showSubSymbol.bind(this); 
        this.node.hideSubSymbol = this.hideSubSymbol.bind(this); 
        this.node.on("INIT_FOR_PAYLINE", this.initForPayline, this);
        this.symbolSpriteFrame = this.staticSymbol.getComponent(cc.Sprite);
        this._isAnimated = false;
        this._isPlayAnim = false;
        this._havingSubSymbol = false;
        if (this.subSymbol) {
            this.subSymbol.active = false;
            this.subSymbolData = {
                position: this.subSymbol.position,
                scale: this.subSymbol.scale
            };
        }
    },

    changeToBlurSymbol(symbolName){
        const name = this.blurNamePrefix + symbolName;
        if(this.blurAssets[name]){
            this.node.symbol = symbolName;
            this.staticSymbol.opacity = 255;
            this.staticSymbol.getComponent(cc.Sprite).spriteFrame = this.blurAssets[name];
        } else {
            this.changeToSymbol(symbolName);
        }
        if (this.subSymbol) {
            this.subSymbol.active = false;
        }
    },

    initForPayline(isAnimated = false){
        this._isAnimated = isAnimated;
    },

    addSubSymbol() {
        if (this.subSymbol) {
            this._havingSubSymbol = true;
            this.subSymbol.stopAllActions();
            this.subSymbol.scale = 1;
            this.subSymbol.position = cc.v2(0, 0);
            this.subSymbol.active = true;
        }
    },

    showSubSymbol() {
        if (this.subSymbol && this._havingSubSymbol) {
            this.subSymbol.active = true;
            this.subSymbol.position = this.subSymbolData.position;
            this.subSymbol.scale = this.subSymbolData.scale;
        }
    },

    hideSubSymbol() {
        if (this.subSymbol) {
            this.subSymbol.active = false;
        }
    },

    scaleSubSymbol() {
        if (this._havingSubSymbol) {
            this.subSymbol.runAction(cc.spawn(
                cc.scaleTo(0.2, this.subSymbolData.scale),
                cc.moveTo(0.2, this.subSymbolData.position)
            ));
        }
    },

    removeSubSymbol() {
        if (this.subSymbol) {
            this.subSymbol.active = false;
            this._havingSubSymbol = false;
        }
    },

    blinkHighlight() {
        this.node.opacity = 255;
        this.staticSymbol.active = true;
        this.staticSymbol.color = cc.Color.WHITE;
        if (this.subSymbol) this.subSymbol.color = cc.Color.WHITE;
        this._isPlayAnim = false;
    },

    enableHighlight() {
        this.node.active = true;
        this.staticSymbol.scale = 1;
        this.staticSymbol.color = cc.Color.WHITE;
        if (this.subSymbol) this.subSymbol.color = cc.Color.WHITE;
        this.staticSymbol.stopAllActions();
        if (this.subSymbol) {
            this.subSymbol.active = false;
        }
        if(this._isAnimated){
            this.staticSymbol.active = false;
            this._isPlayAnim = true;
        }else{
            this.staticSymbol.active = true;
            const seq = cc.repeat(
                cc.sequence(
                    cc.scaleTo(0.2, 1.05),
                    cc.scaleTo(0.2, 0.95),
                    cc.scaleTo(0.2, 1),
                ),2);
            this.staticSymbol.runAction(seq);
        }
    },

    disableHighlight() {
        this.node.active = true;
        this.staticSymbol.active = !this._isPlayAnim;
        this.staticSymbol.color = this.dimColor;
        if (this.subSymbol) this.subSymbol.color = this.dimColor;
        this.resetStaticSymbol();
    },

    reset() {
        this.resetStaticSymbol();
        this.node.opacity = 255;
        this.node.active = true;
        this.staticSymbol.opacity = 255;
        this.staticSymbol.active = true;
        this.staticSymbol.color = cc.Color.WHITE;
        if (this.subSymbol) this.subSymbol.color = cc.Color.WHITE;
        this.staticSymbol.stopAllActions();
        this._isPlayAnim = false;
        if(this._havingSubSymbol) {
            this.subSymbol.active = true;
            this._havingSubSymbol = false;
        }
    },

    resetStaticSymbol() {
        this.staticSymbol.scale = 1;
        this.staticSymbol.position = cc.v3(0, 0);
    },
});