cc.Class({
    extends: require('SlotSymbol'),

    properties: {
        dimColor: cc.Color,
    },

    onLoad() {
        this._super();
        this.node.on("SHOW_FAKE_SYMBOL", this.showFakeSymbol, this);
    },
    showFakeSymbol() {

    },
    changeToSymbol(symbolName, fakeSymbol = null) {
        this._super(symbolName);
        this.node.fakeSymbol = fakeSymbol;
        this.fakeSymbol = fakeSymbol;
        if (fakeSymbol && this.assets[fakeSymbol]) {
            this.staticSymbol.getComponent(cc.Sprite).spriteFrame = this.assets[fakeSymbol];
        }
    },
    changeToBlurSymbol(symbolName, fakeSymbol) {
        this._super(symbolName);
        this.node.fakeSymbol = fakeSymbol;
        this.fakeSymbol = fakeSymbol;
        if (fakeSymbol && this.blurAssets[fakeSymbol]) {
            this.staticSymbol.getComponent(cc.Sprite).spriteFrame = this.blurAssets[fakeSymbol];
        }
    },
    disableHighlight() {
        this.tweenColor(this.staticSymbol, this.dimColor);
    },
    enableHighlight() {
        this.tweenColor(this.staticSymbol, cc.Color.WHITE);
    },
    reset() {
        this.staticSymbol.tweenColor && this.staticSymbol.tweenColor.stop();
        this.staticSymbol.tweenColor = null;

        this.staticSymbol.stopAllActions();
        this.tweenColor(this.staticSymbol, cc.Color.WHITE, 0.2);

        // this.staticSymbol.color = cc.Color.WHITE;
        this.bigSymbol = false;
        this.staticSymbol.opacity = 255;
        this.node.opacity = 255;
    },
    tweenColor(node, finalColor, time = 0.2) {
        node.tweenColor && node.tweenColor.stop();
        node.tweenColor = null;
        node.tweenColor = cc.tween(node)
            .to(time, { color: finalColor })
            .call(() => {
                node.tweenColor = null;
            })
            .start();
    },
    blinkHighlight() { },
});