const { formatMoney } = require('utils');
const { reverseEasing } = require('globalAnimationLibrary');
const CHARACTER = cc.Enum({
    REFEREE: "K1",
    RABBIT: "K2",
    BIRD: 'K3',
});
const ANIMATION = {
    BIG_WIN_APPEAR: 'TL_Appear',
    BIG_WIN_IDLE: 'TL_Idle',
    BIG_WIN_END: 'TL_Disappear',
    BIG_WIN_TO_MEGA: 'TL_to_TCL',

    MEGA_IDLE: 'TCL_Idle',
    MEGA_END: 'TCL_Disappear',
    MEGA_TO_SUPER_RABBIT: 'TCL_to_TSL_Tho',
    MEGA_TO_SUPER_SWALLOW: 'TCL_to_TSL_En',

    SUPER_RABBIT_IDLE: 'TSL_Idle_Tho',
    SUPER_RABBIT_END: 'TSL_Disappear_Tho',

    SUPER_SWALLOW_IDLE: 'TSL_Idle_En',
    SUPER_SWALLOW_END: 'TSL_Disappear_En',
}
const STATE_ANIM = {
    TL: 0,
    TCL: 1,
    TSL: 2,
};

cc.Class({
    extends: require('WinEffectv2'),

    properties: {
        fastParticleNode: cc.Node,
        particleBigWin: cc.Node,
        particleMega: cc.Node,
        particleSuper: cc.Node,
        animBigWin: sp.Skeleton,
    },
    onLoad() {
        this.animBigWin.node.opacity = 0;
        this.winInfo.opacity = 0;
        this._super();
    },

    enter() {
        this.overlayNode.active = false;
        this.node.stopAllActions();
        let modeTurbo = false;
        if (this.node.gSlotDataStore) modeTurbo = this.node.gSlotDataStore.modeTurbo;
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        modeTurbo || isFTR ? this.showFastEffectWin() : this.showEffectWin();
        this.node.fullDisplay = !modeTurbo; const { matrix, freeBattleData, rate } = this.content;
        this.winCharacter = this.getBigWinCharacter(matrix, freeBattleData);
        this.animDuration = 8;
        if (rate >= this.megaRate) this.animDuration = 10;
        if (rate >= this.superRate) this.animDuration = 12;
        this.stateAnim = STATE_ANIM.TL;
    },

    showFastEffectWin() {  // turbo
        this.animBigWin.node.active = false;
        if (this.isShowFastEffect) {
            this.callback && this.callback();
            this.callback = null;
            return;
        }
        this.isShowFastEffect = true;
        if (this.overlayNode) {
            this.overlayNode.active = false;
        }
        this.fastParticleNode.emit('START_PARTICLE');
        this.callback && this.callback();
        this.callback = null;

        this.tweenFastEffect && this.tweenFastEffect.stop()
        this.tweenFastEffect = cc.tween(this)
            .delay(1)
            .call(() => {
                this.fastParticleNode.emit('STOP_PARTICLE');
            })
            .delay(2)
            .call(() => {
                this.exit();
                this.tweenFastEffect = null;
            })
            .start();
    },

    showEffectWin() {
        this.playSoundStart();
        this.isShowNormalEffect = true;

        this.overlayNode.active = true;
        this.overlayNode.runAction(cc.fadeTo(0.3, 200));

        this.animBigWin.node.active = true;
        this.animBigWin.node.opacity = 255;
        this.animBigWin.setAnimation(0, ANIMATION.BIG_WIN_APPEAR, false);
        this.animBigWin.addAnimation(0, ANIMATION.BIG_WIN_IDLE, true);
        this.stateAnim = STATE_ANIM.TL;

        const scaleTime = 0.2;
        this.winAmount.stopAllActions();
        this.winAmount.runAction(cc.repeatForever(cc.sequence(
            cc.scaleTo(scaleTime, 1.2),
            cc.scaleTo(scaleTime, 1),
        )));

        this.winInfo.active = true;
        this.winInfo.runAction(cc.fadeIn(0.3));
        this.initValue();
        this.startParticle();
        this.startUpdateWinAmount();
    },

    update() {
        if (!this.isUpdating) return;
        this.label.string = formatMoney(this.currentValue);
        if (this.currentValue >= this.superWinAmount && this.currentTitle == 1) {
            this.changeAnimState(STATE_ANIM.TSL);
        } else if (this.currentValue >= this.megaWinAmount && this.currentTitle == 0) {
            this.changeAnimState(STATE_ANIM.TCL);
        }
    },
    changeAnimState(state) {
        this.node.soundPlayer.playSfxByID('9881_sfx_bigwin_hit');
        this.currentTitle = state;
        this.startParticle();
        if (state == STATE_ANIM.TCL) {
            this.stateAnim = STATE_ANIM.TCL;
            this.animBigWin.setAnimation(0, ANIMATION.BIG_WIN_TO_MEGA, false);
            this.animBigWin.addAnimation(0, ANIMATION.MEGA_IDLE, true);

        } else if (state == STATE_ANIM.TSL) {
            let animAppear = this.winCharacter == CHARACTER.RABBIT ? ANIMATION.MEGA_TO_SUPER_RABBIT : ANIMATION.MEGA_TO_SUPER_SWALLOW;
            let animIdle = this.winCharacter == CHARACTER.RABBIT ? ANIMATION.SUPER_RABBIT_IDLE : ANIMATION.SUPER_SWALLOW_IDLE;
            this.animBigWin.setAnimation(0, animAppear, false);
            this.animBigWin.addAnimation(0, animIdle, true);
            this.stateAnim = STATE_ANIM.TSL;
        }
    },
    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmount.stopAllActions();
        this.stopParticle();
        this.winInfo.stopAllActions();
        this.winInfo.runAction(cc.sequence(
            cc.delayTime(this.delayShowTime),
            cc.callFunc(() => {
                let animDisappear = '';
                if (this.stateAnim == STATE_ANIM.TL) {
                    animDisappear = ANIMATION.BIG_WIN_END;
                }
                if (this.stateAnim == STATE_ANIM.TCL) {
                    animDisappear = ANIMATION.MEGA_END;
                }
                if (this.stateAnim == STATE_ANIM.TSL) {
                    animDisappear = this.winCharacter == CHARACTER.RABBIT ? ANIMATION.SUPER_RABBIT_END : ANIMATION.SUPER_SWALLOW_END;
                }
                this.animBigWin.setAnimation(0, animDisappear, false);
            }),
            cc.scaleTo(this.hideTime - 0.3, 0),
            cc.callFunc(() => {
                this.overlayNode.runAction(cc.fadeOut(0.3));
            }),
            cc.delayTime(0.3),
            cc.callFunc(() => {
                this.label.string = '';
                this.winInfo.opacity = 0;
                if (this.node.soundPlayer) {
                    this.node.soundPlayer.stopAllAudio();
                    this.node.soundPlayer.playMainBGM();
                }
                this.exit();
            })
        ));
    },
    exit() {
        this.tweenFastEffect && this.tweenFastEffect.stop()
        this.tweenFastEffect = null;
        this.winCharacter = CHARACTER.RABBIT;
        this._super();
    },
    getBigWinCharacter(matrix, battleData) {
        if (battleData) {
            return battleData[battleData.length - 1].isRabbit ? CHARACTER.RABBIT : CHARACTER.BIRD;
        }
        const hasReferee = this._hasBigWild(matrix, "K1");
        const hasRabbit = this._hasBigWild(matrix, "K2");
        const hasBird = this._hasBigWild(matrix, "K3");
        if (hasReferee && hasRabbit && hasBird) return CHARACTER.RABBIT;
        if (hasReferee && hasRabbit) return CHARACTER.RABBIT;
        if (hasReferee && hasBird) return CHARACTER.BIRD;
        if (hasRabbit) return CHARACTER.RABBIT;
        if (hasBird) return CHARACTER.BIRD;
        return CHARACTER.RABBIT;
    },
    _hasBigWild(matrix, symbol) {
        const flatMatrix = matrix.flat();
        return flatMatrix.reduce((count, value) => count + (value === symbol ? 1 : 0), 0) >= 3;
    },
    playSoundStart() {
        this.node.soundPlayer.stopAllAudio();
        this.node.soundPlayer.playSfxByID('9881_sfx_bigwin');
        this.node.soundPlayer.playSoundByID('9881_sfx_counting', true);
    },
    playSoundEnd() {
        this.node.soundPlayer.stopSoundByID('9881_sfx_bigwin');
        this.node.soundPlayer.playSfxByID('9881_sfx_bigwin_end');
    },
    startParticle() {
        this.stopParticle();
        if (this.currentTitle == STATE_ANIM.TL) {
            this.particleBigWin.emit('START_PARTICLE');
        }
        if (this.currentTitle == STATE_ANIM.TCL) {
            this.particleMega.emit('START_PARTICLE');
        }
        if (this.currentTitle == STATE_ANIM.TSL) {
            this.particleSuper.emit('START_PARTICLE');
        }
    },
    stopParticle() {
        this.particleBigWin.emit('STOP_PARTICLE');
        this.particleMega.emit('STOP_PARTICLE');
        this.particleSuper.emit('STOP_PARTICLE');
    },
    onClick() {
        if (!this.isUpdating || this.speedUp) return;
        if (!this.skippable) return;
        this.speedUp = true;
        this.winAmount.stopAllActions();
        const scaleTime = 0.13;
        this.winAmount.stopAllActions();
        this.winAmount.runAction(cc.repeatForever(cc.sequence(
            cc.scaleTo(scaleTime, 1.2),
            cc.scaleTo(scaleTime, 1),
        )));

        this.playSoundEnd();
        if (this.currentTween) {
            this.currentTween.stop();
            this.currentTween = null;
        }
        cc.tween(this)
            .to(1, { currentValue: this.content.winAmount })
            .call(() => {
                this.node.soundPlayer.stopSoundByID('9881_sfx_counting');
                this.node.soundPlayer.playSfxByID('9881_sfx_counting_end');
                this.finish();
            })
            .start();
    },
    startUpdateWinAmount() {
        const speedUpEasing = t => t * t; // constant accelerated
        const slowDownEasing = reverseEasing(t => t * t);
        const halfAmount = 0.5 * this.content.winAmount;
        this.currentTween = cc.tween(this);
        this.currentTween
            .to(0.5 * this.animDuration, { currentValue: halfAmount }, { easing: speedUpEasing })
            .to(0.5 * this.animDuration, { currentValue: this.content.winAmount }, { easing: slowDownEasing })
            .call(() => {
                this.playSoundEnd();
                this.node.soundPlayer.stopSoundByID('9881_sfx_counting');
                this.node.soundPlayer.playSfxByID('9881_sfx_counting_end');
            })
            .delay(0.5)
            .call(() => {
                this.skippable = false;
                this.currentTween = null;
                this.finish();
            });
        this.currentTween.start();
    },
    skip() {
        if (!this.node.active) return;
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playMainBGM();
        }
        this.exit();
    }
});