const { convertAssetArrayToObject } = require('utils');

cc.Class({
    extends: cc.Component,

    properties: {
        staticSymbol: cc.Node,
        particleEffect: cc.Node,
        symbols: [cc.SpriteFrame]
    },
    onLoad() {
        this.assets = convertAssetArrayToObject(this.symbols);
        this.node.on("INIT", this.init, this);
        this.node.on("RESET", this.reset, this);
        this.target = null;
    },
    init(symbolName, target) {
        const asset = this.assets[symbolName];
        this.symbolName = symbolName;
        this.target = target;
        this.staticSymbol.getComponent(cc.Sprite).spriteFrame = asset;
        this.particleEffect.active = true;
        this.particleEffect.emit("START_PARTICLE")

        const worldPos = this.target.parent.convertToWorldSpaceAR(this.target.getPosition());
        const curPos = this.node.parent.convertToNodeSpaceAR(worldPos);
        this.node.position = curPos;
        this.isOutOffTable = false;
        this.staticSymbol.active = true;

    },

    lateUpdate() {
        if (this.target && !this.isOutOffTable) {
            const worldPos = this.target.parent.convertToWorldSpaceAR(this.target.getPosition());
            const curPos = this.node.parent.convertToNodeSpaceAR(worldPos);
            if (curPos.y < (-(this.node.parent.height + this.node.config.SYMBOL_HEIGHT) / 2)) {
                this.staticSymbol.active = false;
                this.particleEffect.emit("STOP_PARTICLE")
                this.isOutOffTable = true;
                this.target = null;
            } else {
                this.node.position = curPos;
            }
        }
    },

    reset(callBack) {
        this.particleEffect.emit("STOP_PARTICLE")
        this.target = null;
        this.isOutOffTable = false;
        this.scheduleOnce(() => {
            this.staticSymbol.active = false;
            this.particleEffect.active = false;
            callBack && callBack();
        }, 0, 5);
    }
});
