cc.Class({
    extends: require('GameModeBasic'),

    properties: {
        //UI
        jackpot: cc.Node,
        trialJackpot: cc.Node,
        freeGameAnimBG: cc.Node,
        animBG: sp.Skeleton,
        dialogBackToReal: cc.Node,
        //frame 

        //btn
        slotButtonNormal: cc.Node,
        slotButtonFree: cc.Node,
        trialBtn: cc.Node,

        //version
        versionNode: cc.Node
    },

    onLoad() {
        this._super();
        this.node.on("HIDE_ALL_UI", this.hideAllUI, this);
        this.node.on("SHOW_UI_MAIN", this.showUIMainGame, this);
        this.node.on("SHOW_UI_FREE", this.showUIFreeGame, this);
        this.node.on("ON_CLICK_SWITCH_MODE", this.onClickSwitchMode, this);
        this.node.on("SHOW_NOTICE_ASK_BACK_TO_REAL", this.showNoticeAskB2R, this);
        this.node.on("SHOW_NOTICE_BACK_TO_REAL", this.showNoticeB2R, this);
    },

    hideAllUI() {
        this.slotButtonNormal.active = false;
        this.slotButtonFree.active = false;
        this.freeGameAnimBG.active = false;
    },

    showUIMainGame() {
        this.slotButtonNormal.active = true;
        this.slotButtonFree.active = false;
        this.freeGameAnimBG.active = false;
        this.animBG.setAnimation(0, "MainG", false)
    },

    showUIFreeGame() {
        this.slotButtonNormal.active = false;
        this.slotButtonFree.active = true;
        this.freeGameAnimBG.active = true;
        this.animBG.setAnimation(0, "FreeG", true)
    },
    onClickSwitchMode() {
        const { MESSAGE_DIALOG } = this.node.config;
        this.dialogBackToReal.active = true;
        this.dialogBackToReal.emit("SHOW_DIALOG_TRIAL", {
            canShowButton: false,
            strText: MESSAGE_DIALOG.START_TRIAL ? MESSAGE_DIALOG.START_TRIAL : "Kt thc hng dn,\ntip tc  ch  chi th.",
        })
    },
    showNoticeAskB2R() {
        const { MESSAGE_DIALOG } = this.node.config;
        this.dialogBackToReal.active = true;
        this.dialogBackToReal.emit("SHOW_DIALOG_TRIAL", {
            canShowButton: true,
            strText: MESSAGE_DIALOG.ASK_B2R ? MESSAGE_DIALOG.ASK_B2R : "Quay v ch  chi tht\n sn h.",
            actionBtnOK: () => {
                this.onClickActionOK();
            },
            actionCancel: () => {

            }
        });
    },
    onClickActionOK() {
        this.node.mainDirector.director.switchMode(null, false);
        this.trialBtn.emit("ON_CLICK_REAL_BUTTON");
    },
    showNoticeB2R() {
        const { MESSAGE_DIALOG } = this.node.config;
        this.dialogBackToReal.active = true;
        this.dialogBackToReal.emit("SHOW_DIALOG_TRIAL", {
            canShowButton: false,
            strText: MESSAGE_DIALOG.B2R ? MESSAGE_DIALOG.B2R : "Kt thc chi th\nv ang  ch  chi tht.",
        })
    }
});