
cc.Class({
    extends: cc.Component,

    properties: {
        anim: sp.Skeleton,
        animTransition: sp.Skeleton,

    },
    onLoad() {
        this.node.on("PLAY_ANIM_RESUME", this.playAnimResume, this);

        this.node.on("PLAY_ANIM_TRANSITION", this.playTransition, this);
        this.node.on("STOP_ANIM_TRANSITION", this.stopTransition, this);

        this.node.on("PLAY_ANIM_WIN", this.playAnimWin, this);
        this.node.on("PLAY_ANIM_IDLE", this.playAnimIdle, this);
    },
    playAnimResume() {
        this.animTransition.node.active = true;
        this.animTransition.setAnimation(0, 'Battle_Card_Start_Reload', false);
        this.animTransition.setCompleteListener(() => {
            this.animTransition.node.active = false;
            this.animTransition.setCompleteListener(() => { });
        });
    },
    playTransition() {
        this.animTransition.node.active = true;
        this.animTransition.setAnimation(0, 'Transition', false);
    },
    stopTransition() {
        this.animTransition.node.active = false;
    },
    playAnimWin() {
        this.anim.setAnimation(0, 'Expand_Win', true)
        this.isIdle = false;
    },
    playAnimIdle() {
        if (this.isIdle) return;
        this.isIdle = true;
        this.anim.setAnimation(0, 'Expand_Idle', true)
    }
});
