
const SKIN_ANIM = {
    C1: 'Mot_Con_Heo',
    C2: 'Hai_Con_Heo',
    C3: 'Ba_Doi_Thong_Nho',
    C4: 'Ba_Doi_Thong_Lon',
    C5: 'Tu_Quy_Nho',
    C6: 'Tu_Quy_Lon',
    C7: 'Bon_Doi_Thong_Nho',
    C8: 'Bon_Doi_Thong_Lon',
}
const SOUND_NAME = {
    C1_RABBIT: '9881_vo_tho_1',
    C2_RABBIT: '9881_vo_tho_2',
    C3_RABBIT: '9881_vo_tho_3',
    C4_RABBIT: '9881_vo_tho_4',
    C5_RABBIT: '9881_vo_tho_5',
    C6_RABBIT: '9881_vo_tho_6',
    C7_RABBIT: '9881_vo_tho_7',
    C8_RABBIT: '9881_vo_tho_8',

    C1_SWALLOW: '9881_vo_en_1',
    C2_SWALLOW: '9881_vo_en_2',
    C3_SWALLOW: '9881_vo_en_3',
    C4_SWALLOW: '9881_vo_en_4',
    C5_SWALLOW: '9881_vo_en_5',
    C6_SWALLOW: '9881_vo_en_6',
    C7_SWALLOW: '9881_vo_en_7',
    C8_SWALLOW: '9881_vo_en_8',
}

const ANIMATION = {
    INTRO_START: 'intro_Start',
    INTRO_IDLE: 'intro_Idle',
    INTRO_END: 'intro_End',

    LOBBY_START: 'lobby_Start',
    LOBBY_IDLE: 'lobby_Idle',

    RABBIT_ATTACK: 'rabbit_Attack',
    RABBIT_COUNTER: 'rabbit_Counter',
    RABBIT_IDLE: 'rabbit_Idle',
    RABBIT_RESULT: 'rabbit_Result',

    RABBIT_WIN_START: 'rabbit_Win_Start',
    RABBIT_WIN_X8_START: 'rabbit_Win_Immediately',

    RABBIT_WIN_IDLE: 'rabbit_Win_Idle',
    RABBIT_WIN_END: 'rabbit_Win_End',


    SWALLOW_ATTACK: 'swallow_Attack',
    SWALLOW_COUNTER: 'swallow_Counter',
    SWALLOW_IDLE: 'swallow_Idle',
    SWALLOW_RESULT: 'swallow_Result',

    SWALLOW_WIN_START: 'swallow_Win_Start',
    SWALLOW_WIN_X8_START: 'swallow_Win_Immediately',
    SWALLOW_WIN_IDLE: 'swallow_Win_Idle',
    SWALLOW_WIN_END: 'swallow_Win_End',

}

cc.Class({
    extends: require('CutsceneMode'),

    properties: {
        overlay: cc.Node,
        bigWildTable: cc.Node,
        stickyWild: cc.Node,
        battleIntro: sp.Skeleton,
        battleAnim: sp.Skeleton,
        animCard_1: sp.Skeleton,
        animCard_2: sp.Skeleton,
        animMultiply_1: sp.Skeleton,
        animMultiply_2: sp.Skeleton,
    },

    onLoad() {
        this.battleIntro.node.opacity = 0;
        this.battleAnim.node.opacity = 0;
        this.animCard_1.node.opacity = 0;
        this.animCard_2.node.opacity = 0;
        this.animMultiply_1.node.opacity = 0;
        this.animMultiply_2.node.opacity = 0;
        this._super();
        /*//!test
        window._testBattle = () => {
            const content = {
                freeBattleData: [{ cardID: 'C2', cardName: 'Di Heo', multiply: 1, isRabbit: true },
                { cardID: 'C5', cardName: 'T Qu Nh', multiply: 2, isRabbit: false },
                { cardID: 'C6', cardName: 'T Qu Nh Ln', multiply: 4, isRabbit: true }]
            }
            this.play(content);
        };
        //*/
    },

    enter() {
        const introDelay = 2;
        const lobbyDelay = 1;
        const attackDelay = 3.8;
        this.countRabbitAttack = 0;
        this._resetAll();
        this.canExit = false;

        const { freeBattleData } = this.content;
        const firstCardSkin = SKIN_ANIM[freeBattleData[0].cardID];
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_vo_referee_1');
        this.node.soundPlayer && this.node.soundPlayer.playMainBGM('battle');

        this.tweenBattle && this.tweenBattle.stop();
        this.tweenBattle = cc.tween(this.node)
            .delay(0.1)
            .call(() => {
                this.canExit = true;
                this.bigWildTable.active = false;
                this.stickyWild.active = false;
                this.battleIntro.node.active = true;
                this.battleIntro.setAnimation(0, 'Battle_Card_Start', false);
            })
            .delay(introDelay)
            .call(() => {
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_start_feature');
                this.overlay.active = true;
                this.battleIntro.node.active = false;
                this.battleAnim.node.active = true;
                this.battleAnim.setSkin(firstCardSkin);
                this.battleAnim.setAnimation(0, ANIMATION.LOBBY_START, false);
                this.battleAnim.addAnimation(0, ANIMATION.LOBBY_IDLE, true);
            })
            .delay(lobbyDelay);
        for (let i = 0; i < freeBattleData.length; i++) {
            const { multiply } = freeBattleData[i];
            this.tweenBattle
                .call(() => {
                    this.animShowCard(freeBattleData[i], i);
                })
                .delay(multiply == 8 ? 2.3 : attackDelay)
        }
        this.tweenBattle
            .call(() => {
                this.node.soundPlayer && this.node.soundPlayer.stopSoundByID('9881_vo_tho_emotion_1');
                this.node.soundPlayer && this.node.soundPlayer.stopSoundByID("9881_vo_en_emotion_1");
                this.playAnimWin(freeBattleData[freeBattleData.length - 1]);
            })
            .delay(2)
            .call(() => {
                this.tweenBattle = null;
                this.exit();
            })
            .start();
    },
    animShowCard(stepData, index) {
        const { cardID, multiply, isRabbit } = stepData;
        const cardSkin = SKIN_ANIM[cardID];
        if (isRabbit) this.countRabbitAttack++
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_draw_card');
        this.playAnimShowMultiply(multiply);

        if (index == 0) {
            this.firstAttack(cardID, cardSkin, isRabbit, multiply);
        } else {
            this.nextAttack(cardID, cardSkin, isRabbit, multiply);
        }
    },

    firstAttack(cardID, cardSkin, isRabbit, multiply) {
        let skin = cardSkin;
        if (cardID == "C4" || cardID == "C6" || cardID == "C8") {
            if (cardID == "C4") updateCardID = 'C3';
            if (cardID == "C6") updateCardID = 'C5';
            if (cardID == "C8") updateCardID = 'C7';
            skin = SKIN_ANIM[updateCardID];
        }
        this.animCard_1.node.active = true;
        this.animCard_1.setSkin(skin);
        this.animCard_1.setAnimation(0, isRabbit ? ANIMATION.RABBIT_ATTACK : ANIMATION.SWALLOW_ATTACK, false);

        this.battleAnim.setSkin(skin);
        this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_ATTACK : ANIMATION.SWALLOW_ATTACK, false);
        //total attack time 2.5s
        this.tweenShowCard && this.tweenShowCard.stop();
        this.tweenShowCard = cc.tween(this)
            .delay(0.9)
            .call(() => {
                const soundName = `${cardID}${isRabbit ? "_RABBIT" : "_SWALLOW"}`
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_card_hit');
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(SOUND_NAME[soundName]);
            })
            .delay(1.6)
            .call(() => {
                this.animCard_1.node.active = false;
                this.animCard_2.node.active = true;
                this.animCard_2.setSkin(skin);
                this.animCard_2.setAnimation(0, isRabbit ? ANIMATION.RABBIT_IDLE : ANIMATION.SWALLOW_IDLE, true);
                this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_IDLE : ANIMATION.SWALLOW_IDLE, true);
                this.tweenShowCard = null;
            })
            .start();
    },
    nextAttack(cardID, cardSkin, isRabbit, multiply) {
        let skin = cardSkin;
        this.battleAnim.setSkin(skin);
        this.animCard_1.setSkin(cardSkin);

        this.animCard_1.node.active = true;
        this.animCard_2.node.active = true;
        this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_COUNTER : ANIMATION.SWALLOW_COUNTER, false);
        this.animCard_1.setAnimation(0, isRabbit ? ANIMATION.RABBIT_ATTACK : ANIMATION.SWALLOW_ATTACK, false);
        this.animCard_2.setAnimation(0, isRabbit ? ANIMATION.RABBIT_COUNTER : ANIMATION.SWALLOW_COUNTER, false);

        //total attack time 2.5s
        this.tweenShowCard && this.tweenShowCard.stop();
        this.tweenShowCard = cc.tween(this)
            .delay(0.9)
            .call(() => {
                const soundName = `${cardID}${isRabbit ? "_RABBIT" : "_SWALLOW"}`
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_card_strong_hit');
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(SOUND_NAME[soundName]);
            })
            .delay(1.5)
            .call(() => {
                const soundEmotion = isRabbit ? '9881_vo_tho_emotion_2' : '9881_vo_en_emotion_2';
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(soundEmotion);
            })
            .delay(0.1)
            .call(() => {
                if (multiply < 8) this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_IDLE : ANIMATION.SWALLOW_IDLE, true);
                this.animCard_2.setSkin(cardSkin);
                this.animCard_2.setAnimation(0, isRabbit ? ANIMATION.RABBIT_IDLE : ANIMATION.SWALLOW_IDLE, true);
                this.animCard_1.node.active = false;
                this.tweenShowCard = null;
            })
            .start();
    },

    playAnimWin(stepData) {
        const { isRabbit, multiply } = stepData;
        let animName = isRabbit ? ANIMATION.RABBIT_WIN_START : ANIMATION.SWALLOW_WIN_START;
        if (multiply == 8) animName = isRabbit ? ANIMATION.RABBIT_WIN_X8_START : ANIMATION.SWALLOW_WIN_X8_START;
        this.battleAnim.setAnimation(0, animName, false);
        this.tweenAnimWin && this.tweenAnimWin.stop();
        this.tweenAnimWin = cc.tween(this)
            .delay(0.5)
            .call(() => {
                const soundEmotionMascot = isRabbit ? '9881_vo_tho_emotion_1' : "9881_vo_en_emotion_1";
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(soundEmotionMascot);
            })
            .delay(0.5)
            .call(() => {
                const soundMascotWin = isRabbit ? '9881_vo_referee_2' : "9881_vo_referee_3"
                this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_WIN_IDLE : ANIMATION.SWALLOW_WIN_IDLE, false);
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(soundMascotWin);
            })
            .call(() => {
                this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_WIN_END : ANIMATION.SWALLOW_WIN_END, false);
                this.tweenAnimWin = null;
            })
            .start();
    },
    playAnimShowMultiply(mul) {
        if (mul == 1) return;
        const animSkin = 'x' + mul;
        this.animMultiply_1.node.active = true;
        this.animMultiply_1.setSkin(animSkin);
        this.animMultiply_1.setAnimation(0, 'Counter', false)

        //total time = 2.8
        this.tweenMultiply && this.tweenMultiply.stop();
        this.tweenMultiply = cc.tween(this)
            .delay(1)
            .call(() => {
                this.animMultiply_2.node.active = false;
            })
            .delay(0.05)
            .call(() => {
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(this._getSoundMul(mul));
                this.node.soundPlayer && this.node.soundPlayer.playSfxByID(this._getSoundClause(mul));
            })
            .delay(1.75)
            .call(() => {
                this.animMultiply_1.node.active = false;
                this.animMultiply_2.node.active = true;
                this.animMultiply_2.setSkin(animSkin);
                this.animMultiply_2.setAnimation(0, 'Idle', true)
            })
            .start();
    },

    exit() {
        if (!this.canExit) return;
        this.canExit = false;
        this._resetAll();
        this.node.emit("STOP");
        if (this.node.mainDirector) this.node.mainDirector.onIngameEvent("ON_CUTSCENE_CLOSE", this.node.name);
        this.callback && this.callback();
        this.callback = null;
        this.node.active = false;
    },
    skip() {
        if (!this.node.active) return
        this.canExit = true;
        this.callback = null;
        this.exit();
    },
    _getSoundMul(multiply) {
        if (multiply == 1) return '9881_sfx_multiply_1';
        if (multiply == 2) return '9881_sfx_multiply_2';
        if (multiply == 4) return '9881_sfx_multiply_3';
        if (multiply == 8) return '9881_sfx_multiply_4';
        return '9881_sfx_multiply_4';
    },
    _getSoundClause(multiply) {
        if (multiply == 2) return '9881_sfx_applause_1_x2';
        if (multiply == 4) return '9881_sfx_applause_2_x4';
        if (multiply == 8) return '9881_sfx_applause_3_x8';
        return '9881_sfx_applause_3_x8';
    },
    _resetAll() {
        this.bigWildTable.active = true;
        this.stickyWild.active = true;
        this.overlay.active = false;

        this.tweenBattle && this.tweenBattle.stop();
        this.tweenBattle = null;
        this.tweenShowCard && this.tweenShowCard.stop();
        this.tweenShowCard = null;
        this.tweenMultiply && this.tweenMultiply.stop();
        this.tweenMultiply = null;
        this.tweenAnimWin && this.tweenAnimWin.stop();
        this.tweenAnimWin = null;

        this.battleIntro.node.opacity = 255;
        this.battleAnim.node.opacity = 255;
        this.animCard_1.node.opacity = 255;
        this.animCard_2.node.opacity = 255;
        this.animMultiply_1.node.opacity = 255;
        this.animMultiply_2.node.opacity = 255;

        this.battleIntro.node.active = false;
        this.battleAnim.node.active = false;
        this.animCard_1.node.active = false;
        this.animCard_2.node.active = false;
        this.animMultiply_1.node.active = false;
        this.animMultiply_2.node.active = false;
    }
});
