const { formatMoney } = require('utils');
const { ccclass, property } = cc._decorator;

@ccclass("JackpotObject")
class JackpotObject {
    @property() jpType: string = "";
    @property({ type: cc.Node }) jpNode: cc.Node = null;
}

@ccclass
export class LobbyJackpot extends cc.Component {
    @property({ type: JackpotObject })
    listJackpots: JackpotObject[] = [];

    jackpots: any = {};
    isInit: boolean = false;
    jackpotData: object = {};

    onLoad() {
        this.node.on("UPDATE_JACKPOT", this.jackpotUpdate, this);
    }

    jackpotUpdate(data) {
        Object.assign(this.jackpotData, data);
        this.renderJackpot(3);
    }

    updateValueJP(jpType, jpValue) {
        if (this.jackpots[jpType]) {
            const jpObject = this.jackpots[jpType];
            this.renderJP({
                node: jpObject.jpNode,
                value: jpValue,
                time: 0.3
            });
        } else {
            cc.warn("do not have jackpot", jpType);
        }
    }

    initData() {
        if (!this.isInit) {
            this.listJackpots.forEach(jpObj => {
                this.jackpots[jpObj.jpType] = jpObj;
            });
            this.isInit = true;
        }
    }

    renderJackpot(time = 3) {
        this.listJackpots.forEach(jpObj => {
            let value = parseInt(this.jackpotData[jpObj.jpType]);
            this.renderJP({
                node: jpObj.jpNode,
                value,
                time: time
            });
        });
    }

    protected renderJP({ node, value, time = 3 }) {
        if (value == null) {
            node.active = false;
            return;
        }
        if (node) {
            let label = node.getComponent(cc.Label);
            if (label && value) {
                this.tweenMoney(label, time, value);
            }
        }
    }


    protected tweenMoney(label: cc.Label, duration: number, endValue: number): any {
        if (label["_tweenMoney"]) label["_tweenMoney"].stop(); // stop if on tween;
        let currentVal: number = parseInt(label.string.replace(/[,.]/g, "")) || 0;
        const _target = { value: currentVal };
        const options: any = {};
        options.onUpdate = function () { label.string = formatMoney(Math.round(_target.value)); }
        let tweenMoney = cc.tween(_target)
            .to(duration, { value: endValue }, options)
            .call(() => {
                label.string = formatMoney(endValue);
                label["_tweenMoney"] = null;
            })
            .start();
        label["_tweenMoney"] = tweenMoney;
        return tweenMoney;
    }
}