

cc.Class({
    extends: require('CutsceneMode'),
    properties: {

    },
    onLoad() {
        this._super();
    },
    enter() {
        this.node.opacity = 0
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = cc.tween(this.node)
            .to(0.1, { opacity: 255 })
            .delay(2)
            .call(() => {
                this.tweenShow = null;
                this.exit();
            })
            .start();
    },
    exit() {
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = null;
        this._super();
    },
    skip() {
        if (!this.node.active) return;
        this.callback = null;
        this.exit();
    }
});
