
cc.Class({
    extends: cc.Component,

    properties: {
        view1: cc.Node,
        view2: cc.Node,
        buttonResize: cc.Node,
        contentNode: cc.Node,
        labelTime: cc.Node,
        canMoveSpine: true,
    },
    onLoad() {
        this.buttonResize.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.buttonResize.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.buttonResize.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.buttonResize.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);
        this.buttonWith = this.buttonResize.width;

        cc.view.on('canvas-resize', () => { this.onSceneResize(); });
        this.onSceneResize();
        this.canvas = cc.find('Canvas');
        this.camera = this.canvas.getComponentInChildren(cc.Camera);
        if (this.canMoveSpine) this.addEventMoveSpine();
    },
    addEventMoveSpine() {
        this.view1.children.forEach(item => {
            const { width, height } = item
            if (width < 100) item.width = 100;
            if (height < 100) item.height = 100;
            item.on(cc.Node.EventType.TOUCH_START, (event) => {
                const location = event.getLocation();
                const worldPos = this.camera.getScreenToWorldPoint(location, new cc.Vec2(0, 0));
                const touchPos = item.convertToNodeSpaceAR(worldPos);
                item['isHolding'] = true;
                item['touchPos'] = touchPos;
            });
            item.on(cc.Node.EventType.TOUCH_MOVE, (event) => {
                if (!item['isHolding']) return;
                const location = event.getLocation();
                const worldPos = this.camera.getScreenToWorldPoint(location, new cc.Vec2(0, 0));
                const localPos = item.parent.convertToNodeSpaceAR(worldPos);
                const touchPos = item['touchPos'];
                item.setPosition(cc.v2(localPos.x - touchPos.x, localPos.y - touchPos.y));
            });
            item.on(cc.Node.EventType.TOUCH_END, () => {
                item['isHolding'] = false;
                item['touchPos'] = null;
            });
            item.on(cc.Node.EventType.TOUCH_CANCEL, () => {
                item['isHolding'] = false;
                item['touchPos'] = null;
            });
        })
    },

    //DraggablePanel
    onSceneResize() {
        const visibleSize = cc.view.getVisibleSize();
        this.node.setContentSize(visibleSize);
        this.view1.height = visibleSize.height;
        this.view2.height = visibleSize.height;
        this.view2.children.forEach(item => { item.height = visibleSize.height; })
        this.buttonResize.height = visibleSize.height;
        this.labelTime.setPosition(cc.v2(-visibleSize.width / 2, visibleSize.height / 2))
        this._updateView();
    },
    onTouchMove(event) {
        if (!this.isResizing) return;

        //button
        const delta = event.getDelta();
        const fullWidth = cc.view.getVisibleSize().width;
        const minX = -fullWidth / 2 + (this.buttonWith / 2) + 300;
        const maxX = fullWidth / 2 - (this.buttonWith / 2) - 300;
        let moveX = this.buttonResize.position.x + delta.x;
        if (moveX < minX) moveX = minX;
        if (moveX > maxX) moveX = maxX;
        this.buttonResize.setPosition(moveX, 0);

        //view
        this._updateView();
    },
    _updateView() {
        const haftPanelWith = this.node.width / 2;
        const buttonPos = this.buttonResize.getPosition();
        const haftButtonWith = this.buttonWith / 2;
        const haftLeftSize = haftPanelWith + (buttonPos.x - haftButtonWith);
        const haftRightSize = haftPanelWith - (buttonPos.x + haftButtonWith);

        this.view1.width = haftLeftSize;
        this.view2.width = haftRightSize;

        const view1Pos = -haftPanelWith + (haftLeftSize / 2);
        const view2Pos = haftPanelWith - (haftRightSize / 2);
        this.view1.setPosition(cc.v2(view1Pos, 0));
        this.view2.setPosition(cc.v2(view2Pos, 0));
        this.contentNode.setPosition(cc.v2(0, this.view2.height / 2));
    },
    onTouchStart() {
        this.isResizing = true;
    },
    onTouchEnd() {
        this.isResizing = false;
    }
});
