cc.Class({
    extends: require('SlotButtonV2'),

    properties: {
        freeBtnSpine: sp.Skeleton,
        freeBtnHoverSpine: sp.Skeleton,
    },

    onLoad() {
        this._super();
        if (this.fastToResultBtn) {
            this.fastToResultBtn.on(cc.Node.EventType.MOUSE_LEAVE, this.onMouseLeaveBtn.bind(this));
            this.fastToResultBtn.on(cc.Node.EventType.MOUSE_ENTER, this.onHoverBtn.bind(this));
        }
        this.ftrBtnComponent = this.fastToResultBtn.getComponent(cc.Button);
    },

    onMouseLeaveBtn(){
        if (this.freeBtnHoverSpine && this.freeBtnHoverSpine.node.active === true) {
            this.freeBtnHoverSpine.node.active = false;
        }
    },

    onHoverBtn() {
        if (this.freeBtnHoverSpine && this.ftrBtnComponent.interactable) {
            this.freeBtnHoverSpine.node.active = true;
            this.freeBtnHoverSpine.setAnimation(0, this.animHover, true);
        }
    },

    enableFastToResult() {
        this.ftrBtnComponent.interactable = true;
        if (this.textSpin && this.txtPressToStop) {
            this.textSpin.getComponent(cc.Sprite).spriteFrame = this.txtPressToStop;
        }
        if (this.freeBtnSpine) {
            this.freeBtnSpine.setAnimation(0, this.animFastToResult, false);
        }
    },
    disableFastToResult() {
        this.ftrBtnComponent.interactable = false;
        if (this.freeBtnSpine) {
            this.freeBtnSpine.setAnimation(0, this.animDisable, false);
        }
    },
});