cc.Class({
    extends: require('SlotReelv2'),

    onLoad() {
        this._super();
        this.node.on("HIDE_SYMBOL_BIG_WILD", this.hideSymbolForBigWild, this);
        this.node.on("RESET_SYMBOL_BIG_WILD", this.resetSymbolBigWild, this);

        this.symbols = [];
        this.symbolPos = [];
    },
    init(showNumber, gameConfig, col, symbolPrefab, isFreeMode = false) {
        this.col = col;
        this.config = gameConfig;
        if (isFreeMode) {
            this.symbolList = this.config.SYMBOL_NAME_LIST_FREE[col];
        } else {
            this.symbolList = this.config.SYMBOL_NAME_LIST[col];
        }
        this.symbolPrefab = symbolPrefab;
        this.showNumber = showNumber;
        this.showSymbols = [];
        this.totalNumber = this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP + this.config.TABLE_SYMBOL_BUFFER.BOT;
        this.isFreeMode = isFreeMode;
        this.symbolStartY = - (this.config.TABLE_FORMAT[col] / 2 + this.config.TABLE_SYMBOL_BUFFER.BOT - 0.5) * this.config.SYMBOL_HEIGHT;
        for (let i = 0; i < this.totalNumber; ++i) {
            const symbol = cc.instantiate(this.symbolPrefab);
            symbol.name = "Symbol_" + i;
            symbol.parent = this.reel;
            symbol.col = col;
            symbol.setPosition(0, this.symbolStartY + i * this.config.SYMBOL_HEIGHT);
            symbol.changeToSymbol(this.getRandomSymbolNameWithExceptions(['K1', 'K2', 'K']));
            symbol.isRealSymbol = true;

            this.symbols.push(symbol);
            this.symbolPos.push(symbol.position.y);
            if (i >= this.config.TABLE_SYMBOL_BUFFER.BOT && this.showSymbols.length < this.showNumber) {
                symbol.changeToSymbol(this.getRandomSymbolName());
                symbol.isRealSymbol = false;
                this.showSymbols.unshift(symbol);
            }
        }

        this.mode = 'FAST';
        this.curentConfig = this.config.STATS[this.mode];
        this.index = 0;
        this.reset();
    },
    getRandomSymbolName() {
        const includesWild = this.symbols.some(symbol => (symbol.symbol == 'K1' || symbol.symbol == 'K2'));
        if (includesWild) {
            return this.getRandomSymbolNameWithExceptions(['K1', 'K2']);
        } else {
            let randomSymbol = this.symbolList[Math.floor(Math.random() * this.symbolList.length)];
            return randomSymbol;
        }
    },
    onReelStop() {
        this.symbols.forEach((child) => {
            const { symbol, fakeSymbol } = child
            child.changeToSymbol(symbol, fakeSymbol);
        });
    },
    hideSymbolForBigWild() {
        this.showSymbols.forEach((symbol) => {
            symbol.isHideForBigWild = true;
            symbol.active = false;
        });
    },
    resetSymbolBigWild() {
        this.showSymbols.forEach((symbol, index) => {
            symbol.isHideForBigWild = false;
            symbol.active = true;
        });
    },
    startSpinningWithDelay(delay) {
        this._super(delay);
        this.isNearWin = false;
    },
    stopSpinningWithDelay(delay, matrix = [], callback) {
        this.delayIndex = delay;
        this.showSymbols = [];
        this.matrix = matrix;
        this.callbackStopReel = callback ? callback : () => { };
        let reelDelayStop = delay * this.curentConfig.REEL_DELAY_STOP;
        this.isNearWin = false;
        this.delay = delay;

        cc.director.getScheduler().schedule(this.setStepToStop, this, 0, 0, reelDelayStop, false);
        this.matrix.unshift(this.getRandomSymbolNameWithExceptions(['K1', 'K2', 'K']));
        this.matrix.push(this.getRandomSymbolNameWithExceptions(['K1', 'K2', 'K']));
    },
    runSpinning() {
        this.runSpinningAnimation(() => {
            if (this.step > this.showNumber) {
                this.runSpinning();
                this.step--;
                if (this.step < this.totalNumber) {
                    this.showResult = 1;
                }
            } else if (this.step == this.showNumber) {
                // check last reel, near win and not fast to result
                if (this.delayIndex === (this.config.TABLE_FORMAT.length - 1) && this.isNearWin && !this.isFastToResult) {
                    this.runStopAnimation(50, 0.2);
                } else {
                    if (this.isFastToResult) {
                        this.runStopAnimation(0, this.curentConfig.REEL_EASING_TIME);
                    } else {
                        this.runStopAnimation(this.curentConfig.REEL_EASING_DISTANCE, this.curentConfig.REEL_EASING_TIME);
                    }
                }
            }
        });
    },
    circularSymbols() {
        const lastSymbol = this.reel.children[this.index % (this.totalNumber)];
        if (!this.showResult) {
            const symbolValue = this.getRandomSymbolName();
            if (this.isSlowDown) {
                lastSymbol.changeToSymbol(symbolValue);
            } else {
                lastSymbol.changeToBlurSymbol(symbolValue);
            }
        } else if (this.stop < this.totalNumber) {
            let isRealSymbol = this.stop >= this.config.TABLE_SYMBOL_BUFFER.TOP && this.stop < this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP;
            let symbolValue = this.matrix[this.stop];
            this.step = this.totalNumber + this.showNumber - (this.stop + this.config.TABLE_SYMBOL_BUFFER.BOT);
            if (isRealSymbol) {
                const fakeSymbol = (symbolValue == "K1" || symbolValue == "K2") ? this.getRandomSymbolNameWithExceptions(['K1', 'K2']) : null;
                lastSymbol.changeToSymbol(symbolValue, fakeSymbol);
                this.usingMotionBlur && lastSymbol.stopBlur();
                this.showSymbols.unshift(lastSymbol);
            } else {
                if (this.isSlowDown) {
                    symbolValue = this.hasFakeWildBottom() ? "K1" : symbolValue;
                    lastSymbol.changeToSymbol(symbolValue);
                } else {
                    lastSymbol.changeToBlurSymbol(symbolValue);
                }
            }
            this.stop++;
        }
        lastSymbol.y = lastSymbol.y + this.config.SYMBOL_HEIGHT * (this.totalNumber);
        this.index++;
    },
    hasFakeWildBottom() {
        const includesWild = this.symbols.some(symbol => symbol['symbol'].includes("K"));
        if (includesWild) return false;
        if (this.stop) return false;
        if (!this.isNearWin) return false;
        const rand = Math.random();
        if (rand > 0.3) return false;
        if (this.matrix.includes("K1")) return false;
        return true;
    },
    extendTimeToStop(nearWin, count) {
        this.isNearWin = nearWin;
        let reelDelayStop = 0;
        reelDelayStop = (this.curentConfig.REEL_DELAY_STOP * this.delay) + (this.curentConfig.NEAR_WIN_DELAY_TIME * count);
        cc.director.getScheduler().unschedule(this.setStepToStop, this);
        cc.director.getScheduler().schedule(this.setStepToStop, this, 0, 0, reelDelayStop, false);
    },
    playNearWin() {
        if (this.isSlowDown) return;
        this.isSlowDown = true;
        this._timeStep = this.currentSpeed;
        this._tweenSpeed && this._tweenSpeed.stop();
        this._tweenSpeed = cc.tween(this)
            .to(2.5, { _timeStep: this.currentSpeed * 5 })
            .call(() => {
                this._tweenSpeed = null;
            })
            .start();
    },
    runSpinningAnimation(callback) {
        let time = this.currentSpeed + this.currentSpeed * this.stop / 4;
        if (this.isSlowDown) time = this._timeStep;
        const action0 = cc.sequence(
            cc.moveBy(time, 0, -1 * this.config.SYMBOL_HEIGHT),
            cc.callFunc(this.circularSymbols, this),
            cc.callFunc(callback)
        );
        this.reel.runAction(action0);
    },
    fastStopSpinning() {
        this._tweenSpeed && this._tweenSpeed.stop();
        this._tweenSpeed = null;
        this._super();
    },

    reset() {
        this.symbols.sort((a, b) => a.position.y - b.position.y);
        this.symbols.forEach((symbol, index) => { symbol.setPosition(0, this.symbolPos[index]); });
        this.isSlowDown = false;
        this._tweenSpeed && this._tweenSpeed.stop();
        this._tweenSpeed = null;
        this.reel.y = 0;
        this.index = this.index % (this.totalNumber);
        this.stop = 0;
        this.step = this.MAX_STEP;
        this.showResult = 0;
        this.matrix = [];
    },
});