
cc.Class({
    extends: cc.Component,

    properties: {
        frameRabbit: cc.Node,
        frameSwallow: cc.Node,
        frameMultiply: cc.Sprite,
        spriteFramesMul: [cc.SpriteFrame],
    },
    onLoad() {
        this.node.on("SHOW_BATTLE_DATA", this.showBattleData, this);
        this.node.on("RESET", this.reset, this);

    },
    showBattleData(data) {
        if (!data || !data.result || !data.result.metaDataDetail) return;
        const { sFt, fGb, mTf } = data.result.metaDataDetail;
        if (!sFt || !fGb || !mTf) return;

        let isRabbit = fGb == "K2";
        const listFT = sFt.split(',');
        for (let i = 0; i < listFT.length; i++) {
            if (i > 0) isRabbit = !isRabbit;
        }
        this.frameRabbit.active = isRabbit;
        this.frameSwallow.active = !isRabbit;

        const multiplys = [2, 4, 8];
        const indexMul = multiplys.indexOf(mTf);
        let spriteFrame = this.spriteFramesMul[indexMul];
        this.frameMultiply.spriteFrame = spriteFrame;
    },
    reset() {
        this.frameRabbit.active = false;
        this.frameSwallow.active = false;
        this.node.active = false;
    }
});
