const MAP_MULTIPLY = {
    2: 0,
    3: 1,
    4: 2
};
cc.Class({
    extends: require('SlotTableHistory'),

    properties: {
        bigWilds: [cc.Node],
        multiplyAssets: [cc.SpriteFrame],
    },

    renderResult(data) {
        const { betDenom, matrixFormat, matrixResult, result, mode } = data;
        const nSubSym = result && result.nSubSym ? result.nSubSym : '';
        const freeWildMultipliers = result && result.freeWildMultipliers ? result.freeWildMultipliers : '';

        this.normalGame.active = false;
        this.freeGame.active = false;
        if (this.bonusGame) this.bonusGame.active = false;
        if (this.topupGame) this.topupGame.active = false;
        this.clearTable();
        this.betDenom = betDenom;
        this.currentMode = mode;
        this.martrixFormat = matrixFormat;
        this.scaleNode.scale = this.scaleRate;
        switch (mode) {
            case "normal":
                this.normalGame.active = true;
                this.renderGameTable(matrixResult, matrixFormat, nSubSym, freeWildMultipliers);
                this.renderExtendData(data);
                break;
            case "free":
                this.freeGame.active = true;
                this.renderGameTable(matrixResult, matrixFormat, nSubSym, freeWildMultipliers);
                this.renderExtendData(data);
                break;
        }
    },

    renderGameTable(matrix, tableFormat, normalSubSymbol, wildMultiply) {
        this.bigWilds.forEach((item) => {
            if (item && item.getChildByName('multiply')) {
                let itemMultiply = item.getChildByName('multiply').getComponent(cc.Sprite);
                if (itemMultiply) itemMultiply.spriteFrame = null;
                item.opacity = 0;
            }
        });
        const startX = (-tableFormat.length / 2 + 0.5) * (this.node.config.SYMBOL_WIDTH);
        let count = 0;

        for (let col = 0; col < tableFormat.length; col++) {
            const HEIGHT_STEP = this.node.config.SYMBOL_HEIGHT;
            const startY = (tableFormat[col] / 2 - 0.5) * (HEIGHT_STEP - 15);
            for (let row = 0; row < tableFormat[col]; row++) {
                let symbol = null;
                symbol = this.getSymbol(this.currentMode);
                symbol.parent = this.symbolNode;
                symbol.emit("RESET");
                symbol.removeSubSymbol();
                symbol.setPosition(startX + col * (this.node.config.SYMBOL_WIDTH), startY - row * (HEIGHT_STEP));
                symbol.changeToSymbol(matrix[count]);
                symbol.col = col;
                symbol.row = row;
                symbol.val = matrix[count];

                //for sub symbol
                if (symbol && normalSubSymbol && normalSubSymbol.includes(count) && this.currentMode === "normal") {
                    symbol.addSubSymbol();
                    symbol.showSubSymbol();
                }

                //for big wild in free
                if (matrix[count] === "K" && col >= 1 && this.currentMode === "free" && symbol) {
                    symbol.opacity = 0;
                    let bigWild = this.bigWilds[col - 1];
                    let currentMultiply = bigWild.getChildByName('multiply').getComponent(cc.Sprite);

                    if (wildMultiply && wildMultiply.length === 5) {
                        const value = wildMultiply[col];
                        const isEnable = value > 1;
                        currentMultiply.spriteFrame = isEnable ? this.multiplyAssets[MAP_MULTIPLY[value]] : null;
                    }

                    bigWild.opacity = 255;
                }

                count++;
            }
        }
    },
});
