const { GUI } = require('dat.gui');
const NETWORK_STATE = {
    NORMAL: 0, 
    SLOW: 1
}
cc.Class({
    extends: cc.Component,

    properties: {
        spinWheel: require("SpinWheel"),
        resultLabel: cc.Label
    },
    start() {
        this._config = this.spinWheel.getConfig();
        this.wheelOptions = {
            "maxSpeed": this._config.maxSpeed,
            "endSpeed": this._config.endSpeed,
            "delayStop": this._config.delayStop,
            "speedUpTime": this._config.speedUpTime,
            "slowDownTime": this._config.slowDownTime,
            "bufferStop": this._config.bufferStop,
            // rotate a bit more before stop with endSpeed
            "totalItem": 12,
            "result": 0,
            "Export Config": () => { this._exportConfig() }
        }
        this._createGui();
        this.networkState = NETWORK_STATE.NORMAL;
    },
    _createGui() {
        const gui = new GUI();
        gui.domElement.parentElement.style.zIndex = 1000;
        this.wheelGui = gui.addFolder("Wheel Config")
        for (let key in this.wheelOptions) {
            let control = this.wheelGui.add(this.wheelOptions, key);
            if (typeof this.wheelOptions[key] === "function") continue;
            control.onChange((value) => {
                this.wheelOptions[key] = value;
                this._updateConfig(key, value);
            })
        }
        this._showResultLabel();
        this.wheelGui.open();
    },
    _updateConfig(key, value) {
        if (key === "result") {
            this.spinWheel.setResult(value);
            this._showResultLabel();
        } else {
            this._config[key] = value;
        }
        this.spinWheel.init(this._config);
    },
    _showResultLabel() {
        const label = this.resultLabel;
        label.string = this.wheelOptions.result;
        const resultItem = label.node.parent;
        resultItem.active = true;
        resultItem.angle = 360 / this._config.totalItem * this.wheelOptions.result;
    },
    _exportConfig() {
        let dataStr = JSON.stringify(this._config);
        let dataUri = 'data:application/json;charset=utf-8,' + encodeURIComponent(dataStr);
        let exportFileDefaultName = 'wheelConfig.json';
        let linkElement = document.createElement('a');
        linkElement.setAttribute('href', dataUri);
        linkElement.setAttribute('download', exportFileDefaultName);
        linkElement.click();
    },
    testSpin() {
        this.spinWheel.node.emit("START_SPIN")
        this.tweenStart = cc.tween(this);
        const { delayStop } =  this.wheelOptions;
        switch (this.networkState) {
            case NETWORK_STATE.NORMAL:
                this.tweenStart
                    .delay(0.2)
                    .call(() => {
                        this.spinWheel.setResult(this.wheelOptions.result);
                    })
                    .delay(this._config.speedUpTime + delayStop)
                    .call(() => {
                        this.spinWheel.node.emit("STOP_SPIN")
                    });
                break;
            case NETWORK_STATE.SLOW:
                this.tweenStart
                    .delay(this._config.speedUpTime + delayStop)
                    .call(() => {
                        this.spinWheel.setResult(this.wheelOptions.result);
                        this.spinWheel.node.emit("STOP_SPIN");
                    });
                break;
        }
        this.tweenStart.start();
    },
});
