const MAP_MULTIPLY = {
    2: 0,
    3: 1,
    4: 2
};
const MAP_SKIN = {
    1: 'Left',
    2: 'Middle',
    3: 'Right'
};
cc.Class({
    extends: cc.Component,

    properties: {
        playerGoal: cc.Node,
        bigWildHolder: cc.Node,
        bigWildPrefab: cc.Prefab,
        wildMultiplyHolder: cc.Node,
        wildMultiplyPrefab: cc.Prefab,
        multiplyAssets: [cc.SpriteFrame],
        effectExpandHolder: cc.Node,
        wildExpandEffectPrefabName: 'WildExpandEffect'
    },

    onLoad() {
        this.node.on("SHOW_BIG_WILD",this.showBigWild,this);
        this.node.on("RESET_BIG_WILD", this.resetBigWild, this);
        this.node.on("SKIP_ALL_EFFECT", this.skipAllEffect, this);
        this.node.on("STOP_SOUND_BIG_WILD", this.stopSoundBigWild, this);
        this.bigWilds = [];
        this.multiplyNodes = [];
        this.usingObj = [];
        this.playerGoalSkeleton = this.playerGoal.getComponent(sp.Skeleton);
        this.playerGoal.active = false;
        this.startWildIndex = 0;
        this.countWild = 0;
        this.createBigWilds();
    },

    createBigWilds(){
        const { SYMBOL_WIDTH, TABLE_FORMAT_FREE } = this.node.config;
        let startX = -(TABLE_FORMAT_FREE.length / 2 - 0.5) * SYMBOL_WIDTH;
        let count = 0;
        for(let i = 1; i<TABLE_FORMAT_FREE.length - 1; i++){
            count++;
            const bigWild = cc.instantiate(this.bigWildPrefab);
            const position = cc.v2(startX + count*SYMBOL_WIDTH, 0);
            if(bigWild){
                bigWild.parent = this.bigWildHolder;
                bigWild.setPosition(position);
                bigWild.active = false;
                this.bigWilds.push(bigWild);
            }
            const multiply = cc.instantiate(this.wildMultiplyPrefab);
            if (multiply) {
                multiply.parent = this.wildMultiplyHolder;
                multiply.setPosition(position);
                multiply.active = false;
                this.multiplyNodes.push(multiply);
            }
        }
    },

    showBigWild({matrix, multiply}) {
        let wildData = [];
        for (let col = 0; col < matrix.length; col++) {
            const value = multiply[col];
            for (let row = 0; row < matrix[0].length; row++) {
                if (matrix[col][row] == 'K') {
                    this.countWild++;
                    if (this.startWildIndex === 0) {
                        this.startWildIndex = col;
                    }
                    wildData.push({col, value});
                }
            }
        }
        if (this.node.soundPlayer) this.node.soundPlayer.playSfxPlayerKick();
        this.playerGoal.active = true;
        this.playerGoalSkeleton.setAnimation(0, this.getSpineAnimationName(), false);
        this.playerGoalSkeleton.setCompleteListener(() => {
            this.playerGoal.active = false;
            this.playerGoalSkeleton.setCompleteListener(() => {});
        });


        const timeShooting = 0.85;
        this.callbackShowWild = () => {
            wildData.forEach(({ col, value}) => {
                this.setUpWild(col, value);
            });
            if (this.node.soundPlayer) this.node.soundPlayer.playSfxExpandWild();
            if (this.node.soundPlayer) this.node.soundPlayer.playSfxMultiply();
            this.node.runAction(cc.sequence(
                cc.delayTime(1),
                cc.callFunc(() => {
                    if (this.node.soundPlayer) this.node.soundPlayer.playSfxWinBigWild();
                })
            ));
            this.callbackShowWild = null;
        };
        this.scheduleOnce(this.callbackShowWild, timeShooting);
    },

    setUpWild(col, value) {
        if (col === 0 || col === this.node.config.TABLE_FORMAT_FREE.length - 1) {
            cc.error("i noooo, cheat sai ri");
            return;
        }
        const currentWild = this.bigWilds[col - 1];
        currentWild.active = true;
        const skeleton = currentWild.getComponent(sp.Skeleton);
        skeleton.setSkin(MAP_SKIN[col]);
        skeleton.setSlotsToSetupPose();
        skeleton.setAnimation(0, 'Appear', false);
        skeleton.addAnimation(0, 'Idle', true);
        this.showMultiply(value, col);
        this.playAnimFire(currentWild, col);
    },

    showMultiply(value, col) {
        const isEnable = value > 1;
        const currentMultiply = this.multiplyNodes[col - 1];
        currentMultiply.getComponent(cc.Sprite).spriteFrame = isEnable ? this.multiplyAssets[MAP_MULTIPLY[value]] : null;
        currentMultiply.active = isEnable;
        currentMultiply.scale = 0.5;
        currentMultiply.runAction(cc.sequence(
            cc.delayTime(0.2),
            cc.scaleTo(0.2, 1.2).easing(cc.easeBackOut()),
            cc.scaleTo(0.1, 1).easing(cc.easeBackOut())
        ));
    },

    playAnimFire(currentWild, col) {
        const { SYMBOL_WIDTH, SYMBOL_HEIGHT, TABLE_FORMAT_FREE } = this.node.config;
        const showNumber = TABLE_FORMAT_FREE[col];
        for (let index = 0; index < showNumber; index++) {
            const randomNumber = Math.random() < 0.5 ? -1 : 1;
            const effect = this.node.poolFactory.getObject(this.wildExpandEffectPrefabName);
            const posX = currentWild.x;
            const posY = ((showNumber/2 - index - 0.5)) * SYMBOL_HEIGHT;
            const randomPosX = posX + (Math.random() * (SYMBOL_WIDTH / 2) * randomNumber);
            const randomPosY = posY + ((Math.random() * (SYMBOL_HEIGHT / 2) * randomNumber));
            effect.position = cc.v2(randomPosX, randomPosY);
            effect.active = true;
            effect.getComponent(cc.ParticleSystem).resetSystem();
            this.effectExpandHolder.addChild(effect);
            this.usingObj.push(effect);
        }
    },

    getSpineAnimationName() {
        let animname = '3';
        if (this.countWild === 2) {
            animname = this.startWildIndex === 1 ? '2' : '4';
        } else if (this.countWild === 1) {
            switch (this.startWildIndex) {
                case 1:
                    animname = '1';
                    break;
                case 2:
                    animname = '3';
                    break;
                case 3:
                    animname = '5';
                    break;
            }
        }
        return animname;
    },

    skipAllEffect() {
        if (this.callbackShowWild) {
            this.unschedule(this.callbackShowWild);
            this.callbackShowWild();
        }
        this.playerGoalSkeleton.setCompleteListener(() => {});
        this.playerGoal.active = false;
    },

    resetBigWild(){
        if (this.callbackShowWild) {
            this.unschedule(this.callbackShowWild);
            this.callbackShowWild = null;
        }
        this.playerGoalSkeleton.setCompleteListener(() => {});
        this.playerGoal.active = false;
        this.startWildIndex = 0;
        for (let col=0; col<this.multiplyNodes.length; col++){
            const currentWild = this.bigWilds[col];
            currentWild.active = false;
            const currentMultiply = this.multiplyNodes[col];
            currentMultiply.getComponent(cc.Sprite).spriteFrame = null;
            currentMultiply.active = false;
            currentMultiply.stopAllActions();
        }
        this.usingObj.forEach(obj => {
            obj.active = false;
            this.node.poolFactory.removeObject(obj);
        });
        this.usingObj = [];
        this.countWild = 0;
    },

    stopSoundBigWild() {
        this.node.stopAllActions();
        if (this.node.soundPlayer) this.node.soundPlayer.stopSfxWinBigWild();
    },
});
