const PoolFactory = require('PoolFactory');

cc.Class({
    extends: require('SlotTablePaylinev2'),

    properties: {
        poolFactory: {
            type: PoolFactory,
            default: null,
        },
        winLineFrameHolder: cc.Node,
        symbolSpinePrefabName: 'SlotSymbolPayline',
        winLineFramePrefabName: 'WinLineFrame',
        isFreeGame: false
    },

    onLoad() {
        this._super();
        this.usingObj = [];
        this.node.poolFactory = this.poolFactory;
        this.node.on("INTRO_WILD", this.introWildFree, this);
        this.node.on("RESET_WILD", this.resetWild, this);
    },

    setupPaylines(matrix, payLines) {
        this.node.curentConfig = this.node.config.STATS[this.node.mode];
        this.paylineHolderNode.active = true;
        this.payLineNormals = payLines;
        this.paylinesMatrix = [];
        this.wildHolderNode = [];

        for (let col = 0; col < this.node.reels.length; ++col) {
            this.paylinesMatrix[col] = [];
            for (let row = 0; row < this.node.reels[col].showSymbols.length; ++row) {
                const symbol = this.node.reels[col].showSymbols[row];
                const paylineSymbol = this.createPaylineSymbol(this.node.reels[col],symbol.symbol, col, row);
                const winLineFrame = this.createWinLineFrame(col, row, this.node.reels[col]);
                symbol.emit("INIT_FOR_PAYLINE", paylineSymbol.isSymbolAnimated);
                const payline = { symbol, paylineSymbol, winLineFrame };
                this.paylinesMatrix[col][row] = payline;
                if (symbol.symbol == "K") {
                    this.wildHolderNode.push(payline);
                }
            }
        }

        this.paylineHolderNode.opacity = 0;
    },

    createPaylineSymbol(reel, symbol, col, row) {
        let paylineSymbol;
        if(this.poolFactory){
            paylineSymbol = this.poolFactory.getObject(this.symbolSpinePrefabName);
            paylineSymbol.active = true;
        }
        this.usingObj.push(paylineSymbol);
        paylineSymbol.parent = this.paylineHolderNode;
        paylineSymbol.x = this.getXPosition(col);
        paylineSymbol.y = ((reel.showNumber/2 - row - 0.5)) * this.node.config.SYMBOL_HEIGHT;
        paylineSymbol.changeToSymbol(symbol);
        paylineSymbol.disableHighlight();
        return paylineSymbol;
    },

    createWinLineFrame(col, row, reel) {
        let winLineFrame = this.poolFactory.getObject(this.winLineFramePrefabName);
        if(winLineFrame){
            winLineFrame.active = false;
            winLineFrame.parent = this.winLineFrameHolder;
            winLineFrame.x = this.getXPosition(col);
            winLineFrame.y = ((reel.showNumber/2 - row - 0.5)) * this.node.config.SYMBOL_HEIGHT;
            this.usingObj.push(winLineFrame);
        }
        return winLineFrame;
    },

    blinkNormalPaylineAllline({symbolId, symbolCount}) {
        if (!this.paylinesMatrix || !this.paylinesMatrix.length) return;
        for (let col = 0; col < symbolCount; col++) {
            for (let row = 0; row < this.paylinesMatrix[col].length; row++) {
                const {symbol, paylineSymbol, winLineFrame} = this.paylinesMatrix[col][row];
                if (paylineSymbol && symbol) {
                    if (symbol.symbol == symbolId || symbol.symbol == "K") {
                        symbol.blinkHighlight(this.node.curentConfig.BLINK_DURATION, this.node.curentConfig.BLINKS);
                        paylineSymbol.blinkHighlight(this.node.curentConfig.BLINK_DURATION, this.node.curentConfig.BLINKS);
                        if (winLineFrame) winLineFrame.active = true;
                    }
                    if (symbol.symbol == "K" && this.isFreeGame) {
                        symbol.active = false;
                        paylineSymbol.active = false;
                    }
                }
            }
        }
    },

    showNormalPaylineAllLine({symbolId, symbolCount}) {
        if (!this.paylinesMatrix || !this.paylinesMatrix.length) return;
        this.disableHighlightNormalPaylines();
        for (let col = 0; col < symbolCount; col++) {
            for (let row = 0; row < this.paylinesMatrix[col].length; row++) {
                const {symbol, paylineSymbol, winLineFrame} = this.paylinesMatrix[col][row];
                if (paylineSymbol && symbol) {
                    if (symbol.symbol == symbolId || symbol.symbol == "K") {
                        symbol.emit('ENABLE_HIGHLIGHT');
                        paylineSymbol.emit('PLAY_ANIMATION', true);
                        paylineSymbol.emit('ENABLE_HIGHLIGHT');
                        if(winLineFrame) winLineFrame.active = true;
                    }
                    if (symbol.symbol == "K" && this.isFreeGame) {
                        symbol.active = false;
                        paylineSymbol.active = false;
                    }
                }
            }
        }
    },

    resetSymbolPaylines(isForce = false) {
        if (!this.paylinesMatrix || this.paylinesMatrix.length === 0) return;
        for (let col = 0; col < this.paylinesMatrix.length; ++col) {
            for (let row = 0; row < this.paylinesMatrix[col].length; ++row) {
                const {symbol, paylineSymbol, winLineFrame} = this.paylinesMatrix[col][row];
                if(symbol && paylineSymbol){
                    symbol.active = true;
                    paylineSymbol.active = true;
                    symbol.emit('RESET');
                    paylineSymbol.emit('RESET');
                    if (this.isFreeGame && symbol && symbol.symbol === "K" && !isForce) {
                        symbol.opacity = 0;
                    }
                }
                if(winLineFrame) winLineFrame.active = false;
            }
        }
    },

    disableHighlightNormalPaylines() {
        if (!this.paylinesMatrix || !this.paylinesMatrix.length) return;
        for (let col = 0; col < this.paylinesMatrix.length; ++col) {
            for (let row = 0; row < this.paylinesMatrix[col].length; ++row) {
                const {symbol, paylineSymbol, winLineFrame} = this.paylinesMatrix[col][row];
                if(symbol && paylineSymbol){
                    symbol.emit('DISABLE_HIGHLIGHT');
                    paylineSymbol.emit('DISABLE_HIGHLIGHT');
                }
                if(winLineFrame) winLineFrame.active = false;
            }
        }
    },

    clearPaylines() {
        for (let i = 0; i < this.usingObj.length; i++) {
            let removedObj = this.usingObj[i];
            this.poolFactory.removeObject(removedObj);
        }
        this.usingObj = [];
        this._super();
    },

    resetWild() {
        for (let i = 0; i < this.usingObj.length; i++) {
            let removedObj = this.usingObj[i];
            this.poolFactory.removeObject(removedObj);
        }
        this.usingObj = [];
        this.resetSymbolPaylines(true);
        this.paylineHolderNode.removeAllChildren();
        this.paylineHolderNode.active = false;
        this.paylinesMatrix = [];
        this.wildHolderNode = [];
    },

    introWildFree() {
        this.setupPaylines(null, null);
        this.paylineHolderNode.opacity = 255;
        this.wildHolderNode.forEach((child) => {
            child.symbol.enableHighlight();
            child.symbol.playAnimation();
            child.paylineSymbol.enableHighlight();
            child.paylineSymbol.playAnimGoalIdle();
        });
    },
});