
cc.Class({
    extends: cc.Component,

    properties: {
        poolFactory: require('PoolFactory'),
        tableTrailHolder: cc.Node,
    },

    onLoad() {
        this.node.on("ON_CHANGE_SYMBOL_TO_WILD", this.onChangeSymbolWild, this);
        this.node.on("SET_UP_PAYLINES_AT_REEL", this.setUpPaylineAtReel, this);
        this.node.on("CLEAR", this.onChangeSymbolWild, this);
        this.usingObject = [];
    },

    onChangeSymbolWild(event) {
        event.stopPropagation();
        const data = event.getUserData();
        const { symbolName, node } = data;
        const { col } = node;
        if (!this.usingObject[col]) {
            this.usingObject[col] = [];
        }
        this.usingObject[col].push(this._createTrail(symbolName, node));
    },
    _createTrail(symbolName, node) {
        const trail = this.poolFactory.getObject("TrailSymbol9881");
        trail.parent = this.tableTrailHolder;
        trail.active = true;
        trail.emit("INIT", symbolName, node);
        return trail;
    },
    setUpPaylineAtReel(col) {
        if (this.usingObject[col] && this.usingObject[col].length) {
            while (this.usingObject[col].length > 0) {
                const removedObject = this.usingObject[col].pop();
                removedObject.emit("RESET", () => {
                    this.poolFactory.removeObject(removedObject);
                });
            }
        }
    },
});
