cc.Class({
    extends: require('SlotTablePaylinev2'),

    properties: {
        bigWildTable: cc.Node,
        drawingLineTable: cc.Node,
        poolFactory: require('PoolFactory'),
        winLineHolderNode: cc.Node,
        winLineBigWild: cc.Node,
        stickyWild: cc.Node,
        backGroundReelLeft: cc.Node,
        backGroundReelRight: cc.Node,
        dimColor: cc.Color,
    },

    onLoad() {
        this._super();
        this.node.on("SET_UP_BIG_WILD", this.setupBigWild, this);
        this.node.on("SHOW_BIG_WILD", this.showBigWild, this);
        this.node.on("START_SPINNING", this.startSpinning, this);
        this.node.on("SET_UP_PAYLINES_AT_REEL", this.setUpPaylineAtReel, this);
        this.node.on("RUN_ANIM_NEAR_WIN", this.playAnimNearWin, this);
        this.node.on("STOP_ANIM_NEAR_WIN", this.stopAnimNearWin, this);
        this.node.on("RUN_BIG_WILD_IDLE", this.runBigWildIdle, this);

        this.bigWildReels = [];
        this.usingObject = [];
    },
    playAnimNearWin() {
        if (this.bigWildReels && this.bigWildReels.length) {
            if (this.bigWildReels.includes(0)) this.bigWildTable.emit("PLAY_NEAR_WIN", 0);
            if (this.bigWildReels.includes(4)) this.bigWildTable.emit("PLAY_NEAR_WIN", 4);
        }
    },
    stopAnimNearWin() {
        this.bigWildTable.emit("STOP_NEAR_WIN");
    },
    setUpPaylineAtReel(col) {
        const { payLines, matrix } = this.node.gSlotDataStore.playSession;
        this.node.curentConfig = this.node.config.STATS[this.node.mode];
        this.paylineHolderNode.active = true;
        this.payLineNormals = payLines;
        this.scatterHolderNode = [];
        this.bonusHolderNode = [];
        this.wildHolderNode = [];
        this.jackpotHolderNode = [];
        this.paylinesMatrix[col] = [];
        for (let row = 0; row < this.node.reels[col].showSymbols.length; ++row) {
            const symbol = this.node.reels[col].showSymbols[row];
            const isBigWild = this.bigWildReels.includes(col);
            const paylineSymbol = this._createPayLineSymbol(symbol.symbol, col, row, isBigWild);
            const winLine = this._createWinLine(col, row, isBigWild);
            const payline = { symbol, paylineSymbol, winLine };
            this.paylinesMatrix[col][row] = payline;
        }
        if (col == 4 && matrix && this.payLineNormals) {
            const hasK3LastReel = matrix[4].includes("K3");
            if (hasK3LastReel) {
                this.payLineNormals.forEach(pl => {
                    if (pl.payLineWinNumbers == 5) pl.playK3WinLastReel = true;
                })
            }
        }
    },
    setupPaylines(data) {
        const { rate, isResume, payLines, matrix } = data;
        this.winRate = rate;
        if (!isResume) return;
        this.node.curentConfig = this.node.config.STATS[this.node.mode];
        this.paylineHolderNode.active = true;
        this.payLineNormals = payLines;
        this.paylinesMatrix = [];
        this.scatterHolderNode = [];
        this.bonusHolderNode = [];
        this.wildHolderNode = [];
        this.jackpotHolderNode = [];
        for (let col = 0; col < this.node.reels.length; ++col) {
            this.paylinesMatrix[col] = [];
            for (let row = 0; row < this.node.reels[col].showSymbols.length; ++row) {
                const symbol = this.node.reels[col].showSymbols[row];
                const isBigWild = this.bigWildReels.includes(col);
                const paylineSymbol = this._createPayLineSymbol(symbol.symbol, col, row, isBigWild);
                const winLine = this._createWinLine(col, row, isBigWild);
                const payline = { symbol, paylineSymbol, winLine };
                this.paylinesMatrix[col][row] = payline;
            }
        }

        if (matrix && this.payLineNormals) {
            const hasK3LastReel = matrix[4].includes("K3");
            if (hasK3LastReel) {
                this.payLineNormals.forEach(pl => {
                    if (pl.payLineWinNumbers == 5) pl.playK3WinLastReel = true;
                })
            }
        }
        
    },

    setupBigWild(data) {
        const { bigWild } = data;
        this.bigWildReels = bigWild ? [...bigWild] : [];
        this.bigWildTable.emit("SETUP_BIG_WILD", data);
        if (this.node.isFreeMode) this.bigWildReels.push(2);
    },
    showBigWild(col) {
        this.bigWildTable.emit("SHOW_BIG_WILD", col);
    },
    startSpinning() {
        this.bigWildTable.emit("RESET_BIG_WILD");
    },

    disableHighlightNormalPaylines(isWinFree = false) {
        if (this.drawingLineTable) this.drawingLineTable.emit("DISABLE_ALL_LINE");
        for (let col = 0; col < this.paylinesMatrix.length; ++col) {
            for (let row = 0; row < this.paylinesMatrix[col].length; ++row) {
                const { symbol, paylineSymbol, winLine } = this.paylinesMatrix[col][row];
                if (symbol.isHideForBigWild) {
                    symbol.active = false;
                } else {
                    symbol.active = true;
                    symbol.emit("DISABLE_HIGHLIGHT");
                }
                if (paylineSymbol) {
                    paylineSymbol.emit("DISABLE_HIGHLIGHT");
                    paylineSymbol.emit("STOP_ANIMATION");
                }
                if (winLine) winLine && winLine.emit("STOP_ANIMATION");
            }
            if (!isWinFree) this.bigWildTable.emit("DISABLE_HIGHLIGHT", col)
            if (!isWinFree) this.bigWildTable.emit("PLAY_ANIMATION_IDLE", col)
        }
    },
    blinkHighlightPaylines(callback) {
        if (this.backGroundReelLeft) this._tweenColor(this.backGroundReelLeft, this.dimColor, 0.3);
        if (this.backGroundReelRight) this._tweenColor(this.backGroundReelRight, this.dimColor, 0.3);
        const { BLINK_DURATION } = this.node.curentConfig;
        const { isAutoSpin } = this.node.gSlotDataStore;
        const { freeGame } = this.node.gSlotDataStore.playSession;
        const blinkTime = (isAutoSpin || freeGame) ? 2 : this._getBlinkTime(this.winRate);
        const delayTime = blinkTime * BLINK_DURATION;
        this._blinkingCallback = () => {
            callback && callback();
            this._blinkingCallback = null;
        };
        this.paylineHolderNode.opacity = 255;
        this.disableHighlightNormalPaylines();
        let maxPayLineWinNumbers = 0;
        if (this.payLineNormals && this.payLineNormals.length) {
            for (let i = 0; i < this.payLineNormals.length; ++i) {
                const { payLineWinNumbers } = this.payLineNormals[i];
                if (payLineWinNumbers && payLineWinNumbers > maxPayLineWinNumbers) maxPayLineWinNumbers = payLineWinNumbers;
                this.blinkNormalPaylinePerline(this.payLineNormals[i]);
            }
        }
        this.bigWildReels.forEach(col => {
            if (!this.node.isFreeMode && col < 2) {
                this.node.soundPlayer.playSfxByID("9881_sfx_wild_expand_reel1");
            }
            if (col != 2 && maxPayLineWinNumbers >= col) {
                this.bigWildTable.emit("ENABLE_HIGHLIGHT", col)
                this.bigWildTable.emit("PLAY_ANIMATION_WIN", col)
            }
        });
        if (this.stickyWild) this.stickyWild.emit("PLAY_ANIM_WIN");
        this.node.emit('BLINK_ALL_PAYLINE');
        this.scheduleOnce(this._blinkingCallback, delayTime);
    },
    blinkNormalPaylinePerline({ payLineID, payLineWinNumbers }) {
        const payLine = this.node.config.PAY_LINE_MATRIX[payLineID];
        if (this.drawingLineTable) this.drawPayLine(payLine);
        for (let payLinePos = 0; payLinePos < payLineWinNumbers; ++payLinePos) {
            const row = payLine[payLinePos];
            const col = payLinePos;
            const { paylineSymbol, winLine, symbol } = this.paylinesMatrix[col][row];
            if (symbol) symbol.active = false;
            paylineSymbol.emit("ENABLE_HIGHLIGHT");
            paylineSymbol.emit("PLAY_ANIMATION");
            winLine && winLine.emit('PLAY_ANIMATION');
        }
    },
    showNextPayline() {
        this.paylineHolderNode.opacity = 255;
        const { isAutoSpin } = this.node.gSlotDataStore;
        if (isAutoSpin) {
            const topLineTime = this._getLineTime(this.winRate);
            const canShowLine = !!(topLineTime.length && topLineTime[this.paylineIndex]);
            const hasPayLine = !!(this.payLineNormals && this.payLineNormals[this.paylineIndex]);
            if (!canShowLine || !hasPayLine) {
                this.showingPayline = false;
                this.paylineIndex = 0;
                this.callbackShowPayline && this.callbackShowPayline();
                this.callbackShowPayline = null;
            }
            if (canShowLine && hasPayLine && this.showingPayline) {
                const paylineTime = topLineTime[this.paylineIndex];
                const paylineInfo = this.payLineNormals[this.paylineIndex];
                this.showNormalPayline(paylineInfo);
                this.tablePaylineInfo.emit('SHOW_PAYLINE', { line: paylineInfo });
                this.extShowPayline();
                this.paylineIndex += 1;
                this.paylineTime = paylineTime;
            }
        } else {
            this._super();
        }
    },
    showNormalPaylinePerline({ payLineID, payLineWinNumbers, playK3WinLastReel }) {
        this.disableHighlightNormalPaylines();
        const payLine = this.node.config.PAY_LINE_MATRIX[payLineID];
        if (this.drawingLineTable) this.drawPayLine(payLine);
        if (payLine && payLine.length > 0 && this.paylinesMatrix && this.paylinesMatrix.length > 0) {
            if (playK3WinLastReel) {
                this.bigWildTable.emit("ENABLE_HIGHLIGHT", 4);
                this.bigWildTable.emit("PLAY_ANIMATION_WIN", 4);
            } else {
                this.bigWildTable.emit("DISABLE_HIGHLIGHT", 4);
                this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 4);
            }

            for (let payLinePos = 0; payLinePos < payLineWinNumbers; ++payLinePos) {
                const row = payLine[payLinePos];
                const col = payLinePos;
                const { symbol, paylineSymbol, winLine } = this.paylinesMatrix[col][row];
                if (symbol) symbol.active = false;
                if (paylineSymbol) {
                    paylineSymbol.emit("ENABLE_HIGHLIGHT");
                    paylineSymbol.emit("PLAY_ANIMATION");
                }
                if (winLine) winLine && winLine.emit('PLAY_ANIMATION');
                if (this.bigWildReels.includes(col)) {
                    if (col !== 4) {
                        this.bigWildTable.emit("ENABLE_HIGHLIGHT", col)
                        this.bigWildTable.emit("PLAY_ANIMATION_WIN", col)
                    }
                }
            }
        }
    },
    showScatterPayLine(callback) {
        this.disableHighlightNormalPaylines(true);
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_freegame_trigger');
        for (let row = 0; row < this.paylinesMatrix[2].length; ++row) {
            const { symbol, paylineSymbol } = this.paylinesMatrix[2][row];
            if (symbol.symbol == "K1") {
                paylineSymbol.emit("ENABLE_HIGHLIGHT");
                paylineSymbol.emit("PLAY_ANIMATION");
            }
        }
        this.bigWildTable.emit("SHOW_PAYLINE_FREE");
        this._callback = () => {
            this._callback = null;
            this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 0)
            this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 4)
            callback && callback();
        }
        this.scheduleOnce(this._callback, 1);
    },
    showJackpotPayLine(callback) {
        this.disableHighlightNormalPaylines();
        for (let i = 0; i < this.payLineNormals.length; ++i) {
            this.blinkNormalPaylinePerline(this.payLineNormals[i]);
        }
        this.bigWildTable.emit("SHOW_PAYLINE_JACKPOT")
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_prejackpot');
        this._callback = () => {
            this._callback = null;
            this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 0)
            this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 4)
            callback && callback();
        }
        this.scheduleOnce(this._callback, 1);
    },

    drawPayLine(payLine) {
        if (!payLine) return;
        for (let i = 0; i < payLine.length; i++) {
            let col = i;
            let row = payLine[i];
            this.drawingLineTable.emit("SHOW_DOT", col, row);

            if (col == 0) {
                // add first line 
                this.drawingLineTable.emit("DRAWING_FIRST_LINE", row);
            }
            if (i < payLine.length - 1) {
                let val = payLine[i] - payLine[i + 1];
                let type = 0;
                if (val == 0) type = 0; // di ngang
                else if (val == 1) type = 1; // cheo len 1 o
                else if (val == 2) type = 3; // cheo len 2 o
                else if (val == -1) type = 2; // cheo xuong 1 o
                else if (val == -2) type = 4; // cheo xuong 2 o
                this.drawingLineTable.emit("DRAWING_LINE_AT", type, col, row);
            }
            if (col == payLine.length - 1) {
                // add last line
                this.drawingLineTable.emit("DRAWING_LAST_LINE", row);
            }
        }
    },
    runBigWildIdle() {
        this.bigWildTable.emit("STOP_NEAR_WIN");
        this.bigWildTable.emit("ENABLE_HIGHLIGHT", 0)
        this.bigWildTable.emit("ENABLE_HIGHLIGHT", 4)
        this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 0)
        this.bigWildTable.emit("PLAY_ANIMATION_IDLE", 4)
    },
    clearPaylines() {
        this._clearPool();
        this._super();
        this.winLineHolderNode.removeAllChildren();
        this.winLineBigWild.removeAllChildren();
        this.drawingLineTable.emit("CLEAR_DRAWING_LINE");
        if (this.stickyWild) this.stickyWild.emit("PLAY_ANIM_IDLE");
        this.winRate = 0;
        this.bigWildReels = [];
        if (this._callback) {
            this.unschedule(this._callback);
            this._callback = null;
        }
        if (this.backGroundReelLeft) this._tweenColor(this.backGroundReelLeft, cc.Color.WHITE, 0.3);
        if (this.backGroundReelRight) this._tweenColor(this.backGroundReelRight, cc.Color.WHITE, 0.3);
    },
    resetSymbolPaylines() {
        if (this.paylinesMatrix && this.paylinesMatrix.length) {
            for (let col = 0; col < this.paylinesMatrix.length; ++col) {
                if (this.paylinesMatrix[col] && this.paylinesMatrix[col].length) {
                    for (let row = 0; row < this.paylinesMatrix[col].length; ++row) {
                        const { symbol, paylineSymbol, winLine } = this.paylinesMatrix[col][row];
                        if (symbol.isHideForBigWild) {
                            symbol.active = false;
                        } else {
                            symbol.active = true;
                        }
                        symbol.reset();
                        paylineSymbol.emit("RESET");
                        winLine && winLine.emit("RESET");
                    }
                }
            }
        }
        this.bigWildTable.emit("ENABLE_HIGHLIGHT", 0)
        this.bigWildTable.emit("ENABLE_HIGHLIGHT", 4)
    },

    _createPayLineSymbol(symbol, col, row, isBigWild = false) {
        const payLineSymbol = this.poolFactory.getObject("SymbolPayline9881");
        payLineSymbol.parent = this.paylineHolderNode;
        payLineSymbol.active = true;
        payLineSymbol.position = this._getPosByColRow(col, row);
        payLineSymbol.changeToSymbol(symbol);
        payLineSymbol.isHideForBigWild = isBigWild;
        this.usingObject.push(payLineSymbol);
        return payLineSymbol;
    },
    _createWinLine(col, row, isBigWild = false) {
        return null;
        const winLine = this.poolFactory.getObject("WinLine9881");
        winLine.parent = isBigWild ? this.winLineBigWild : this.winLineHolderNode;
        winLine.active = true;
        winLine.position = this._getPosByColRow(col, row);
        winLine && winLine.emit("RESET");
        this.usingObject.push(winLine);
        return winLine;
    },
    _clearPool() {
        this.usingObject.forEach(obj => {
            obj.emit("RESET");
            this.poolFactory.removeObject(obj);
        })
        this.usingObject = [];
    },
    _getPosByColRow(col, row) {
        const { TABLE_FORMAT, SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const startX = -(TABLE_FORMAT.length / 2 - 0.5) * SYMBOL_WIDTH;
        const startY = TABLE_FORMAT[row] / 2 - row - 0.5;
        const X = (startX + SYMBOL_WIDTH * col);
        const Y = startY * SYMBOL_HEIGHT;
        return cc.v2(X, Y);
    },
    _tweenColor(node, finalColor, time = 0.2) {
        node.tweenColor && node.tweenColor.stop();
        node.tweenColor = null;
        node.tweenColor = cc.tween(node)
            .to(time, { color: finalColor })
            .call(() => {
                node.tweenColor = null;
            })
            .start();
    },
    _getLineTime(rate) {
        let topLineTime = [];
        if (rate >= 3 && rate < 10) topLineTime = [2];
        if (rate >= 10 && rate < 20) topLineTime = [2, 2];
        if (rate >= 20) topLineTime = [2, 2];
        return topLineTime;
    },
    _getBlinkTime(rate) {
        let blinkTime = 1;
        if (rate > 0 && rate < 1) blinkTime = 1;
        if (rate >= 1 && rate < 3) blinkTime = 1;
        if (rate >= 3 && rate < 10) blinkTime = 2;
        if (rate >= 10 && rate < 20) blinkTime = 2;
        if (rate >= 20) blinkTime = 0.5;
        return blinkTime;
    },
});