import { ItemLobby } from "./ItemLobby";
const {setDeviceOrientation} = require('utils');

const { ccclass, property } = cc._decorator;

@ccclass
export class LobbyScene extends cc.Component {

    @property({ type: cc.Node })
    iconHolder = null;

    @property({ type: cc.Prefab })
    iconPrefab = null;


    @property({ type: cc.Node })
    gameSelection = null;

    @property({ type: cc.Toggle })
    toggleDebug = null;

    StorageKeyBGM = "enableBackgroundMusic";
    iconPool = null;
    private projectConfig: cc.JsonAsset = null;
    private iconList: cc.SpriteAtlas = null;
    multiSlotConfig = {};

    @property({ type: cc.Button })
    buttonEvent: cc.Button = null;

    private _loadBundleDone: boolean;
    private _mapIconByGameId = {};

    onLoad() {
        this.node.on('RQ_PLAY_GAME', this.requestPlayGame.bind(this));
        this.iconPool = new cc.NodePool();

        let loadCount = 0;
        let _completeLoad = () => {
            loadCount++;
            if (loadCount >= 2) {
                this.loadProjects();
            }
            this.buttonEvent.interactable = true;
        };
        this.buttonEvent.interactable = false;
        cc.loader.loadRes("jsons/LobbyConfig", cc.JsonAsset, (err, json) => {
            this.projectConfig = json;
            _completeLoad();
        });

        cc.loader.loadRes("sprites/iconLobby", cc.SpriteAtlas, (err, atlas) => {
            this.iconList = atlas;
            _completeLoad();
        });
    }

    getIcon() {
        let icon = this.iconPool.get();
        if (!icon) {
            icon = cc.instantiate(this.iconPrefab);
        }
        return icon;
    }

    getIconSpriteFrame(id: string): cc.SpriteFrame {
        if (this.iconList) {
            return this.iconList.getSpriteFrame(id);
        }
        return null;
    }

    removeAllIcons() {
        this._mapIconByGameId = {};
        if (this.iconHolder.children) {
            while (this.iconHolder.children.length > 0) {
                this.iconPool.put(this.iconHolder.children[0]);
            }
        }
    }

    loadProjects() {
        this.removeAllIcons();
        for (let id in this.projectConfig.json.games) {
            let config = this.projectConfig.json.games[id];
            if (config.isActive) {
                let item = this.getIcon();
                item.setParent(this.iconHolder);
                let icon = this.iconList.getSpriteFrame(config.icon)
                let itemScript: ItemLobby = item.getComponent(ItemLobby);
                icon && itemScript.setSprite(icon);
                let lbl = item.getChildByName("label").getComponent(cc.Label);
                lbl.string = icon ? '' : id; 
                itemScript.setGameConfig(id, config);
                this._mapIconByGameId[id] = item;
            }
        }
    }

    requestPlayGame(ev) {
        let config = ev.config;
        if (ev.config.isMultiSlot) {
            cc.director.loadScene("MultiSlot", () => {
                const multiSlotComp = cc.director.getScene().getChildByName('Canvas').getComponentInChildren('MultiSlot');
                multiSlotComp.requestPlayFromLobby(config);
            });
        }
        else if (ev.config.bundleName) {
            let icon = this._mapIconByGameId[ev.gameId];
            this.loadBundle(ev.config.bundleName, ev.config.prefabPath, (percent) => {
                icon.getChildByName("label").getComponent(cc.Label).string = `Load ${Math.floor(percent)}%`;
            }, (err, assets, status) => {
                assets.forEach(asset => {
                    let prefab = asset as unknown as cc.Prefab;
                    if (prefab.data.name == ev.config.startScene) {
                        icon.getChildByName("label").getComponent(cc.Label).string = ev.config.startScene;
                        let node = cc.instantiate(prefab) as cc.Node;
                        cc.game.addPersistRootNode(node);
                    }
                });
            })
        }
        else {
            cc.director.preloadScene(ev.config.startScene, () => {
                if (cc.sys.isNative) {
                    setDeviceOrientation(ev.config.isPortrait);
                }
                cc.director.loadScene(ev.config.startScene);
            })
        }
    }

    private loadBundle(bundleName: string, prefabPath: string, onProgress: Function, onComplete: Function) {
        let bundle = cc.loader.downloader._subpackages[bundleName];
        if (!bundle) {
            this._loadBundleDone = false;
            cc.loader.downloader.loadSubpackage(bundleName, (err: string, bundle: any) => {
                if (err) {
                    this._loadBundleDone = false;
                    onComplete && onComplete(err, [], "load bundle error");
                } else {
                    bundle.loadDir(prefabPath, cc.Prefab, (finish: number, total: number, item: any) => {
                        let percent = 100 * finish / total;
                        onProgress && onProgress(percent);
                    }, (error: string, assets: any) => {
                        if (error) {
                            this._loadBundleDone = false;
                            onComplete && onComplete(err, [], "load prefab error");
                            return;
                        }

                        onComplete(error, assets, "done");
                        this._loadBundleDone = true;
                    })
                }
            });
        }
    }

    onEnable() {
        if (cc.sys.isNative) {
            cc.sys.garbageCollect();
        }
    }

    showDebug() {
        cc.debug.setDisplayStats(this.toggleDebug.isChecked);
    }

    onDestroy() {
        if (this.projectConfig) {
            this.projectConfig = null;
        }
        if (this.iconList) {
            this.iconList = null;
        }
        this.removeAllIcons();
        this.iconPool.clear();
    }
}
