cc.Class({
    extends: cc.Component,

    properties: {
        nearWinEffect: cc.Node,
    },

    onLoad() {
        this.node.on("REEL_ABOUT_TO_STOP_NEARWIN", this.setupNearWin, this);
        this.node.on("REEL_STOP_NEARWIN", this.reelStopNearWin, this);
        this.node.on("TABLE_START_NEARWIN", this.resetNearWin, this);
        this.nearWinEffect.active = false;
    },

    setupNearWin({ reels, data }) {
        const matrix = this._getMatrix(data);
        this._isSkipEffect = reels[2].curentConfig.MODE === "TURBO";
        this.isNearWinFreeGame = false;
        this.isNearWinJackpot = false;
        if (this._isSkipEffect) return;
        this.isNearWinFreeGame = this._isNearWinFreeGame(matrix);
        this.isNearWinJackpot = this._isNearWinJp(matrix);
        const isNearWin = this.isNearWinFreeGame || this.isNearWinJackpot;
        if (isNearWin) {
            reels[2].extendTimeToStop(isNearWin);
            this.reel = reels[2];
        }
    },

    reelStopNearWin({ col, context }) {
        if (col == 2) return this.resetNearWin();
        if (context.isFastToResult) return;
        this._playNearWinEffect(col);
    },

    resetNearWin() {
        this._stopNearWinEffect();
        this.node.soundPlayer && this.node.soundPlayer.stopSoundByID("9881_sfx_nearwin");
    },

    /** @private */
    _isNearWinFreeGame(matrix) {
        const countK2 = this._countSymbol(matrix[0], "K2");
        const countK3 = this._countSymbol(matrix[4], "K3");
        return countK2 >= 3 && countK3 >= 3;
    },
    _isNearWinJp(matrix) {
        const countK2 = this._countSymbol(matrix[0], "K2");
        const countK3 = this._countSymbol(matrix[4], "K3");

        const symbolNearWin = matrix[1][0];
        const countCol0 = countK2 >= 3 ? countK2 : this._countSymbol(matrix[0], symbolNearWin);
        const countCol1 = this._countSymbol(matrix[1], symbolNearWin);
        const countCol3 = this._countSymbol(matrix[3], symbolNearWin);
        const countCol4 = countK3 >= 3 ? countK3 : this._countSymbol(matrix[4], symbolNearWin);
        return countCol0 >= 3 && countCol1 >= 3 && countCol3 >= 3 && countCol4 >= 3;
    },
    _countSymbol(row, symbol) {
        return row.reduce((count, value) => count + (value === symbol ? 1 : 0), 0);
    },

    _playNearWinEffect(col) {
        if ((col == 1 || col == 3) && (this.isNearWinFreeGame || this.isNearWinJackpot)) {
            if (this.reel) this.reel.playNearWin();
            this.nearWinEffect.stopAllActions();
            this.nearWinEffect.opacity = 0;
            this.nearWinEffect.active = true;
            this.nearWinEffect.runAction(cc.fadeIn(0.3));
            this.node.soundPlayer && this.node.soundPlayer.playSoundByID("9881_sfx_nearwin", false);
            for (let col = 0; col < this.node.reels.length; ++col) {
                if (col != 2) {
                    const reel = this.node.reels[col];
                    reel.reel.children.forEach(symbol => {
                        symbol.emit("DISABLE_HIGHLIGHT")
                    });
                }
            }
            this.node.emit("RUN_ANIM_NEAR_WIN");
        }
        if (col == 2) {
            return this._stopNearWinEffect();
        }
    },
    _stopNearWinEffect() {
        this.nearWinEffect.stopAllActions();
        this.nearWinEffect.runAction(cc.sequence(
            cc.fadeOut(0.3),
            cc.callFunc(() => {
                this.nearWinEffect.active = false;
                this.node.soundPlayer && this.node.soundPlayer.stopSoundByID("9881_sfx_nearwin");
            }),
        ));

        for (let col = 0; col < this.node.reels.length; ++col) {
            const reel = this.node.reels[col];
            reel.reel.children.forEach(symbol => {
                symbol.emit("ENABLE_HIGHLIGHT")
            });
        }
        this.node.emit("STOP_ANIM_NEAR_WIN");
    },
    _getMatrix(data) {
        return data.slice();
    },
});
