const {formatMoney} = require('utils');

cc.Class({
    extends: require('SlotTablePaylineInfo'),

    showPaylineInfoAllways({line}){
        const {symbolId, totalWinAmount, symbolCount, combination, payableSymbol, multiplier} = line;
        const betDenom = this.calculateBetDenom();

        this.node.active = true;
        this.lbLeft.string = 'Trng ' + Number(symbolCount);
        this.imgSymbol.getComponent(cc.Sprite).spriteFrame = this.assets[this.smallSymbolPrefix + symbolId];

        const multiply = parseInt(multiplier) > 1 ? ` x ${parseInt(multiplier)}` : '';
        const combinationString = combination > 1 ? ` x ${combination}` : '';
        const calculateDenom = ' x ' + formatMoney(betDenom) + multiply + ' = ';
        const winAmount = formatMoney(totalWinAmount);
        this.lbRight.string = ': ' + payableSymbol + combinationString + calculateDenom + winAmount;
    },
});
