cc.Class({
    extends: cc.Component,

    properties: {
        nearWinEffect: cc.Node,
    },

    onLoad() {
        this.node.on("REEL_ABOUT_TO_STOP_NEARWIN", this.setupNearWin, this);
        this.node.on("REEL_STOP_NEARWIN", this.reelStopNearWin, this);
        this.node.on("TABLE_START_NEARWIN", this.resetNearWin, this);
        this.nearWinEffect.active = false;
        this._nearWinData = [];
        this.listWildCanWin = null;
    },

    setupNearWin({ reels, data }) {
        this._nearWinData = [];
        const matrix = this._getMatrix(data);
        this._isSkipEffect = reels[2].curentConfig.MODE === "TURBO";
        this.listWildCanWin = this._getWildCanWin();
        if (this._isSkipEffect || !this.listWildCanWin) return;
        this.hasWinFree = false;

        let isNearWin = false;
        let canCheckNearWin = true;
        let count = 0;
        for (let col = 0; col < matrix.length; col++) {
            const payLineNearWin = this._getPayLineNearWin(matrix, col);
            this._nearWinData[col] = payLineNearWin;
            if (col > 0) {
                isNearWin = true;
                if (!canCheckNearWin) isNearWin = false;
                if (!payLineNearWin || !payLineNearWin.length) {
                    canCheckNearWin = false;
                } else {
                    payLineNearWin.forEach(info => { if (info.isWinning) canCheckNearWin = false; });
                }
            }
            if (isNearWin) count++;
            reels[col].extendTimeToStop(isNearWin, count);
        }
    },

    reelStopNearWin({ col, context }) {
        if (!this._nearWinData.length) return;
        const nexCol = col + 1;
        const nearWinData = this._nearWinData[col];
        const nextWinData = this._nearWinData[nexCol];
        if (nextWinData) nextWinData.forEach(info => { if (info.isWinning) this.hasWinFree = true; });
        if (!nearWinData.length || context.isFastToResult || this.hasWinFree) return this._stopNearWinEffect();
        this._playNearWinEffect(nexCol, nearWinData);
    },

    /** @private */
    _getPayLineNearWin(matrix, col) {
        let payLineCanNearWin = [];
        const listPayLine = this._getPayLineCanWin(matrix, col);
        let isWinning = false;
        listPayLine.forEach((item) => {
            const { payLine, hasWild } = item;
            let canWin = false;
            this.listWildCanWin.forEach((wildData) => {
                if (payLine[wildData.col] == 0) canWin = true;
            })
            if (col >= 2 && hasWild) {
                isWinning = true;
            }
            if (canWin) payLineCanNearWin.push({ payLine, isWinning });
        });
        return payLineCanNearWin;
    },
    _getWildCanWin() {
        const { fishInfo } = this.node.gSlotDataStore.playSession;
        if (!fishInfo || !fishInfo.length) return null;
        let listWild = [];
        for (let i = 0; i < fishInfo.length; i++) {
            if (fishInfo[i].row === 0) listWild.push(fishInfo[i]);
        }
        return listWild.length ? listWild : null;;
    },
    _getPayLineCanWin(matrix, col) {
        let listPayLine = [];
        const { PAY_LINE_MATRIX } = this.node.config;
        Object.keys(PAY_LINE_MATRIX).forEach((key) => {
            const payLine = PAY_LINE_MATRIX[key];
            let canMakePayLine = true;
            let symbolCanMake = matrix[0][payLine[0]];
            let hasWild = false;
            for (let i = 0; i <= col; ++i) {
                const row = payLine[i];
                const symbol = matrix[i][row];
                if (symbol == symbolCanMake || symbol == 'K1' || symbol == 'K2' || symbolCanMake == 'K1' || symbolCanMake == 'K2') {
                    if (symbol == 'K1' || symbol == 'K2') {
                        hasWild = true;
                    }
                    symbolCanMake = symbol;
                } else {
                    canMakePayLine = false;
                    break;
                }
            }
            if (canMakePayLine) {
                listPayLine[key] = { payLine, hasWild };
            }
        })
        return listPayLine;
    },
    _playNearWinEffect(col, nearWinData) {
        if (col == 4) {
            return this._stopNearWinEffect();
        }
        //if (this.reel) this.reel.playNearWin();
        this.nearWinEffect.stopAllActions();
        this.nearWinEffect.opacity = 0;
        this.nearWinEffect.active = true;
        this.nearWinEffect.x = this._getXPosition(col);
        this.nearWinEffect.runAction(cc.fadeIn(0.3));
        this.node.soundPlayer && this.node.soundPlayer.playSoundByID("9877_sfx_nearwin", false);

        for (let col = 0; col < this.node.reels.length; ++col) {
            const reel = this.node.reels[col];
            reel.reel.children.forEach(symbol => {
                symbol.emit("DISABLE_HIGHLIGHT")
            });
        }
        this.node.emit("RUN_ANIM_NEAR_WIN", col, nearWinData);
    },

    _stopNearWinEffect() {
        this.nearWinEffect.stopAllActions();
        this.nearWinEffect.runAction(cc.sequence(
            cc.fadeOut(0.3),
            cc.callFunc(() => {
                this.nearWinEffect.active = false;
                this.node.soundPlayer && this.node.soundPlayer.stopSoundByID("9881_sfx_nearwin");
            }),
        ));
        for (let col = 0; col < this.node.reels.length; ++col) {
            const reel = this.node.reels[col];
            reel.reel.children.forEach(symbol => {
                symbol.emit("ENABLE_HIGHLIGHT")
            });
        }
        this.node.emit("STOP_ANIM_NEAR_WIN");
    },
    _getMatrix(data) {
        return data.slice();
    },
    _getXPosition(col) {
        const _startX = -(this.node.config.TABLE_FORMAT.length / 2 - 0.5) * this.node.config.SYMBOL_WIDTH;
        return _startX + this.node.config.SYMBOL_WIDTH * col;
    },
});
