const { convertAssetArrayToObject } = require('slotUtils');

cc.Class({
    extends: require('SlotSymbolPaylinev2'),

    properties: {
        labelWinFree: cc.Label,
        dimColor: cc.Color
    },

    onLoad() {
        this._super();
        this.assets = convertAssetArrayToObject(this.symbols);
        this.node.on("PLAY_ANIM_IDLE", this.playAnimationIdle, this);
        this.node.on("PLAY_ANIM_NEAR_WIN", this.playAnimationNearWin, this);

        this.node.on("PLAY_ANIM_RANDOM_FREE", this.playAnimRandomFree, this);
        this.reset();
    },
    playAnimRandomFree(wildInfo) {
        const { wildType, wildValue } = wildInfo;
        this.staticSymbol.active = true;
        this.staticSymbol.opacity = 255;
        this.staticSymbol.scale = 1;
        if (wildType == 1) {
            this.labelWinFree.string = 'x' + wildValue;
        }
        if (wildType == 2) {
            this.labelWinFree.string = '+' + wildValue;
        }
        if (wildType == 3) {
            //todo show bigWild
        }
    },

    changeToSymbol(symbolName) {
        this.staticSymbol.getComponent(cc.Sprite).spriteFrame = this.assets[symbolName];
        this._super(symbolName);
    },
    enableHighlight() {
        if (this.node.isHideForBigWild || this.isEnableHighlight) return;
        this.tweenColor(this.staticSymbol, cc.Color.WHITE)
        this.tweenColor(this.spineNode, cc.Color.WHITE)
        this.isEnableHighlight = true;
    },
    disableHighlight() {
        if (!this.isEnableHighlight) return;
        this.isEnableHighlight = false;
        this.tweenColor(this.staticSymbol, this.dimColor)
        this.tweenColor(this.spineNode, this.dimColor)
        this.spineNode.active = this.isIdle;
        this.node.isPlaying = false;
        this.staticSymbol.active = false;
        this.tweenAnimation && this.tweenAnimation.stop();
        this.tweenAnimation = null;
        if (this.node.isHideForBigWild) this.spineNode.active = false;
    },
    playAnimationIdle() {
        this.spineNode.active = true;
        this.spineNode.opacity = 255;
        this.staticSymbol.opacity = 0;
        this.animation.setAnimation(0, "Appear", false);
        this.animation.addAnimation(0, "Idle", true);
        this.isIdle = true;
    },
    playAnimationNearWin() {
        this.spineNode.active = true;
        this.spineNode.opacity = 255;
        this.staticSymbol.opacity = 0;
        this.animation.setAnimation(0, "NearWin", true);
    },
    playAnimation() {
        if (this.node.isHideForBigWild) return;
        if (this.havingAnim) {
            this.spineNode.opacity = 255;
            this.spineNode.active = true;
            this.staticSymbol.opacity = 0;
            let animName = "animation";
            if (this.node.symbol == 'K' || this.node.symbol == 'K2' || this.node.symbol == 'K3') {
                animName = "Win";
                this.animation.setAnimation(0, animName, true);
                this.node.isPlaying = true;
            } else if (this.animation.findAnimation(animName)) {
                if (!this.node.isPlaying) {
                    this.animation.setAnimation(0, animName, true);
                    this.node.isPlaying = true;
                }
            }
        } else {
            this.staticSymbol.opacity = 255;
            this.staticSymbol.active = true;
            this.spineNode.opacity = 0;
            this.staticSymbol.scale = 1;
            this.tweenAnimation && this.tweenAnimation.stop();
            this.tweenAnimation = cc.tween(this.staticSymbol)
                .to(0.2, { scale: 1.05 })
                .to(0.2, { scale: 0.95 })
                .to(0.2, { scale: 1 })
                .delay(0.4)
                .union()
                .repeatForever()
                .start()
        }
    },
    reset() {
        this.isEnableHighlight = true;
        this.isIdle = false;
        this.isPlaying = false;
        this.staticSymbol.color = cc.Color.WHITE;
        this.spineNode.color = cc.Color.WHITE;
        this.staticSymbol.tweenColor && this.staticSymbol.tweenColor.stop();
        this.staticSymbol.tweenColor = null;
        this.spineNode.tweenColor && this.spineNode.tweenColor.stop();
        this.spineNode.tweenColor = null;

        this.spineNode.opacity = 0;
        this.spineNode.active = false;
        this.tweenAnimation && this.tweenAnimation.stop();
        this.tweenAnimation = null;
        this.node.isHideForBigWild = false
        this.labelWinFree.string = "";
        this._super();
    },
    tweenColor(node, finalColor, time = 0.2) {
        node.tweenColor && node.tweenColor.stop();
        node.tweenColor = null;
        node.tweenColor = cc.tween(node)
            .to(time, { color: finalColor })
            .call(() => {
                node.tweenColor = null;
            })
            .start();
    },
    stopAnimation() { },
});
