
cc.Class({
    extends: cc.Component,

    properties: {
        bigWilds: [cc.Node],
    },
    onLoad() {
        this.node.on("SETUP_BIG_WILD", this.setupBigWild, this);
        this.node.on("SHOW_BIG_WILD", this.showBigWild, this);
        this.node.on("RESET_BIG_WILD", this.reset, this);
        this.node.on("SHOW_PAYLINE_JACKPOT", this.showPayLineJackpot, this);

        this.node.on("DISABLE_HIGHLIGHT", this.disableHightLight, this);
        this.node.on("ENABLE_HIGHLIGHT", this.enableHightLight, this);
        this.node.on("PLAY_ANIMATION_WIN", this.playAnimationWin, this);

        this.node.on("PLAY_ANIMATION_IDLE", this.playAnimationIdle, this);
        this.node.on("PLAY_NEAR_WIN", this.playAnimNearWin, this);
        this.node.on("STOP_NEAR_WIN", this.stopAnimNearWin, this);

        this.reset();
        this.bigWild = [];
    },

    setupBigWild(data) {
        const { isResume, matrix0, bigWild } = data;
        this.bigWild = bigWild ? bigWild : [];
        this.matrixNormal = matrix0;
        if (isResume) {
            this.bigWild.forEach((col)=>{
                this.showBigWild(col, isResume);
            })
        }
    },

    disableHightLight(col) {
        const bigWild = this.bigWilds[col];
        if (bigWild && bigWild.active) bigWild.emit("DISABLE_HIGHLIGHT");
    },
    enableHightLight(col) {
        const bigWild = this.bigWilds[col];
        if (bigWild && bigWild.active) bigWild.emit("ENABLE_HIGHLIGHT");
    },
    playAnimationWin(col) {
        const bigWild = this.bigWilds[col];
        if (bigWild && bigWild.active) bigWild.emit("PLAY_ANIMATION_WIN");
    },
    playAnimNearWin(col) {
        const bigWild = this.bigWilds[col];
        if (bigWild && bigWild.active) bigWild.emit("PLAY_ANIM_NEAR_WIN");
    },
    stopAnimNearWin() {

    },
    playAnimationIdle(col) {
        const bigWild = this.bigWilds[col];
        if (bigWild && bigWild.active) bigWild.emit("PLAY_ANIMATION_IDLE");
    },
    showPayLineJackpot() {

    },

    showBigWild(col, isResume) {
        if (!this.bigWild.includes(col) || !this.matrixNormal) return;
        const showBigWildEvent = new cc.Event.EventCustom('ON_BIG_WILD_SHOW', true);
        showBigWildEvent.setUserData({ col });
        this.node.dispatchEvent(showBigWildEvent);
        this.playAnimExpand(col, isResume);
    },

    playAnimExpand(col, isFast) {
        // const { payLines } = this.node.gSlotDataStore.playSession;
        const bigWild = this.bigWilds[col];
        const row = this.getBigWildRow(col);
        this.scheduleOnce(() => {
            // if (payLines) {
            //     this.node.soundPlayer.playSfxByID("9881_sfx_wild_expand");
            // }
        }, 0.2);
        bigWild.active = true;
        // bigWild.position = this._getPosByColRow(col, isFast ? 1 : row);
        bigWild.emit("PLAY_ANIMATION", row, isFast);
    },
    reset() {
        this.bigWilds.forEach(bigWild => {
            bigWild.emit("RESET");
            bigWild.active = false;
        });
        this.matrixNormal = null;
        this.bigWild = [];
    },
    getBigWildRow(reel) {
        const reelSymbols = this.matrixNormal[reel];
        let bigWild = reel < 2 ? 'K2' : 'K3';
        let row = -1;
        for (let i = 0; i < reelSymbols.length; i++) {
            if (reelSymbols[i] == bigWild) {
                row = i;
                break;
            }
        }
        return row;
    },
    _getPosByColRow(col, row) {
        const { TABLE_FORMAT, SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const startX = -(TABLE_FORMAT.length / 2 - 0.5) * SYMBOL_WIDTH;
        const startY = TABLE_FORMAT[row] / 2 - row - 0.5;
        const X = (startX + SYMBOL_WIDTH * col);
        const Y = startY * SYMBOL_HEIGHT;
        return cc.v2(X, Y);
    }
});
