

cc.Class({
    extends: require('CutsceneMode'),
    properties: {
        animationNode: cc.Node,
        animHolder: cc.Node,
    },
    onLoad() {
        this._super();
        this.animation = this.animationNode.getComponent(sp.Skeleton)
    },
    enter() {
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_transition');
        this.node.soundPlayer && this.node.soundPlayer.playMainBGM('freeGame');
        this.animationNode.parent = this.node;

        this.node.opacity = 0
        this.animation.setAnimation(0, "Transition_Appear", false) //2s
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = cc.tween(this.node)
            .to(0.1, { opacity: 255 })
            .delay(1.9)
            .call(() => {
                this.animation.setAnimation(0, "Transition_Disappear", false) //2s
            })
            .delay(1)
            .call(() => {
                this.animationNode.parent = this.animHolder;
                this.callback && this.callback();
                this.callback = null;
            })
            .delay(2)
            .call(() => {
                this.tweenShow = null;
                this.exit();
            })
            .start();
    },
    exit() {
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = null;
        this.animationNode.parent = this.node;
        this._super();
    },
    skip() {
        if (!this.node.active) return;
        this.callback = null;
        this.exit();
    }
});
