/* global cc */

// const PROJECT_LIST = ['Config9000', 'Config9987', 'Config9977'];

cc.Class({
    extends: cc.Component,

    properties: {
        background: cc.Sprite,
        mask: cc.Node,
        paylineMask: cc.Node,
        table: cc.Node,
        payline: cc.Node,
        symbolPayline: cc.Node
    },

    onLoad() {
        this.node.symbolPayline = this.symbolPayline;
    },

    start() {
        this.node.emit('INIT');
        this.loadBackground();
        this.loadConfig();
    },

    loadBackground() {
        cc.loader.load("https://static.ktek.io/animTool/background.jpg", (errors, background) => {
            this.background.spriteFrame = new cc.SpriteFrame(background);
        });
    },

    loadConfig() {
        cc.loader.load("https://static.ktek.io/animTool/symbol.json", (errors, config) => {
            this.projectConfig = config;
            this.generateDebug();
        });
    },

    generateDebug() {
        let thiz = this;
        const DEBUG = function () {
            this.symbolWidth = 200;
            this.symbolHeight = 200;
            this.project = 'g9000';
            this.spin = ()=> {
                thiz.spinClick();
            },
            this.stop = ()=>{
                thiz.stopClick();
            };
        };
        const dat = require('dat.gui');
        this.gui = new dat.GUI({
            autoPlace: true,
            load: JSON,
            preset: 'lasted'
        });

        this.debug = new DEBUG();
        let project = this.gui.add(this.debug, 'project', Object.keys(this.projectConfig));
        project.onChange((value)=>{
            this.mask.width = this.projectConfig[value].WIDTH * 5;
            this.mask.height = this.projectConfig[value].HEIGHT * 3;
            this.paylineMask.width = this.projectConfig[value].WIDTH * 5;
            this.paylineMask.height = this.projectConfig[value].HEIGHT * 3;
            this.node.config.SYMBOL_WIDTH = this.projectConfig[value].WIDTH;
            this.node.config.SYMBOL_HEIGHT = this.projectConfig[value].HEIGHT;
            this.node.config.SYMBOL_NAME_LIST = this.projectConfig[value].LIST;
            this.table.removeAllChildren();
            this.table.x = this.projectConfig[value].TABLE_X;
            this.table.y = this.projectConfig[value].TABLE_Y;
            this.payline.x = this.projectConfig[value].TABLE_X;
            this.payline.y = this.projectConfig[value].TABLE_Y;
            this.node.emit("INIT");
            this.node.emit("UPDATE_SYMBOL_LIST", this.projectConfig[value].LIST);
            this.replaceSymbols(this.node.assets);
            this.replaceBlurSymbols(this.node.blurAssets);
        });
        this.gui.add(this.debug, 'spin');
        this.gui.add(this.debug, 'stop');
        document.body.childNodes[document.body.childNodes.length - 1].style.zIndex = 999;
    },

    spinClick() {
        console.log('spinClick');
        this.node.emit('START_SPINNING');
        this.table.active = true;
        this.payline.active = false;
    },

    stopClick() {
        console.log('stopClick');
        this.node.emit('STOP_REEL_WITH_RANDOM_MATRIX', ()=>{
            this.playAllAnimation();
        });
    },
    replaceSymbols(assets) {
        let symbols = this.node.getComponentsInChildren('SlotSymbol');
        symbols.forEach((it)=>{
            it.assets = assets;
            it.changeToSymbol('2');
        });
    },
    replaceBlurSymbols(assets){
        let symbols = this.node.getComponentsInChildren('SlotSymbol');
        symbols.forEach((it)=>{
            it.blurAssets = assets;
        });
    },
    playAllAnimation() {
        this.table.active = false;
        this.payline.active = true;
        this.payline.removeAllChildren();
        let symbols = this.node.getComponentsInChildren('SlotSymbol');
        symbols.forEach((it)=>{
            let symbol = cc.instantiate(this.symbolPayline);
            symbol.setParent(this.payline);
            symbol.getComponent("SlotSymbolPayline").assets = this.node.assets;
            symbol.getComponent("SlotSymbolPayline").changeToSymbol(it.getSymbolName());
            symbol.getComponent("SlotSymbolPayline").playAnimation(2, true);
            let position = this.payline.convertToNodeSpaceAR(it.node.parent.convertToWorldSpaceAR(it.node.position));
            symbol.position = position;
        });
    }
});
