cc.Class({
    extends: cc.Component,

    onLoad() {
        this.node.on('TABLE_START_SOUND', this.reelStartSound, this);
        this.node.on('REEL_STOP_SOUND', this.reelStopSound, this);
        this.node.on('REEL_ABOUT_TO_STOP_SOUND', this.setUpSound, this);


        const payLineMatrix = this.node.config.PAY_LINE_MATRIX;
        this.payLineMatrixForCompare = [];
        if (payLineMatrix) {
            Object.keys(payLineMatrix).forEach(key => {
                this.payLineMatrixForCompare.push(payLineMatrix[key].join());
            });
        }
        this.canPlaySoundWild = false;
    },
    setUpSound({ reels, data }) {
        this.canPlaySoundWild = false;
        const matrix = this._getMatrix(data);
        let totalExpandWild = 0;
        matrix.forEach((rowMatrix, col) => {
            let count = 0;
            rowMatrix.forEach((symbol) => {
                if (symbol.includes('K')) {
                    count++;
                }
            })
            if (count == 3 && col != 2) {
                totalExpandWild++;
            }
        });
        if (totalExpandWild >= 2) {
            this.canPlaySoundWild = true;
        }
    },
    reelStartSound() {
        this.countScatter = 0;
        const { isAutoSpin } = this.node.gSlotDataStore;
        if (!this.node.isFreeGame && !isAutoSpin && this.node.soundPlayer) {
            this.node.soundPlayer.playSfxByID('9881_btn_spin');
        }
    },
    reelStopSound({ matrix, count }) {
        const { modeTurbo } = this.node.gSlotDataStore;
        const gameMode = this.node.gSlotDataStore.currentGameMode;
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        if (modeTurbo || isFTR) {
            if (count == 5) this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_reelstop');
        } else {
            this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_reelstop');
            if ((count == 1 || count == 3 || count == 5) && this.canPlaySoundWild) {
                if (matrix.includes("K2") || matrix.includes("K3")) {
                    this.node.soundPlayer.playSfxByID('9881_sfx_wild_1');
                }
                if (matrix.includes("K1")) {
                    this.node.soundPlayer.playSfxByID('9881_sfx_wild_2');
                }
            }
            if ((count == 2 || count == 4) && this.canPlaySoundWild) {
                if (matrix.includes("K2") || matrix.includes("K3")) {
                    this.node.soundPlayer.playSfxByID('9881_sfx_wild_1');
                }
            }
        }

        if (!matrix.includes("A") || gameMode == 'freeGame' || modeTurbo || isFTR) return;
        this.countScatter++;
        if (this.canGetFreeGame(count)) {
            this.node.soundPlayer && this.node.soundPlayer.playSfxByID('SCATTER_' + this.countScatter);
        }
    },

    canGetFreeGame(count) {
        if (count === 1 && this.countScatter >= 1) return true;
        if (count === 3 && this.countScatter >= 2) return true;
        if (count === 5 && this.countScatter >= 3) return true;
        return false;
    },

    _getMatrix(data) {
        return data.slice();
    },

});