cc.Class({
    extends: require('SlotGameDirector'),

    onLoad() {
        this._super();
        this.node.on("START_AUTO_SPIN_TRIAL", this.startAutoSpinTrial, this);
        this.node.gSlotDataStore.countTrialSpinTime = 0;
    },

    _delayTimeScript(script, time) {
        this._tweenDelayTimeScript && this._tweenDelayTimeScript.stop();
        this._tweenDelayTimeScript = cc.tween(this.node)
            .delay(time)
            .call(() => {
                this._tweenDelayTimeScript = null;
                this.executeNextScript(script);
            })
            .start();
    },
    _clearDelayTimeScript() {
        this._tweenDelayTimeScript && this._tweenDelayTimeScript.stop();
        this._tweenDelayTimeScript = null;
    },
    runPromotionSpin() {
        const { promotionBetId, promotionRemain } = this.node.gSlotDataStore;
        this.buttons.emit('SHOW_ALL_PROMOTION_BUTTONS');
        this.spinTimes.emit("UPDATE_SPINTIMES", promotionRemain);
        this.spinTimes.opacity = 255;
        this.isRunPromotion = true;
        this.runAction('SetUpBet', this.getTotalBetValue(promotionBetId));
    },
    _updateValueJP(script, data) {
        const { jpType, jpValue } = data;
        this.node.mainDirector.jackpot.emit('UPDATE_VALUE_JACKPOT', jpValue, jpType);
        this.executeNextScript(script);
    },
    _runAutoSpin(script) {
        if (!this.isAlwaysAutoSpin /*normal*/ && this.node.gSlotDataStore.isAutoSpin) {
            this.buttons.emit('STOP_AUTO_SPIN_SHOW');
        }
        this.node.mainDirector.gui.emit("HIDE_INTRO");
        this.table.emit("HIDE_ANIM_INTRO");
        this.skipAllEffects();
        this.buttons.emit('SPIN_DISABLE');
        this._showTrialButtons(null, false);
        this._callBackAutoSpin = () => {
            this.runAction('SpinClick');
            this.executeNextScript(script);
            this._callBackAutoSpin = null;
        };
        this.scheduleOnce(this._callBackAutoSpin, 0.3);
    },
    _showBtnStopAuto(script) {
        this.buttons.emit('STOP_AUTO_SPIN_SHOW');
        this.executeNextScript(script);
    },
    forceStopSpinning() {
        this.stopAutoSpinClick();

        const { promotion, promotionRemain, promotionTotal } = this.node.gSlotDataStore;
        if (promotionTotal && promotion && this.node.gSlotDataStore.currentGameMode == "normalGame") {
            let spinTimes = promotionTotal == promotionRemain ? Number(promotionTotal) : (Number(promotionRemain || 0) + 1);
            this.spinTimes.emit("UPDATE_SPINTIMES", spinTimes);
            this.node.gSlotDataStore.promotionRemain = spinTimes;
        }
        this.node.gSlotDataStore.isUpdateWinAmount = false;

        if (this._callBackAutoSpin) {
            this.unschedule(this._callBackAutoSpin);
        }
        this.resetPlaysessionDataLastSpin();
        this.table.emit('STOP_REEL_WITH_RANDOM_MATRIX', () => {
            this.isStopRunning = true;
        });
        this.fsm.resultReceive();
        this.fastToResultClick();
        this.runAction('GameFinish');
        this.scheduleOnce(() => {
            this._gameRestart();
        }, this.node.config.DELAY_FORCE_STOP_SPINNING ? this.node.config.DELAY_FORCE_STOP_SPINNING : 0.6);
    },
    spinClick() {
        if (!this.node || !this.node.director || !this.node.director.fsm ||
            !this.node.director.fsm.can('actionTrigger') || !this.node.mainDirector.readyToPlay) return;
        const { winAmountPS } = this.node.gSlotDataStore.playSession;
        if (winAmountPS) {
            this.winAmount.emit("UPDATE_WIN_AMOUNT", { value: winAmountPS, time: 0 });
        }
        this.skipAllEffects();
        this.node.mainDirector.gui.emit("HIDE_INTRO");
        this.table.emit("HIDE_ANIM_INTRO");
        this.runAction('SpinClick');
    },
    _setUpPaylines(script, data) {
        this.hasPayline = true;
        this.table.emit("SETUP_PAYLINES", data);
        this.executeNextScript(script);
    },
    _setupBigWild(script, data) {
        this.table.emit("SET_UP_BIG_WILD", data);
        this.executeNextScript(script);
    },
    _blinkAllPaylines_2(script) {
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        this.table.emit("BLINK_ALL_NORMAL_PAYLINES", () => { });
        this.executeNextScript(script);
    },
    _forceToClearPaylines(script) {
        this.table.emit("CLEAR_PAYLINES", true);
        this.hasPayline = false;
        this.executeNextScript(script);
    },
    _showTrialButtons(script, isOn) {
        let isAutoSpin = this.node.gSlotDataStore.isAutoSpin;
        const isFreeGame = this.node.gSlotDataStore.currentGameMode == 'freeGame';
        if (this.node.mainDirector) {
            const { trialMode } = this.node.mainDirector;
            if (trialMode && !this.forceToExitMode) {
                this.node.mainDirector.showTrialButtons(isOn);
            } else {
                isOn = isOn && !isAutoSpin && this.isStopRunning && !isFreeGame;
                this.node.mainDirector.showTrialButtons(isOn);
            }
        }
        this.executeNextScript(script);
    },
    startAutoSpinTrial() {
        this.multiSpin4Click();
    },
    _resumeGameMode(script, { name, data }) {
        this.node.soundPlayer && this.node.soundPlayer.playMainBGM('mainGame');
        this._super(script, { name, data });
    },
    _stopStepTrialMode(script) {
        this.stopAutoSpinClick();
        this.node.mainDirector.gui.emit("ON_CLICK_SWITCH_MODE");
        this.node.gSlotDataStore.canCountTrial = true;
        this.executeNextScript(script);
    },
    _updateTrialWallet(script) {
        const { winAmountPS } = this.node.gSlotDataStore.playSession;
        if (this.node.mainDirector) {
            this.node.mainDirector.updateTrialWallet(winAmountPS);
            this.executeNextScript(script);
        } else {
            cc.error('There is no main Director to play cutscenes');
            this.executeNextScript(script);
        }
    },
    _showNoticeBackToReal(script) {
        this.stopAutoSpinClick();
        this.node.mainDirector.gui.emit("SHOW_NOTICE_ASK_BACK_TO_REAL");
        this.node.gSlotDataStore.countTrialSpinTime = 0;
        this.executeNextScript(script);
    },
    _gameEnd(script) {
        const { isAutoSpin } = this.node.gSlotDataStore;
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        if (isAutoSpin && !isFTR) {
            this.hasPayline = false;
            this.table.emit("CLEAR_PAYLINES");
        }
        this.runAction('GameEnd');
        this.executeNextScript(script);
    },
    _showNormalPayline(script) {
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        if (isFTR) {
            this.table.emit("SHOW_ALL_NORMAL_PAYLINES", () => { });
            this.executeNextScript(script);
        } else {
            this.table.emit("SHOW_ALL_NORMAL_PAYLINES", () => {
                this.executeNextScript(script);
            });
        }
    },
    _updateWinningAmount(script, { winAmount, time, isFTR, rate }) {
        const { winAmountPS } = this.node.gSlotDataStore.playSession;
        if (winAmountPS > 0) {
            this.winAmount.emit("UPDATE_WIN_AMOUNT", { value: winAmount, time });
            this.winAmount.emit("PLAY_ANIM_WIN", { winAmount, time, isFTR, rate });
        }
        this.executeNextScript(script);
    },

    _playSoundWinLine(script, rate) {
        let soundName = '9881_sfx_win_1';
        if (rate > 0 && rate < 1) soundName = '9881_sfx_win_1';
        if (rate >= 1 && rate < 3) soundName = '9881_sfx_win_2';
        if (rate >= 3 && rate < 10) soundName = '9881_sfx_win_3';
        if (rate >= 10 && rate < 20) soundName = '9881_sfx_win_4';
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID(soundName);
        this.executeNextScript(script);
    },
    _playSoundIntroFree(script) {
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_freegame_trigger');
        this.executeNextScript(script);
    },
    _clearWinAmount(script) {
        this.winAmount.emit("FADE_OUT_NUMBER", 0.4);
        this.node.soundPlayer.stopSoundByID("9881_sfx_win_1");
        this.node.soundPlayer.stopSoundByID("9881_sfx_win_2");
        this.node.soundPlayer.stopSoundByID("9881_sfx_win_3");
        this.node.soundPlayer.stopSoundByID("9881_sfx_win_4");
        this.executeNextScript(script);
    },
    _pauseWallet(script, isPause) {
        this.node.gSlotDataStore.isUpdateWinAmount = isPause;
        this.executeNextScript(script);
    },

    forceToExit() {
        this.forceToExitMode = true;
        const isFree = this.node.gSlotDataStore.currentGameMode !== "normalGame";
        if (isFree) {
            this.exitScript = [{
                command: "_resumeGameMode",
                data: { name: "normalGame" }
            }, {
                command: "_gameReset"
            }];
        } else {
            this.exitScript = [{
                command: "_gameReset"
            }];
        }
        if (this.isStopRunning) {
            this.executeNextScript(this.exitScript);
        } else {
            this.fastToResultClick();
        }
    },
    _gameReset() {
        this.unscheduleAllCallbacks();
        this._clearDelayTimeScript();
        this.skipAllEffects();
        this.stopAutoSpinClick();
        this._updateSpinTimes([], 0);
        this.node.resetCallbackWhenHide();
        this.winAmount.emit("RESET_NUMBER");
        this.table.emit("RUN_BIG_WILD_IDLE")
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playMainBGM();
        }
        this._resetSpinButton([]);
        this._resumeUpdateJP([]);
        this._gameRestart();
        this.forceToExitMode = false;
    },
    skipAllEffects() {
        this._clearDelayTimeScript();
        if (this.node.mainDirector) {
            this.node.mainDirector.cutscene.emit("SKIP_CUTSCENES");
        } else {
            cc.error('There is no main Director to skip cutscenes');
        }
        this._clearPaylines([]);
    },
});