
cc.Class({
    extends: cc.Component,

    properties: {
        fishHolder: cc.Node,
    },

    onLoad() {
        this.node.on("SHOW_FISH", this.showFish, this);
        this.node.on("SKIP_FISH_FLY", this.skipFishFly, this);
        this.node.on("RESET_FISH_POOL", this.reset, this);

        this.poolFactory = this.node.poolFactory;
    },
    showFish(listFish) {
        const timeFlying = 0.5;
        let hasNewFish = false;
        listFish.forEach((fishInfo) => {
            const { symbol, col, row, isNewFish } = fishInfo;
            if (isNewFish) {
                hasNewFish = true;
                const targetPos = this._getPosByColRow(col, row);
                const newFish = this._createFish(symbol);
                newFish.emit("PLAY_ANIM_FLY", targetPos, timeFlying, () => {
                    this.node.emit("ADD_NEW_WILD", fishInfo);
                    this.poolFactory.removeObject(newFish);
                })
            }
        })
    },
    skipFishFly() {
        this.tweenFishFlying && this.tweenFishFlying.stop();
        this.tweenFishFlying = null;
    },
    _createFish(symbol) {
        const fish = this.poolFactory.getObject("Fish9877");
        fish.parent = this.fishHolder;
        fish.active = true;
        fish.emit("INIT", symbol);
        fish.position = cc.v2(this._randRange(-450, 450), -430)
        return fish;
    },
    reset() {
        this.callBackFistFly && this.callBackFistFly();
        this.callBackFistFly = null;
        this.tweenFishFlying && this.tweenFishFlying.stop();
        this.tweenFishFlying = null;
    },
    _getPosByColRow(col, row) {
        const { TABLE_FORMAT, SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const startX = -(TABLE_FORMAT.length / 2 - 0.5) * SYMBOL_WIDTH;
        const startY = TABLE_FORMAT[row] / 2 - row - 0.5;
        const X = (startX + SYMBOL_WIDTH * col);
        const Y = startY * SYMBOL_HEIGHT;
        return cc.v2(X, Y);
    },
    _randRange(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }
});
