
cc.Class({
    extends: cc.Component,

    properties: {
        page1: cc.Node,
        page2: cc.Node,
    },
    onLoad() {
        this.node.on("PLAY_ANIM", this.playAnim, this);
        this.node.on("RESET", this.reset, this);
    },
    playAnim() {
        this.reset();
        this.tweenAnim = cc.tween(this.node)
            .delay(0.5)
            .call(() => {
                this.page1.runAction(cc.fadeOut(1));
                this.page2.runAction(cc.fadeIn(1));
            })
            .delay(5)
            .call(() => {
                this.page1.runAction(cc.fadeIn(0.5));
                this.page2.runAction(cc.fadeOut(0.5));
            })
            .delay(5)
            .union()
            .repeatForever()
            .start()
    },
    reset() {
        this.page1.stopAllActions();
        this.page2.stopAllActions();
        this.page1.opacity = 255;
        this.page2.opacity = 0;
        this.tweenAnim && this.tweenAnim.stop();
        this.tweenAnim = null;
    }
});
