
cc.Class({
    extends: cc.Component,

    properties: {
        fishPoolManager: cc.Node,
        wildHolder: cc.Node,
        bellNode: cc.Node,
        particleControl: cc.Node,
    },

    onLoad() {
        this.node.on("SETUP_WILD_TABLE", this.setupWildTable, this);
        this.node.on("MOVE_WILD", this.moveWild, this);
        this.node.on("RESET_ALL_WILD", this.resetAll, this);
        this.fishPoolManager.on("ADD_NEW_WILD", this.addNewWild, this);
        this.poolFactory = this.node.poolFactory;
        this.usingObject = [];
        this.wildsMatrix = [];
        this.callbackMoveWild = null;
    },
    setupWildTable(wildData) {
        this.resetAll();
        if (!wildData || !wildData.length) return;
        wildData.forEach((wildInfo) => {
            const { symbol, col, row, fishMultiply } = wildInfo;
            const wildNode = this._createWild(col, row);
            wildNode.emit("INIT", symbol, fishMultiply);
            if (!this.wildsMatrix[col]) this.wildsMatrix[col] = [];
            this.wildsMatrix[col][row] = { wildInfo, wildNode };
        })
    },
    moveWild(newData, callback) {
        let hasWildToMove = newData.find(wildInfo => wildInfo.isNewFish);
        this.callbackMoveWild = callback;
        const { TABLE_FORMAT } = this.node.config;
        for (let i = 0; i < TABLE_FORMAT.length; i++) {
            for (let j = 0; j < TABLE_FORMAT[i]; j++) {
                if (this.wildsMatrix && this.wildsMatrix[i] && this.wildsMatrix[i][j]) {
                    const { wildNode } = this.wildsMatrix[i][j];
                    const updateWildInfo = newData.find(
                        wildInfo => !wildInfo.isNewFish && wildInfo.col === i && wildInfo.row + 1 === j
                    );
                    if (updateWildInfo) {
                        this.moveWildUp(wildNode, updateWildInfo);
                    } else {
                        this.removeWild(wildNode, i, j);
                    }
                    this.wildsMatrix[i][j] = null;
                    hasWildToMove = true;
                }
            }
        }
        this.fishPoolManager.emit("SHOW_FISH", newData);
        this.scheduleOnce(() => { callback && callback(); }, hasWildToMove ? 1 : 0);
    },
    addNewWild(wildInfo) {
        if (!wildInfo) return;
        if (!this.wildsMatrix[col]) this.wildsMatrix[col] = [];
        const { symbol, col, row, fishMultiply } = wildInfo;
        const wildNode = this._createWild(col, row);
        wildNode.emit("INIT", symbol, fishMultiply);
        if (!this.wildsMatrix[col]) this.wildsMatrix[col] = [];
        this.wildsMatrix[col][row] = { wildNode };
    },
    removeWild(destroyFish) {
        cc.tween(destroyFish)
            .to(0.1, { scale: 1.05 })
            .to(0.1, { scale: 0.95 })
            .to(0.1, { scale: 1 })
            .call(() => {
                if (destroyFish.symbol == "K1") {
                    const movingData = {
                        startNode: destroyFish,
                        endNode: this.bellNode,
                        movingTime: 0.2,
                        particleType: 'normalExplosion'
                    };
                    this.particleControl.emit("MOVE_PARTICLES", movingData);
                    this.poolFactory.removeObject(destroyFish);
                } else {
                    this.poolFactory.removeObject(destroyFish);
                }
            })
            .start()
    },
    moveWildUp(wildNode, wildInfo) {
        const { col, row, fishMultiply } = wildInfo;
        wildNode.position = this._getPosByColRow(col, row);
        wildNode.emit("UPDATE_MULTIPLY", fishMultiply);
        this.wildsMatrix[col][row] = { wildInfo, wildNode };
    },

    resetAll() {
        this._clearPool();
        this.wildsMatrix = [];
        this.usingObject = [];
        this.callbackMoveWild = null;
    },
    _createWild(col, row) {
        const wild = this.poolFactory.getObject("Wild9877");
        wild.parent = this.wildHolder;
        wild.active = true;
        wild.emit("RESET");
        wild.position = this._getPosByColRow(col, row);
        this.usingObject.push(wild)
        return wild;
    },
    _clearPool() {
        this.usingObject.forEach(obj => {
            obj.emit("RESET");
            this.poolFactory.removeObject(obj);
        })
        this.usingObject = [];
    },
    _getPosByColRow(col, row) {
        const { TABLE_FORMAT, SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const startX = -(TABLE_FORMAT.length / 2 - 0.5) * SYMBOL_WIDTH;
        const startY = TABLE_FORMAT[row] / 2 - row - 0.5;
        const X = (startX + SYMBOL_WIDTH * col);
        const Y = startY * SYMBOL_HEIGHT;
        return cc.v2(X, Y);
    },
});
