cc.Class({
    extends: require('Bet'),

    properties: {
        betIncreaseSFX: {
            default: null,
            type: cc.AudioClip,
        },
        betDecreaseSFX: {
            default: null,
            type: cc.AudioClip,
        },
    },

    reduceBet() {
        this._super();
        if (this.node.soundPlayer) this.node.soundPlayer.playSFX(this.betDecreaseSFX);
    },
    increaseBet() {
        this._super();
        if (this.node.soundPlayer) this.node.soundPlayer.playSFX(this.betIncreaseSFX);
    },
});
