cc.Class({
    extends: cc.Component,

    properties: {
        bannerSkeleton: sp.Skeleton,
    },
    
    onLoad () {
        this.node.on("PLAY_ANIM_IDLE", this.playAnimIdle, this);
        this.node.on("PLAY_ANIM_OPEN", this.playAnimOpen, this);
        this.node.on("RESET_BANNER", this.resetBanner, this);
    },

    playAnimIdle() {
        this.bannerSkeleton.timeScale = 1;
        this.bannerSkeleton.setAnimation(0, 'Idle', true);
    },

    playAnimOpen(callback) {
        this.node.runAction(cc.sequence(
            cc.delayTime(0.2),
            cc.callFunc(() => {
                if (this.node.soundPlayer) {
                    this.node.soundPlayer.playSfxOpenFlag();
                }
                this.bannerSkeleton.setAnimation(0, 'Open', false);
                this.bannerSkeleton.timeScale = 1.5;
                this.bannerSkeleton.setCompleteListener(() => {
                    callback && callback();
                    this.bannerSkeleton.setCompleteListener(() => {});
                });
            })
        ));
        
    },

    resetBanner() {
        this.node.stopAllActions();
        this.bannerSkeleton.setCompleteListener(() => {});
    },
});
