const { formatMoney } = require("utils");
const lodash = require('lodash');

cc.Class({
    extends: require('WinAmountv2'),
    properties: {
        animWinAmt: sp.Skeleton,
    },

    onLoad() {
        this._super();
        this.node.on("PLAY_ANIM_WIN", this.playAnimWin, this);
        this.node.on("HIDE_ANIM_WIN", this.hideAnimWin, this);
    },

    playAnimWin({ winAmount, rate }) {
        const valueFormat = formatMoney(winAmount);
        let animName = 'Win_Lv1';
        if (rate < 1) animName = 'Win_Lv1';
        if (rate >= 1 && rate < 3) animName = 'Win_Lv2';
        if (rate >= 3 && rate < 10) animName = 'Win_Lv3';
        if (rate >= 10 && rate < 20) animName = 'Win_Lv4';
        if (rate < 3) {
            this.labelWinAmount.node.opacity = 0;
            this.labelWinAmount.node.runAction(cc.fadeIn(0.2));
        } else {
            this.labelWinAmount.node.opacity = 255;
        }
        if (valueFormat != this.labelWinAmount.string) {
            this.animWinAmt.node.active = true;
            this.animWinAmt.setAnimation(0, animName, false);
        }
    },
    hideAnimWin() {
        this.animWinAmt.node.active = false;
    },
    clearWinAmount(){
        this.animWinAmt.node.active = false;
        this._super();
    },
    fadeOutNumber(time = 1) {
        this.labelWinAmount.node.stopAllActions();
        this.labelWinAmount.node.opacity = 255;
        this._super(time);
    },
    _updateWallet() {
        if (this.gameDirector) {
            const wallet = this.gameDirector.gameStateManager.getCurrentWallet();
            if (wallet && lodash.isNumber(wallet) && !lodash.isNaN(wallet)) {
                this.node.gSlotDataStore.slotBetDataStore.updateWallet(wallet);
            }
            if (this.gameDirector.trialMode) {
                this.gameDirector.updateTrialWallet();
            } else {
                this.gameDirector.updateWallet();
            }
        }
    },
    updateBgToWin() { },
    updateBgToLastWin() { }
});
