const SOUND_STATE = {
    NOT_PLAY: 0,
    CAN_PLAY: 1,
    PLAYED: 2
};
cc.Class({
    extends: cc.Component,

    properties: {
        sfxReelSpinning: {
            default: null,
            type: cc.AudioClip,
        },
        sfxReelStop: {
            default: null,
            type: cc.AudioClip,
        },
        sfxWilds: {
            default: [],
            type: cc.AudioClip,
        },
        wildSymbol: 'K',
    },

    start () {
        this.node.on("SET_UP_SOUND_STOP",this.setUpSoundStop,this);
        this.node.on("REEL_STOP_SOUND",this.reelStopSound,this);
        this.node.on("TABLE_START_SOUND",this.reelStartSound,this);
    },
    
    reelStartSound() {
        this.wildMatrix = new Array(5).fill(SOUND_STATE.NOT_PLAY);
        this.wildMatrix[0] = SOUND_STATE.CAN_PLAY;
    },

    setUpSoundStop(matrix) {
        for (let col = 1; col < matrix.length - 1; col++) {
            for (let row = 0; row < matrix[col].length; row++) {
                const previous = col - 1;
                if (matrix[col][row] === this.wildSymbol && this.wildMatrix[previous] === SOUND_STATE.CAN_PLAY) {
                    this.wildMatrix[col] = SOUND_STATE.CAN_PLAY;
                }
            }
        }
    },

    reelStopSound({count, context}) {
        if (count === 1 || count === 5 || !this.node.soundPlayer) return;
        const reelIndex = count - 1;
        const currentState = this.wildMatrix[reelIndex];
        const previousState = this.wildMatrix[reelIndex - 1];
        const isMissFree = count === 4 && currentState === SOUND_STATE.NOT_PLAY && previousState !== SOUND_STATE.NOT_PLAY;
        
        if (context.isSureWin || context.node.mode === 'TURBO' || context.isFastToResult) {
            const index = this.wildMatrix.lastIndexOf(SOUND_STATE.CAN_PLAY);
            if (index === 3 && this.wildMatrix[3] === SOUND_STATE.CAN_PLAY) {
                this.playLastWild(index);
            } else if (isMissFree) {
                this.node.soundPlayer.playSFX(this.sfxReelStop);
            } else {
                this.wildMatrix[reelIndex] = SOUND_STATE.NOT_PLAY;
            }
            return;
        }
        if (isMissFree) {
            this.node.soundPlayer.playSFX(this.sfxReelStop);
        } else if (currentState === SOUND_STATE.CAN_PLAY && previousState !== SOUND_STATE.NOT_PLAY) {
            this.wildMatrix[reelIndex] = SOUND_STATE.PLAYED;
            this.node.soundPlayer.playSFX(this.sfxWilds[reelIndex - 1]);
        }
    },

    playLastWild(index) {
        this.node.soundPlayer.playSFX(this.sfxWilds[index - 1]);
        this.wildMatrix = new Array(5).fill(SOUND_STATE.PLAYED);
    },
});