
cc.Class({
    extends: cc.Component,
    properties: {
        labelMultiply: cc.Label,
        wildK1: cc.Node,
        wildK2: cc.Node,
    },

    onLoad() {
        this.node.on("INIT", this.init, this);
        this.node.on("UPDATE_MULTIPLY", this.updateMultiply, this);
        this.node.on("RESET", this.reset, this);
        this.tweenFlying = null;
    },
    init(symbol, mul) {
        this.node.symbol = symbol;
        this.labelMultiply.string = 'x' + mul;
        if (symbol == "K1") {
            this.wildK1.active = true;
            this.wildK2.active = false;
        } else {
            this.wildK1.active = false;
            this.wildK2.active = true;
        }
    },
    updateMultiply(mul) {
        this.labelMultiply.string = 'x' + mul;
    },
    reset() {
        this.labelMultiply.string = '';
    }
});
