const serviceRest = require('serviceRest');
const globalNetwork = require('globalNetwork');

const { ccclass, property } = cc._decorator;

@ccclass
export class GameEvents extends cc.Component {

    listEvent = [];

    @property({type: cc.Prefab})
    eventPrefab = null;

    @property({type: cc.Node})
    eventHolder = null;

    @property({type: cc.Toggle})
    filterSlot = null;

    @property({type: cc.EditBox})
    filterTag = null;

    eventPool = null;

    onLoad() {
        this.eventPool = new cc.NodePool();
    }

    getEvent() {
        let event = this.eventPool.get();
        if (!event) {
            event = cc.instantiate(this.eventPrefab);
        }
        return event;
    }

    removeAllEvents() {
        while (this.eventHolder.children.length > 0) {
            this.eventPool.put(this.eventHolder.children[0]);
        }
    }

    onEnable() {
        this.removeAllEvents();
        this.loadEvents();
    }

    onDisable() {
        this.listEvent = null;
        cc.sys.garbageCollect();
    }

    loadEvents() {
        const eventUrl = 'event-config/event-available-by-user';
        const headers = {
            'Authorization': globalNetwork.getToken()
        }
        serviceRest.getWithHeader({url: eventUrl, callback:this.onEventsLoaded.bind(this), headers: headers})
    }

    onEventsLoaded(res) {
        this.listEvent = res.data || [];
        this.filterEvent(this.filterSlot.isChecked ? this.filterTag.string : "");
    }

    onToggleFilter() {
        this.filterEvent(this.filterSlot.isChecked ? this.filterTag.string : "");
    }

    filterEvent(key: string) {
        this.removeAllEvents();
        for (let i=0; i<this.listEvent.length; i++) {
            const { eventInfo } = this.listEvent[i];
            if (eventInfo) {
                const eventName = eventInfo.name.toLocaleLowerCase();
                const updatedTitle = key.toLocaleLowerCase();
                if (eventName.includes(updatedTitle)) {
                    let event = this.getEvent();
                    event.setParent(this.eventHolder);
                    event.getComponent('GameEvent').init(this.listEvent[i]);
                }
            }
        }
    }

    onLoadError(error) {
        // console.log(error);
    }

    onDestroy() {
        this.eventPool.clear();
    }
}