const EXPAND_ANIM = ['Expand_Top_FreeG', 'Expand_Mid_FreeG', 'Expand_Bot_FreeG'];
cc.Class({
    extends: cc.Component,

    properties: {
        anim: sp.Skeleton,
        dimColor: cc.Color,
    },

    onLoad() {
        this.node.on("PLAY_ANIMATION", this.playAnimation, this);
        this.node.on("PLAY_ANIMATION_WIN", this.playAnimationWin, this);
        this.node.on("PLAY_ANIMATION_IDLE", this.playAnimationIdle, this);

        this.node.on("ENABLE_HIGHLIGHT", this.enableHighlight, this);
        this.node.on("DISABLE_HIGHLIGHT", this.disableHighlight, this);

        this.node.on("PLAY_ANIM_NEAR_WIN", this.playAnimNearWin, this);
        this.node.on("STOP_ANIM_NEAR_WIN", this.stopAnimNearWin, this);

        this.node.on("RESET", this.reset, this);
        this.reset();
    },
    playAnimNearWin() {
        this.isNearWin = true;
        this.anim.setCompleteListener(() => {
            this.node.position = cc.v2(this.node.position.x, 0);
            this.anim.setAnimation(0, 'Expand_NearWin_Start', false);
            this.anim.addAnimation(0, 'Expand_NearWin_Idle', true);
            this.anim.setCompleteListener(() => { });
        });
    },
    stopAnimNearWin() {
        if (!this.isNearWin) return;
        this.isNearWin = false;
        this.anim.setCompleteListener(() => { });
        this.node.position = cc.v2(this.node.position.x, 0);
        this.anim.setAnimation(0, 'Expand_NearWin_End', false);
        this.anim.addAnimation(0, 'Expand_Idle', true);
    },
    playAnimationIdle() {
        if (this.isIdle) return;
        this.isIdle = true;
        this.isPlayingAnimWin = false;
        this.tweenExpand && this.tweenExpand.stop();
        this.tweenExpand = null;
        this.anim.setCompleteListener(() => { });
        this.node.position = cc.v2(this.node.position.x, 0);
        this.anim.setAnimation(0, 'Expand_Idle', true);
    },
    playAnimationWin() {
        if (this.isPlayingAnimWin) return;
        this.isIdle = false;
        this.isPlayingAnimWin = true;
        this.node.position = cc.v2(this.node.position.x, 0);
        this.anim.setAnimation(0, 'Expand_Win', true);
    },
    playAnimation(row, isFast) {
        if (isFast) {
            this.node.position = cc.v2(this.node.position.x, 0);
            this.anim.setAnimation(0, 'Expand_Idle', true)
            this.isIdle = true;
        } else {
            this.isIdle = false;
            const animName = EXPAND_ANIM[row];
            this.anim.setAnimation(0, animName, false)
            this.tweenExpand = cc.tween(this.node)
                .delay(0.98)
                .call(() => {
                    this.node.position = cc.v2(this.node.position.x, 0);
                    this.anim.setAnimation(0, 'Expand_Idle', true)
                    this.isIdle = true;
                    this.tweenExpand = null;
                })
                .start();
        }
    },
    enableHighlight() {
        if (this.isEnableHighlight) return;
        this.isEnableHighlight = true;
        this.tweenColor(this.anim.node, cc.Color.WHITE)
    },
    disableHighlight() {
        if (!this.isEnableHighlight) return;
        this.isEnableHighlight = false;
        this.tweenColor(this.anim.node, this.dimColor)
    },
    reset() {
        this.isIdle = false;
        this.isPlayingAnimWin = false;
        this.anim.node.tweenColorStatic && this.anim.node.tweenColorStatic.stop();
        this.anim.node.tweenColorStatic = null;
        this.tweenExpand && this.tweenExpand.stop();
        this.tweenExpand = null;
        this.anim.node.color = cc.Color.WHITE;
        this.isEnableHighlight = true;
    },
    tweenColor(node, finalColor) {
        node.tweenColorStatic && node.tweenColorStatic.stop();
        node.tweenColorStatic = cc.tween(node)
            .to(0.2, { color: finalColor })
            .call(() => {
                node.tweenColorStatic = null;
            })
            .start();
    },
});
