import { LobbyScene } from "./LobbyScene";

const { formatMoney } = require('utils');
const { ccclass, property } = cc._decorator;

@ccclass
export class GameQuest extends cc.Component {

    gameId = null;
    finished = false;
    eventId = '';
    @property({ type: cc.Sprite })
    iconGame: cc.Sprite = null;

    @property({ type: cc.Node })
    iconDone: cc.Node = null;

    @property({ type: cc.Label })
    reward: cc.Label = null;

    init(data, eventId) {
        this.eventId = eventId;
        this.gameId = data.serviceIds[0];
        const scene = cc.director.getScene();
        const lobbyScene = scene.getComponentInChildren(LobbyScene);
        if (lobbyScene) {
            this.iconGame.spriteFrame = lobbyScene.getIconSpriteFrame(this.gameId);
        } else {
            this.iconGame.spriteFrame = null;
        }
        this.finished = data.isComplete;
        this.iconDone.active = this.finished;
        this.reward.string = formatMoney(data.rewardAmount);
        // console.log(`Reward String ${this.reward.string}`)
    }
}