
cc.Class({
    extends: cc.Component,

    properties: {
        nextBtn: cc.Button,
        preBtn: cc.Button,
        pageView: cc.PageView,
    },

    onLoad() {
        this.pageView.node.on("page-turning", this.pageViewEvent, this);
        this.curInfoID = 0;
        this.activeButtons(this.curInfoID);
        this.scheduleOnce(() => {
            this.renderPage(this.curInfoID)
        }, 1)
    },
    nextClick() {
        this.curInfoID++;
        this.pageView.scrollToPage(this.curInfoID);
        this.activeButtons(this.curInfoID);
        this.renderPage(this.curInfoID);
    },
    previousClick() {
        this.curInfoID--;
        this.pageView.scrollToPage(this.curInfoID);
        this.activeButtons(this.curInfoID);
        this.renderPage(this.curInfoID);
    },

    pageViewEvent() {
        let newIndex = this.pageView.getCurrentPageIndex();
        if (Math.abs(newIndex - this.curInfoID) !== 1) {
            this.pageView.scrollToPage(this.curInfoID, 0.1);
            return;
        }
        this.curInfoID = newIndex;
        this.activeButtons(this.curInfoID);
        this.renderPage(this.curInfoID);
    },

    renderPage(pageIdx) {
        const totalInfo = this.pageView.content.children.length;
        for (let i = 0; i <= totalInfo - 1; i++) {
            const page = this.pageView.content.children[i];
            if (pageIdx == i) {
                page.emit("PLAY_ANIM");
            } else {
                page.emit("RESET");
            }
        }
    },

    activeButtons(index) {
        const totalInfo = this.pageView.content.children.length;
        if (index >= totalInfo - 1) {
            index = totalInfo - 1;
            this.nextBtn.interactable = false;
        } else {
            this.nextBtn.interactable = true;
        }
        if (index <= 0) {
            index = 0;
            this.preBtn.interactable = false;
        } else {
            this.preBtn.interactable = true;
        }
    },

});
