const { formatMoney } = require('utils');
cc.Class({
    extends: require('TotalWinPanelv2'),

    properties: {
        animation: sp.Skeleton,
        particle: cc.Node,
    },

    enter() {
        if (this.closeBtn) {
            this.closeBtn.active = true;
            this.closeBtn.opacity = 0;
            this.closeBtn.runAction(cc.fadeTo(0.3, 200));
        }
        this.animation.node.active = true;
        this.canClose = false;
        this.winAmount.getComponent(cc.Label).string = '';
        const { winAmount, rate } = this.content;
        let delayTime = 4;
        let animAppear = 'LvL1_Appear';
        let animIdle = 'LvL1_Idle';
        let animDisappear = 'LvL1_Disappear';
        let soundID = '9881_total_win_1';
        if (rate >= 30 && rate < 70) { //win 2
            delayTime = 5;
            animAppear = 'LvL2_Appear';
            animIdle = 'LvL2_Idle';
            animDisappear = 'LvL2_Disappear';
            soundID = '9881_total_win_2';
            this.particle.emit("START_PARTICLE");
        }
        if (rate >= 70) {  //win 3
            delayTime = 6;
            animAppear = 'LvL3_Appear';
            animIdle = 'LvL3_Idle';
            animDisappear = 'LvL3_Disappear';
            soundID = '9881_total_win_3';
            this.particle.emit("START_PARTICLE");
        }
        this.winAmount.stopAllActions();
        this.winAmount.opacity = 0;
        this.winAmount.setScale(1, 1)
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID(soundID);
        this.node.runAction(cc.sequence(
            cc.callFunc(() => {
                this.winAmount.runAction(cc.fadeIn(0.5));
                this.animation.setAnimation(0, animAppear, false);
                this.animation.addAnimation(0, animIdle, true);
                this.winAmount.getComponent(cc.Label).string = formatMoney(winAmount);
            }),
            cc.delayTime(0.5),
            cc.callFunc(() => {
                this.canClose = true;
                this.closeBtn.getComponent(cc.Button).interactable = true;
            }),
            cc.delayTime(delayTime - 1),
            cc.callFunc(() => {
                this.animation.setAnimation(0, animDisappear, false);
                this.particle.emit("STOP_PARTICLE");
            }),
            cc.delayTime(0.2),
            cc.callFunc(() => {
                this.winAmount.stopAllActions();
                this.winAmount.runAction(cc.spawn(
                    cc.scaleTo(0.3, 0),
                    cc.fadeOut(0.3)
                ));
                this.callback && this.callback();
                this.callback = null;
                this.closeBtn.runAction(cc.fadeOut(0.3));
            }),
            cc.delayTime(1.3),
            cc.callFunc(() => {
                this.exit();
            }),
        ));
    },
});
