cc.Class({
    extends: require('SlotGameWriter'),

    makeScriptResultReceive() {
        const {matrix, feuro, matrix0, wildMultiply} = this.node.gSlotDataStore.playSession;

        let listScript = [];
        if (feuro) {
            listScript.push({
                command: "_setupEuroBanner",
                data: feuro
            });
        }
        if (matrix0) {
            listScript.push({
                command: "_showBigWild",
                data: { matrix: matrix0, multiply: wildMultiply }
            });
        }
        listScript.push({
            command: "_resultReceive",
            data: { matrix: matrix }
        });

        listScript.push({
            command: "_showResult",
            data: matrix,
        });

        return listScript;
    },

    makeScriptShowResults() {
        const {winAmountPS, freeGameRemain, matrix, winAmount, payLines, feuro} = this.node.gSlotDataStore.playSession;
        const {currentBetData} = this.node.gSlotDataStore.slotBetDataStore.data;
        const listScript = [];
        const isBigwin = winAmount && winAmount >= currentBetData * 20;
        this.isFastResult = false;

        listScript.push({
            command: "_setUpPaylines",
            data: {matrix, payLines},
        });
        
        if (feuro) {
            listScript.push({
                command: "_openAllBanners",
            });
        }

        const rate = winAmount ? winAmount / currentBetData : 0;
        if (rate >= 1) {
            listScript.push({
                command: "_playLightWin",
            });
        }

        if (rate >= 5) {
            listScript.push({
                command: "_playAnimGirls",
            });
        }

        if (isBigwin) {
            listScript.push({
                command: "_blinkAllPaylines",
            });
            listScript.push({
                command: "_showCutscene",
                data: {
                    name: "WinEffect",
                    content: {
                        winAmount,
                        currentBetData
                    }
                }
            });
        }

        if (payLines && payLines.length > 0) {
            if (!isBigwin) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
            }
            listScript.push({
                command: "_showNormalPayline",
            });
        } else {
            listScript.push({
                command: "_clearPaylines",
            });
        }
        listScript.push({
            command: "_gameEnd"
        });

        if (!freeGameRemain || freeGameRemain <= 0) {
            if (winAmountPS && winAmountPS > 0) {
                listScript.push({
                    command: '_updateWinningAmount',
                    data: {
                        winAmount: winAmountPS,
                        time: 300
                    }
                });
            }
            listScript.push({
                command: "_delayTimeScript",
                data: 1.5
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "TotalWinPanel",
                    content: {}
                }
            });
            listScript.push({
                command: "_gameExit",
            });
        } else {
            listScript.push({
                command: "_gameRestart"
            });
        }
        return listScript;
    },

    scriptUpdateWinAmount(listScript) {
        const {winAmountPS, winAmount} = this.node.gSlotDataStore.playSession;
        const {gameSpeed} = this.node.gSlotDataStore;
        const isFTR = gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        if (winAmount && winAmount > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: {winAmount: winAmountPS, time: 300, isFTR, rate: this._checkRateSoundWin()}
            });
        }
    },

    _checkRateSoundWin() {
        const { winAmount } = this.node.gSlotDataStore.playSession;
        const { currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        if (winAmount < currentBetData) return 0;
        if (winAmount <= currentBetData * 5) return 1;
        if (winAmount < currentBetData * 20) return 2;
    },
});
