
cc.Class({
    extends: cc.Component,

    onLoad() {
        this.node.on('START_PARTICLE', this.startParticle, this);
        this.node.on('STOP_PARTICLE', this.stopParticle, this);
        this._particles = this.node.getComponentsInChildren(cc.ParticleSystem);
        this._trails = this.node.getComponentsInChildren(cc.MotionStreak);

    },

    startParticle() {
        this.node.active = true;
        this._particles.forEach((particle) => {
            particle.node.opacity = 0;
            particle.resetSystem();
            this.scheduleOnce(() => {
                particle.node.opacity = 255;
            }, 0.1);
        });
    },

    stopParticle() {
        this._particles.forEach(it => it.stopSystem());
    },
});
