cc.Class({
    extends: require('DataStorev2'),

    onLoad() {
        this._super();
        this.node.gSlotDataStore.convertPayLineAll = this.convertPayLineAllways.bind(this);
    },

    formatData(playSession) {
        const { TABLE_FORMAT, TABLE_FORMAT_FREE } = this.node.config;
        const { matrix, matrix0, normalGameMatrix, freeGameMatrix, payLines, state } = playSession;
        let tableFormat = TABLE_FORMAT;
        const freeGameState = 2;
        if (state == freeGameState) {
            tableFormat = TABLE_FORMAT_FREE;
        }

        if (matrix) {
            playSession.matrix = this.node.gSlotDataStore.convertSlotMatrix(matrix, tableFormat);
        } else if (freeGameMatrix) {
            playSession.matrix = this.node.gSlotDataStore.convertSlotMatrix(freeGameMatrix, tableFormat);
        } else if (normalGameMatrix) {
            playSession.matrix = this.node.gSlotDataStore.convertSlotMatrix(normalGameMatrix, tableFormat);
        }

        if (matrix0) {
            playSession.matrix0 = this.node.gSlotDataStore.convertSlotMatrix(matrix0, tableFormat);
        }
        if (playSession.payLines) {
            playSession.payLines = this.convertPayLineAllways(payLines);
        }
        this.node.gSlotDataStore.playSession = playSession;
        cc.warn("%c run data-update ", "color: red", playSession);
        return playSession;
    },

    convertPayLineAllways(payLines = []) {
        return payLines.map(item => {
            const dataSplit = item.split(';');
            return {
                symbolId: dataSplit[0],
                totalWinAmount: dataSplit[1],
                symbolCount: dataSplit[2],
                combination: dataSplit[3],
                payableSymbol: dataSplit[4],
                multiplier: dataSplit[5],
                hasWild: dataSplit[6],
            };
        });
    }
});