cc.Class({

    extends: cc.Component,

    properties: {
        wheel: cc.Node,
        configFile: {
            type: cc.Asset,
            default: null,
        }
    },
    onLoad() {
        this._config = this.configFile.json;
        this.node.on("INIT", this.init, this);
        this.node.on("START_SPIN", this.startSpin, this);
        this.node.on("SET_RESULT", this.setResult, this);
        this.node.on("STOP_SPIN", this.stopSpin, this);
        this.init(this._config);
        window.wheel = this;
    },
    lateUpdate(dt) {
        if (!this.isSpinning) return;
        this.wheel.angle += dt * this.rotateSpeed;
        this.wheel.angle %= 360;
    },

    init(config) {
        Object.assign(this._config, config);
        this.tweenSpeed = null;
        this.tweenStop = null;
        this.result = null;
        this.stepAngle = 360 / this._config.totalItem;
        this.rotateSpeed = 0;
    },
    startSpin() {
        if (this.isSpinning) return;
        this.isSpinning = true;
        const { speedUpTime, maxSpeed } = this._config;
        this.tweenSpeed && this.tweenSpeed.stop();
        this.tweenSpeed = cc.tween(this)
            .to(speedUpTime, { rotateSpeed: maxSpeed })
            .call(() => {
                this.tweenSpeed = null;
            })
            .start();
    },
    setResult(result) {
        this.result = result;
        this.endAngle = 360 - this._getItemAngle(this.result);
    },
    _getItemAngle(index) {
        return this.stepAngle * index;
    },
    stopSpin() {
        const { slowDownTime, endSpeed , maxSpeed, bufferStop} = this._config;
        const bufferAngle = bufferStop * this.stepAngle;
        let slowDownAngle = (maxSpeed + endSpeed) * slowDownTime / 2;
        let delayAngle = 360 + (this.endAngle - bufferAngle - slowDownAngle) % 360;
        let delayTime = ((360 + delayAngle - this.wheel.angle) % 360) / maxSpeed;

        this.tweenStop = cc.tween(this)
            .delay(delayTime)
            .to(slowDownTime, { rotateSpeed: endSpeed })
            .call(() => {
                this.isSpinning = false;
                this._tweenToResult();
            })
            .start();
    },
    _tweenToResult() {
        const { endSpeed } = this._config;
        const offset = (360 + this.endAngle - this.wheel.angle) % 360;
        this.tweenStop = cc.tween(this.wheel)
            .to(offset / endSpeed, { angle: this.endAngle })
            .call(() => {
                this.tweenStop = null;
            })
            .start()
    },
    resetSpinWheel() {
        this.tweenSpeed && this.tweenSpeed.stop();
        this.tweenSpeed = null;

        this.tweenStop && this.tweenStop.stop();
        this.tweenStop = null;

        this.isSpinning = false;
        this.rotateSpeed = 0;
    },
    getConfig() {
        return Object.assign(Object.create(null), this._config);
    }
});
