/* global cc,sp */

cc.Class({
    extends: cc.Component,

    properties: {
        symbolIds: '2,3,4,5,6,7,8,9,10,K,K1,K2,K3,J,Q,JP,A,R',
        defaultUrl: 'https://static.ktek.io/animTool/spines/',
        prefix: 'Symbol_',
    },

    onLoad() {
        this.node.controller = this;
        const types = ['png', 'atlas', 'json'];
        this.spineSymbolUrls = [];
        this.spineObject = {};
        
        this.symbolIds.split(',').forEach(id => {
            types.forEach(type => {
                const url = `${this.defaultUrl}${this.prefix}${id}.${type}`;
                this.spineSymbolUrls.push(url);
                if (this.spineObject[id]) {
                    this.spineObject[id][type] = url;
                } else {
                    this.spineObject[id] = {
                        [type]: url
                    };
                }
            });
        });
        
        this.updatedSpines = [];
        let thiz = this;
        cc.loader.load(this.spineSymbolUrls, (errors, result) => {
            if (errors) {
                for (let i = 0; i < errors.length; i++) {
                    cc.log('Error url [' + errors[i] + ']: ' + result.getError(errors[i]));
                }
            }
            
            for (const id in this.spineObject) {
                const spine = this.spineObject[id];
                let atlasText, skeletonJson, texture;
                for (const type in spine) {
                    let content = result.getContent(spine[type]);
                    if (content) {
                        if (type === 'png') texture = content;
                        else if (type === 'json') skeletonJson = content;
                        else atlasText = content;
                    }
                    if (texture && skeletonJson && atlasText) {
                        const skeletonData = new sp.SkeletonData();
                        skeletonData.atlasText = atlasText;
                        skeletonData.skeletonJson = skeletonJson;
                        skeletonData.textures.push(texture);
                        skeletonData.textureNames.push(`${this.prefix}${id}.png`);
                        this.updatedSpines.push({name: id, spine: skeletonData});
                    }
                }
            }
            thiz.node.symbolPayline.getComponent("SlotSymbolPayline").spineList = this.updatedSpines;
        });
    },
});
