/* global cc */

const {convertAssetArrayToObject} = require('utils');

cc.Class({
    extends: cc.Component,
    properties: {
        symbolIds: '2,3,4,5,6,7,8,9,10,K,K1,K2,K3,J,Q,JP,A,R',
        defaultUrl: 'https://static.ktek.io/animTool/sprites/',
        blurSymbolPrefix: ''
    },

    onLoad() {
        this.listIds = this.symbolIds.split(',');
        this.staticSymbolUrls = this.listIds.map(value => `${this.defaultUrl}${value}.png`) || [];
        this.blurSymbolUrls = this.listIds.map(value => `${this.defaultUrl}blur${value}.png`) || [];
        this.updatedSprites = [];
        this.updatedBlurs = [];

        this.getStaticSymbols();
        this.getBlurSymbols();
    },

    getStaticSymbols() {
        cc.loader.load(this.staticSymbolUrls, (errors, resources) => {
            if (errors) {
                for (let i = 0; i < errors.length; i++) {
                    // cc.log('Error url [' + errors[i] + ']: ' + result.getError(errors[i]));
                }
            }
            for (let i = 0; i < this.staticSymbolUrls.length; i++) {
                let texture = resources.getContent(this.staticSymbolUrls[i]);
                if (texture instanceof cc.Texture2D) {
                    let spriteFrame = new cc.SpriteFrame(texture);
                    spriteFrame.name = this.listIds[i];
                    this.updatedSprites.push(spriteFrame);
                }
            }
            this.node.assets = convertAssetArrayToObject(this.updatedSprites);
            this.node.emit('REPLACE_SYMBOL_ASSETS', this.node.assets);
        });
    },

    getBlurSymbols() {
        cc.loader.load(this.blurSymbolUrls, (errors, resources) => {
            if (errors) {
                for (let i = 0; i < errors.length; i++) {
                    // cc.log('Error url [' + errors[i] + ']: ' + results.getError(errors[i]));
                }
            }
            for (let i = 0; i < this.blurSymbolUrls.length; i++) {
                let texture = resources.getContent(this.blurSymbolUrls[i]);
                if (texture instanceof cc.Texture2D) {
                    let spriteFrame = new cc.SpriteFrame(texture);
                    spriteFrame.name = `${this.blurSymbolPrefix}${this.listIds[i]}`;
                    this.updatedBlurs.push(spriteFrame);
                }
            }
            this.node.blurAssets = convertAssetArrayToObject(this.updatedBlurs);
            this.node.emit('REPLACE_SYMBOL_BLUR_ASSETS', this.node.blurAssets);
        });
    }
});
