cc.Class({
    extends: cc.Component,

    properties: {
        listLights: [cc.Node],
        firePrefabName: 'FireEffectPrefab',
        confettiPrefabName: 'ConfettiEffectPrefab',
        fireHolder: cc.Node,
        confettiHolder: cc.Node,
    },

    onLoad () {
        this.node.on("PLAY_LIGHT_IDLE", this.playLightIdle, this);
        this.node.on("PLAY_LIGHT_NEAR_WIN", this.playLightNearWin, this);
        this.node.on("PLAY_SURE_WIN_EFFECT", this.playSureWinEffect, this);
        this.node.on("PLAY_ANIM_STOP_SURE_WIN", this.playAnimStopSureWin, this);
        this.playLightIdle();
        this.usingFireObj = [];
        this.usingConfettiObj = [];
        this.fireResetTime = 0.5;
        this.bufferTime = 0.2;
        this.isPlaying = false;
    },

    playLightIdle() {
        this.listLights.forEach(light => {
            light.active = false;
        });
    },

    playLightNearWin() {
        this.listLights.forEach(light => {
            light.active = true;
            light.getComponent(sp.Skeleton).setAnimation(0, 'animation', true);
        });
    },

    createObject(type) {
        const poolFactory = this.node.poolFactory;
        if (type === 1) {
            if (this.usingFireObj.length) {
                for (let index = 0; index < this.usingFireObj.length; index++) {
                    this.node.poolFactory.removeObject(this.usingFireObj[index]);
                }
                this.usingFireObj = [];
            }
            for (let index = 0; index < 6; index++) {
                const fire = poolFactory.getObject(this.firePrefabName);
                fire.width = this.node.config.SYMBOL_WIDTH;
                fire.parent = this.fireHolder;
                this.usingFireObj.push(fire);
                this.playAnimFire(this.usingFireObj[index]);
            }
        } else {
            if (this.usingConfettiObj.length) {
                for (let index = 0; index < this.usingConfettiObj.length; index++) {
                    this.node.poolFactory.removeObject(this.usingConfettiObj[index]);
                }
                this.usingConfettiObj = [];
            }
            for (let index = 0; index < 6; index++) {
                const confetti = poolFactory.getObject(this.confettiPrefabName);
                confetti.width = this.node.config.SYMBOL_WIDTH;
                confetti.parent = this.confettiHolder;
                this.usingConfettiObj.push(confetti);
            }
        }
    },

    playSureWinEffect() {
        this.isPlaying = true;
        this.curentConfig = this.node.curentConfig;
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playSfxSureWin();
        }
        this.fireHolder.stopAllActions();
        this.createObject(1);
    },

    playAnimFire(node) {
        const listParticle = node.getComponentsInChildren(cc.ParticleSystem);
        node.active = true;
        listParticle.forEach(item => {
            item.resetSystem();
        });
    },

    playAnimStopSureWin() {
        if (!this.isPlaying) return;
        this.isPlaying = false;

        for (let index = 0; index < this.usingFireObj.length; index++) {
            const node = this.usingFireObj[index];
            const listParticle = node.getComponentsInChildren(cc.ParticleSystem);
            listParticle.forEach(item => {
                item.stopSystem();
            });
        }
        this.fireHolder.runAction(cc.sequence(
            cc.delayTime(this.fireResetTime),
            cc.callFunc(() => {
                this.usingFireObj.forEach(node => {
                    this.node.poolFactory.removeObject(node);
                });
                this.usingFireObj = [];
            })
        ));

        this.createObject(2);
        for (let index = 0; index < this.usingConfettiObj.length; index++) {
            const node = this.usingConfettiObj[index];
            const listParticle = node.getComponentsInChildren(cc.ParticleSystem);
            listParticle.forEach(item => {
                item.resetSystem();
            });
        }
        this.confettiHolder.runAction(cc.sequence(
            cc.callFunc(() => {
                this.usingConfettiObj.forEach(node => {
                    node.active = true;
                    node.opacity = 0;
                    node.runAction(cc.sequence(
                        cc.delayTime(0.05),
                        cc.fadeIn(0.05)
                    ));
                });
            }),
            cc.delayTime(this.curentConfig.CONFETTI_DUR - this.bufferTime),
            cc.callFunc(() => {
                this.usingConfettiObj.forEach(node => {
                    node.active = false;
                });
            })
        ));

        this.scheduleOnce(() => {
            for (let index = 0; index < this.usingConfettiObj.length; index++) {
                this.node.poolFactory.removeObject(this.usingConfettiObj[index]);
            }
            this.usingConfettiObj = [];
            this.confettiHolder.stopAllActions();
        }, this.curentConfig.CONFETTI_DUR);
    },

    playAnimConfetti(node) {
        const listParticle = node.getComponentsInChildren(cc.ParticleSystem);
        listParticle.forEach(item => {
            item.resetSystem();
        });
        
    },
});
