
cc.Class({
    extends: require('BetHistoryScrollItem'),

    properties: {
        frameBG: cc.Sprite,
        normalFrame: cc.SpriteFrame,
        selectedFrame: cc.SpriteFrame
    },
    onLoad() {
        this._super();
        this.node.on('UPDATE_HIGHLIGHT', this.updateHighLight, this);
    },
    updateHighLight(isEnable) {
        const spriteFrame = isEnable ? this.selectedFrame : this.normalFrame;
        this.frameBG.spriteFrame = spriteFrame;
    }

});
