cc.Class({
    extends: cc.Component,

    properties: {
        delayTime: 3,
        bufferTime: 5,
    },

    onLoad() {
        this.anim = this.node.getComponent(cc.Animation);
        this.anim.play();
        this.node.runAction(cc.repeatForever(
            cc.sequence(
                cc.delayTime(this.delayTime + this.bufferTime),
                cc.callFunc(() => {
                    this.anim.play();
                }),
            )
        ));
    },
});