const { convertAssetArrayToObject } = require('utils');

cc.Class({
    extends: require('SlotSoundPlayer'),

    properties: {
        bgmFree: {
            default: null,
            type: cc.AudioClip,
        },
        bgmBattle: {
            default: null,
            type: cc.AudioClip,
        },
        listSfx: {
            default: [],
            type: cc.AudioClip,
        },
    },

    onLoad() {
        this._super();
        this.soundObjects = convertAssetArrayToObject(this.listSfx);
        if (!this.webSound) {
            this.listSfx.forEach(sound => {
                const id = cc.audioEngine.play(sound, false, 0);
                cc.audioEngine.stopEffect(id);
            });
        }
    },
    playMainBGM(currentGameMode) {
        const mode = currentGameMode || this.node.gSlotDataStore.currentGameMode;
        let soundBG = this.bgmMain;
        if (mode == 'freeGame') soundBG = this.bgmFree;
        if (mode == 'battle') soundBG = this.bgmBattle;
        soundBG = soundBG || this.bgmMain;
        this.playMusic(soundBG, true, this.MUSIC_VOLUME);
    },

    playSfxTurboClick() {
        this.playSfxByID('9881_btn_turbo');
    },
    playSFXTrialButton() {
        this.playSfxByID('9881_btn_general');
    },

    playSfxByID(id) {
        if (!this.soundObjects[id]) return;
        const audioClip = this.soundObjects[id];
        this.soundObjects[id]._playingId = this.playSFX(audioClip);
    },
    playSoundByID(id, isLoop = false) {
        if (!this.soundObjects[id]) return;
        const audioClip = this.soundObjects[id];
        this.soundObjects[id]._playingId && this.stopSound(this.soundObjects[id]._playingId);
        this.soundObjects[id]._playingId = this.playSound(audioClip, isLoop);
    },
    stopSoundByID(id) {
        if (!this.soundObjects[id]) return;
        this.soundObjects[id]._playingId && this.stopSound(this.soundObjects[id]._playingId);
        this.soundObjects[id]._playingId = null;
    },
    playSfxCancel() {
        this.playSfxByID('9881_btn_cancel');
    }
});