
cc.Class({
    extends: cc.Component,

    properties: {
        lineHolder: cc.Node,
        dotHolder: cc.Node,
        dotPrefab: cc.Prefab,
        totalRow: 3,
        totalCol: 5,
        lineSprites: [cc.SpriteFrame]
    },
    onLoad() {
        this.node.on("CLEAR_DRAWING_LINE", this.clear, this);
        this.node.on("DRAWING_LINE_AT", this.addLine, this);
        this.node.on("DRAWING_FIRST_LINE", this.addFirstLine, this);
        this.node.on("DRAWING_LAST_LINE", this.addLastLine, this);
        this.node.on("DISABLE_ALL_LINE", this.disableAllLine, this);
        this.node.on("SHOW_DOT", this.showDot, this);

        const tableFormat = this.node.config.TABLE_FORMAT;
        this._startXPos = -(tableFormat.length / 2) * this.node.config.SYMBOL_WIDTH;
        this._startYPos = -(tableFormat[0] / 2) * this.node.config.SYMBOL_HEIGHT;

        this.cachedLine = [];
        this.cachedFirstLine = [];
        this.cachedLastLine = [];
        this._usingObj = [];
        this._initDots();
    },
    _initDots() {
        this.dots = [];
        const { SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const startX = -2 * SYMBOL_WIDTH;
        const startY = SYMBOL_HEIGHT;
        for (let col = 0; col < 5; col++) {
            for (let row = 0; row < 3; row++) {
                const dot = cc.instantiate(this.dotPrefab);
                this.dots.push(dot);
                dot.parent = this.dotHolder;
                dot.x = startX + col * SYMBOL_WIDTH;
                dot.y = startY - row * SYMBOL_HEIGHT;
                dot.active = false;
            }
        }
    },
    showDot(col, row) {
        const index = col * 3 + row;
        const dot = this.dots[index];
        dot.active = true;
    },
    addFirstLine(row) {
        if (this.cachedFirstLine[row]) {
            this.cachedFirstLine[row].active = true;
            return;
        }
        const line = this.createLine();
        if (!line) return;

        const { SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const posX = (SYMBOL_WIDTH / 2) + this._startXPos;
        const posY = (this._startYPos + SYMBOL_HEIGHT / 2 + (this.totalRow - 1 - row) * SYMBOL_HEIGHT);
        line.getComponent(cc.Sprite).spriteFrame = this.lineSprites[0];
        line.anchorX = 1;
        line.anchorY = 0.5;
        line.setScale(row === 1 ? 0.49 : 0.55, 1);
        line.setPosition(posX, posY);
    },
    addLastLine(row) {
        if (this.cachedLastLine[row]) {
            this.cachedLastLine[row].active = true;
            return;
        }
        const line = this.createLine();
        if (!line) return;

        const { SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const posX = this._startXPos + (SYMBOL_WIDTH / 2) + (this.totalCol - 1) * (SYMBOL_WIDTH);
        const posY = (this._startYPos + SYMBOL_HEIGHT / 2 + (this.totalRow - 1 - row) * SYMBOL_HEIGHT);
        line.getComponent(cc.Sprite).spriteFrame = this.lineSprites[0];
        line.anchorX = 0;
        line.anchorY = 0.5;
        line.setScale(row === 1 ? 0.49 : 0.55, 1)
        line.setPosition(posX, posY);
        this.cachedLastLine[row] = line;
    },
    addLine(type, col, row) {
        if (!this.cachedLine[col]) {
            this.cachedLine[col] = [];
        }
        if (!this.cachedLine[col][row]) {
            this.cachedLine[col][row] = [];
        } else if (this.cachedLine[col][row][type]) {
            this.cachedLine[col][row][type].active = true;
            return;
        }
        const line = this.createLine();
        if (!line) return;
        const { SYMBOL_WIDTH, SYMBOL_HEIGHT } = this.node.config;
        const posX = this._startXPos + SYMBOL_WIDTH / 2 + col * (SYMBOL_WIDTH);
        const posY = (this._startYPos + SYMBOL_HEIGHT / 2 + (this.totalRow - 1 - row) * SYMBOL_HEIGHT);
        let offsetY = 0; 
        if (type == 0) {
            line.getComponent(cc.Sprite).spriteFrame = this.lineSprites[0];
            line.anchorX = 0;
            line.anchorY = 0.5;
            line.setPosition(posX, posY);
        }
        if (type == 1 || type == 2) {
            // 1: cheo len 1 o
            // 2: cheo xuong 1 o 
            line.getComponent(cc.Sprite).spriteFrame = this.lineSprites[1];
            if (type == 1) {
                line.anchorX = 0;
                line.anchorY = 0;
                line.setScale(1,1);
                offsetY = -2;
            }else{
                line.anchorX = 1;
                line.anchorY = 1;
                line.setScale(-1,1);
                offsetY = 2;
            }
            line.setPosition(posX, posY + offsetY);
        }
        if (type == 3 || type == 4) {
            // 3: cheo len 2 o
            // 4: cheo xuong 2 o
            line.getComponent(cc.Sprite).spriteFrame = this.lineSprites[2];
            line.anchorX = type == 3 ? 0 : 1;
            line.anchorY = type == 3 ? 0 : 1;
            line.setScale(type == 3 ? 1.005 : -1.005, 1.005);
            offsetY = type == 3 ? -2 : 2;
            line.setPosition(posX, posY + offsetY);
        }
        this.cachedLine[col][row][type] = line;
    },

    disableAllLine() {
        for (let i = 0; i < this._usingObj.length; i++) {
            const line = this._usingObj[i];
            line.active = false;
        }
        this.dots.forEach(dot => dot.active = false);
    },
    clear() {
        this.clearLine();
        this.cachedLine = [];
        this.cachedFirstLine = [];
        this.cachedLastLine = [];
        this.cachedDotLine = [];
        this.dots.forEach(dot => dot.active = false);
    },
    createLine() {
        if (!this.node.poolFactory) return null;
        let line = this.node.poolFactory.getObject("PayLineLine9881");
        line.parent = this.lineHolder;
        line.active = true;
        this._usingObj.push(line);
        return line;
    },
    clearLine() {
        for (let i = 0; i < this._usingObj.length; i++) {
            const removedObj = this._usingObj[i];
            removedObj.anchorX = 0.5;
            removedObj.anchorY = 0.5;
            removedObj.setPosition(0, 0);
            removedObj.setScale(1, 1)
            this.node.poolFactory.removeObject(removedObj);
        }
        this._usingObj = [];
        this.lineHolder.remove
    },
});
