cc.Class({
    extends: require('SlotSymbol'),

    properties: {
        dimColor: cc.Color,
    },

    changeToSymbol(symbolName, canShowTrail = false) {
        this._super(symbolName);
        this.staticSymbol.active = true;
        if (symbolName.includes("K") && canShowTrail) {
            const changeToSymbolEvent = new cc.Event.EventCustom('ON_CHANGE_SYMBOL_TO_WILD', true);
            changeToSymbolEvent.setUserData({ symbolName, node: this.node });
            this.node.dispatchEvent(changeToSymbolEvent);
        }
    },
    changeToBlurSymbol(symbolName, canShowTrail = false) {
        const blurSymbolName = "Blur_" + symbolName;
        if (this.blurAssets[blurSymbolName]) {
            this.node.symbol = symbolName;
            this.staticSymbol.opacity = 255;
            this.staticSymbol.getComponent(cc.Sprite).spriteFrame = this.blurAssets[blurSymbolName];
        } else {
            this.changeToSymbol(symbolName, canShowTrail);
        }
    },

    blinkHighlight() { },
    disableHighlight() {
        this.tweenColor(this.staticSymbol, this.dimColor);
    },
    enableHighlight() {
        this.tweenColor(this.staticSymbol, cc.Color.WHITE);
    },
    reset() {
        this.staticSymbol.tweenColor && this.staticSymbol.tweenColor.stop();
        this.staticSymbol.tweenColor = null;

        this.staticSymbol.stopAllActions();
        this.tweenColor(this.staticSymbol, cc.Color.WHITE, 0.2);

        // this.staticSymbol.color = cc.Color.WHITE;
        this.bigSymbol = false;
        this.staticSymbol.opacity = 255;
        this.node.opacity = 255;
    },
    tweenColor(node, finalColor, time = 0.2) {
        node.tweenColor && node.tweenColor.stop();
        node.tweenColor = null;
        node.tweenColor = cc.tween(node)
            .to(time, { color: finalColor })
            .call(() => {
                node.tweenColor = null;
            })
            .start();
    },
});