const { convertSlotMatrixTBLR } = require("utils");
cc.Class({
    extends: require('SlotTableHistory'),

    properties: {
        bigWildLeft: cc.Node,
        bigWildRight: cc.Node,
        labelRefix: cc.Node,
        stickyWild: cc.Node,
        battle: cc.Node,
        dimColor: cc.Color,
    },

    renderResult(data) {
        const { paylines, betDenom, mode, matrixFormat, matrixResult } = data;
        this.normalGame.active = false;
        this.freeGame.active = false;
        if (this.bonusGame) this.bonusGame.active = false;
        if (this.topupGame) this.topupGame.active = false;
        this.clearTable();
        this.betDenom = betDenom;
        this.currentMode = mode;
        this.martrixFormat = matrixFormat;
        const payLines = this.node.gSlotDataStore.convertPayLine(paylines);
        switch (mode) {
            case "normal":
                this.normalGame.active = true;
                this.renderGameTable(matrixResult, matrixFormat, payLines);
                this.renderExtendData(data);
                break;
            case "free":
                this.freeGame.active = true;
                this.renderGameTable(matrixResult, matrixFormat, payLines);
                this.renderExtendData(data);
                this._showBattale(data)
                this.stickyWild.active = true;
                break;
        }
    },

    renderGameTable(matrix, format, payLines) {
        let startX = (-format.length / 2 + 0.5) * this.node.config.SYMBOL_WIDTH;
        let count = 0;
        this.symbolMatrix = [];
        for (let i = 0; i < format.length; i++) {
            let startY = (format[i] / 2 - 0.5) * this.node.config.SYMBOL_HEIGHT;
            this.symbolMatrix[i] = [];
            for (let j = 0; j < format[i]; j++) {
                let symbol = this.getSymbol(this.currentMode);
                symbol.parent = this.symbolNode;
                symbol.active = true;
                symbol.setPosition(startX + i * this.node.config.SYMBOL_WIDTH, startY - j * this.node.config.SYMBOL_HEIGHT);
                symbol.changeToSymbol(matrix[count]);
                symbol.col = i;
                symbol.row = j;
                symbol.val = matrix[count];
                symbol.emit("ENABLE_HIGHLIGHT");
                this.symbolMatrix[i][j] = symbol;
                count++;
            }
        }
        this.showPayLines(payLines);
        this._showBigWild(this.symbolMatrix, startX);
    },
    showPayLines(payLines) {
        if (!payLines || !payLines.length) return;
        this.symbolNode.children.forEach(symbol => { symbol.emit("DISABLE_HIGHLIGHT") });
        let maxPayLineWinNumbers = 0;
        this.dimColorNode(this.bigWildLeft, true);
        this.dimColorNode(this.bigWildRight, true);
        for (let i = 0; i < payLines.length; ++i) {
            const { payLineID, payLineWinNumbers } = payLines[i]
            const payLine = this.node.config.PAY_LINE_MATRIX[payLineID];
            if (this.drawingLineTable) this.drawPayLine(payLine);
            for (let payLinePos = 0; payLinePos < payLineWinNumbers; ++payLinePos) {
                const row = payLine[payLinePos];
                const col = payLinePos;
                const symbol = this.symbolMatrix[col][row];
                if (symbol && symbol.active) {
                    symbol.emit("ENABLE_HIGHLIGHT");
                }
            }
            if (payLineWinNumbers && payLineWinNumbers > maxPayLineWinNumbers) maxPayLineWinNumbers = payLineWinNumbers;
        }
        this.dimColorNode(this.bigWildLeft, false);
        if ((this.currentMode == "normal" && maxPayLineWinNumbers > 3) || (this.currentMode == "free" && maxPayLineWinNumbers > 2)) {
            this.dimColorNode(this.bigWildRight, false);
        }
    },

    _showBigWild(matrix, startX) {
        matrix.forEach((rowMatrix, col) => {
            let count = 0;
            rowMatrix.forEach((symbol) => {
                if (symbol.symbol.includes('K')) {
                    count++;
                }
            })
            if (count == 3) {
                const posX = startX + this.node.config.SYMBOL_WIDTH * col;
                if (col < 2) {
                    this.bigWildLeft.active = true;
                    this.bigWildLeft.x = posX;
                }
                if (col > 2) {
                    this.bigWildRight.active = true;
                    this.bigWildRight.x = posX;
                }
                this._disableSymbolAtCol(matrix, col);
            }
        });
    },
    _disableSymbolAtCol(matrix, col) {
        matrix[col].forEach((symbol) => {
            symbol.active = false;
        })
    },

    _showBattale(data) {
        const { mTf } = data.result.metaDataDetail;
        if (!mTf || mTf <= 1) return;
        this.bigWildRight.active = false;
        this.bigWildLeft.active = false;
        this.stickyWild.active = false;
        this.battle.active = true;
        this.battle.emit("SHOW_BATTLE_DATA", data);
    },

    clearTable() {
        this.bigWildLeft.active = false;
        this.bigWildRight.active = false;
        this.stickyWild.active = false;
        this.battle.emit("RESET");
        this.battle.active = false;
        this.dimColorNode(this.bigWildLeft, false);
        this.dimColorNode(this.bigWildRight, false);
        let pool = this.symbolPool;
        this.showingPayline = false;
        while (this.symbolNode.children.length > 0) {
            pool.put(this.symbolNode.children[0]);
        }
    },
    formatMatrix(matrix, tableFormat) {
        return convertSlotMatrixTBLR(matrix, tableFormat);
    },
    dimColorNode(node, isDim = true) {
        node.color = isDim ? this.dimColor : cc.Color.WHITE;
    }
});
