
cc.Class({
    extends: cc.Component,

    properties: {
        targetNode: cc.Node,
        btnStop: cc.Node,
        direction: 1,
        isRotate: true
    },
    onLoad() {
        if (!this.targetNode) return;

        this.targetNode.on(cc.Node.EventType.MOUSE_ENTER, this.onHover, this, true);
        this.targetNode.on(cc.Node.EventType.MOUSE_LEAVE, this.onMouseOut, this, true);
        this.targetNode.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this, true);
        this.targetNode.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this, true);
        
        this.targetNode.on("ON_MOUSE_LEAVE", this.showIcon, this);
        this.targetNode.on("BUTTON_SPIN_SHOW", this.showIcon, this);
        this.targetNode.on("BUTTON_SPIN_HIDE", this.hideIcon, this);
    },
    start() {
        this.rotateIcon();
    },
    onTouchStart() {
        this.hideIcon();
    },
    onTouchEnd() {
        this.showIcon();
    },
    onHover() {
        this.rotateIcon(2);
    },
    onMouseOut() {
        this.showIcon();
        this.rotateIcon(1);
    },
    showIcon() {
        if (this._canShowRotateIcon()) this.node.active = true;
    },
    hideIcon() {
        this.node.active = false;
    },
    rotateIcon(speed = 1) {
        if (!this.isRotate) return;
        this.tweenRotate && this.tweenRotate.stop();
        this.tweenRotate = cc.tween(this.node)
            .by(2 / speed, { angle: -360 * this.direction })
            .repeatForever()
            .start();
    },
    onEnable() {
        this.rotateIcon();
    },
    onDestroy() {
        this.tweenRotate ** this.tweenRotate.stop();
        this.tweenRotate = null;
    },
    _canShowRotateIcon() {
        return this.targetNode.active && this.targetNode.getComponent(cc.Button).interactable == true;
    }

});
