const LIGHT_STATE = {
    IDLE: 1,
    WIN: 2
};
cc.Class({
    extends: require('SlotTablev2'),

    properties: {
        lighBG: sp.Skeleton,
    },

    onLoad() {
        this._super();
        this.node.on("SET_UP_EURO_BANNER", this.setUpEuroBanner, this);
        this.node.on("SET_UP_BIG_WILD", this.setUpBigWild, this);
        this.node.on("SET_UP_SURE_WIN", this.setUpSureWin, this);
        this.node.on("SCALE_SUB_SYMBOL", this.scaleSubSymbol, this);
        this.node.on("OPEN_ALL_BANNERS", this.openAllBanners, this);
        this.node.on("PLAY_SPOT_LIGHT_IDLE", this.playSpotLightIdle, this);
        this.node.on("PLAY_SPOT_LIGHT_WIN", this.playSpotLightWin, this);
        this.node.on("SHOW_INTRO_JACKPOT", this.showIntroJackpot, this);
        this.node.on("SHOW_SUB_SYMBOL_PAYLINE", this.showSubSymbolPayline, this);
        this.lightState = LIGHT_STATE.IDLE;
        this.listSubSymbols = [];
    },

    init() {
        this._super();
        if (this.node.poolFactory) {
            this.node.reels.forEach(reel => {
                reel.initPoolFactory(this.node.poolFactory);
            });
        }
    },

    changeMatrix({matrix, rowOffset = 0}) {
        this.matrix = matrix;
        for (let col = 0; col < this.tableFormat.length; ++col) {
            const reel  = this.node.reels[col];
            for (let row = 0; row < this.tableFormat[col]; ++row) {
                if(reel){
                    const symbol = reel.showSymbols[row+rowOffset];
                    if(symbol) {
                        symbol.opacity = 255;
                        symbol.changeToSymbol(this.matrix[col][row]);
                    }
                }
            }
        }
    },

    startSpinning() {
        this.isShowingBigWild = false;
        this.isSureWin = false;
        this._super();
    },

    openAllBanners() {
        this.node.reels.forEach(reel => {
            reel.playAnimOpenBanner();
        });
    },

    setUpSureWin() {
        this.isSureWin = true;
        this.node.emit('PLAY_LIGHT_NEAR_WIN');
        this.node.emit('PLAY_SURE_WIN_EFFECT');
        this.node.reels.forEach(reel => {
            reel.adjustReelSpeed(this.node.config.SUPER_TURBO);
        });
    },

    setUpEuroBanner(format) {
        if (format && format.length > 0) {
            for (let col = 0; col < format.length; ++col) {
                this.node.reels[col].setUpEuroBanner(format[col]);
            }
        }
    },

    setUpBigWild() {
        this.isShowingBigWild = true;
    },

    scaleSubSymbol() {
        this.node.reels.forEach(reel => {
            reel.scaleSubSymbol();
        });
    },

    stopSpinning(data, callback) {
        const { matrix } = data;
        data.isShowingBigWild = this.isShowingBigWild;
        data.isSureWin = this.isSureWin;
        this.matrix = matrix;
        this.node.bigSymbols = [];
        this.stopSpinningCallbackCount = 0;

        for (let col = 0; col < this.tableFormat.length; ++col) {
            let matrix = [...this.matrix[col]].reverse();
            this.node.reels[col].stopSpinningWithDelay(col, matrix, data, this.checkStopSpinningCallback.bind(this, matrix, callback));
        }
        
        if (this.table) {
            this.table.bigSymbols = this.node.bigSymbols;
        }

        this.node.emit('SET_UP_SOUND_STOP', matrix);
        this.node.emit('REEL_ABOUT_TO_STOP_NEARWIN', {reels: this.node.reels, data, context: this,});
        this.node.emit('REEL_ABOUT_TO_STOP_SOUND', {reels: this.node.reels, data, context: this,});
        this.node.emit('REEL_ABOUT_TO_STOP_EFFECT', {reels: this.node.reels, data, context: this,});
        this.node.emit('REEL_ABOUT_TO_STOP_MISC', {reels: this.node.reels, data, context: this,});
    },

    stopReeWithRandomMatrix(callback) {
        this.matrixRandom = [];
        for (let col = 0; col < this.tableFormat.length; ++col) {
            this.matrixRandom[col] = [];
            for (let row = this.tableFormat[col] - 1; row >=0 ; --row) {
                const reel = this.node.reels[col];
                if(reel && reel.getRandomSymbolNameWithExceptions){
                    this.matrixRandom[col][row] = reel.getRandomSymbolNameWithExceptions('K');
                } else{
                    this.matrixRandom[col][row] = "3";
                }
            }
        }
        this.stopSpinning({ matrix: this.matrixRandom}, () => {
            callback && callback();
        });
    },

    gameExit() {
        this.node.reels.forEach(reel => {
            reel.resetBanner();
        });
    },

    playAnimLight(updatedState) {
        if (this.lightState === updatedState) return;
        this.lightState = updatedState;

        if (this.lightState === LIGHT_STATE.IDLE) {
            this.lighBG.addAnimation(0, 'MainG', true);
        } else if (this.node.gSlotDataStore.isAutoSpin) {
            this.lighBG.setAnimation(0, 'MainG_Win', false);
            this.lighBG.addAnimation(0, 'MainG', true);
        } else {
            this.lighBG.setAnimation(0, 'MainG_Win', true);
        }
    },

    playSpotLightIdle() {
        this.playAnimLight(LIGHT_STATE.IDLE);
    },
    playSpotLightWin() {
        this.playAnimLight(LIGHT_STATE.WIN);
    },

    getXPosition(index) {
        let startX = -(this.node.config.TABLE_FORMAT.length / 2 - 0.5) * this.node.config.SYMBOL_WIDTH;
        return (startX + this.node.config.SYMBOL_WIDTH * index);
    },

    showIntroJackpot(subSymbol = [], callback) {
        this.listSubSymbols = [];
        this.scheduleOnce(() => {
            subSymbol.forEach(index => {
                const col = Math.floor(index / 3);
                const row = index % 3;
                const poolFactory = this.node.poolFactory;
                const subSymbol = poolFactory.getObject('SubSymbolPrefab');
                subSymbol.active = true;
                subSymbol.scale = 1;
                subSymbol.x = this.getXPosition(col);
                subSymbol.y = ((3/2 - row - 0.5)) * this.node.config.SYMBOL_HEIGHT;
                subSymbol.parent = this.table;
                this.listSubSymbols.push(subSymbol);
            });
            this.listSubSymbols.forEach((item, index) => {
                this.playAnimSubSymbol(item, index === 0 ? callback : null);
            });
            for (let col = 0; col < this.tableFormat.length; ++col) {
                const reel  = this.node.reels[col];
                for (let row = 0; row < this.tableFormat[col]; ++row) {
                    if(reel){
                        const symbol = reel.showSymbols[row];
                        if(symbol) symbol.hideSubSymbol();
                    }
                }
            }
        }, 1);
    },

    playAnimSubSymbol(node, callback) {
        node.runAction(cc.sequence(
            cc.moveTo(0.4, cc.v2(0, -30)),
            cc.callFunc(() => {
                callback && callback();
            }),
            cc.delayTime(0.45),
            cc.callFunc(() => {
                this.table.removeChild(node);
            })
        ));
    },

    showSubSymbolPayline() {
        for (let col = 0; col < this.tableFormat.length; ++col) {
            const reel  = this.node.reels[col];
            for (let row = 0; row < this.tableFormat[col]; ++row) {
                if(reel){
                    const symbol = reel.showSymbols[row];
                    if(symbol) symbol.showSubSymbol();
                }
            }
        }
    },
});