
cc.Class({
    extends: cc.Component,
    properties: {
        bigWildsNode: cc.Node,
        battleNode: cc.Node,
        multiNodes: [cc.Node]
    },

    onEnable() {
        const breakTime = 1.5;
        const fadeTime = 0.5;
        const scaleTime = 0.3;
        const ease = cc.easeSineInOut();
        const totalMultiNode = this.multiNodes.length;
        cc.tween(this.node)
            .call(() => {
                this.battleNode.opacity = 0;
                this.multiNodes.forEach(node => node.opacity = 0);
                cc.tween(this.bigWildsNode)
                    .to(fadeTime, { opacity: 255 })
                    .to(scaleTime, { scale: 1.1 })
                    .to(scaleTime, { scale: 1 })
                    .start();
            })
            .delay(0)
            .call(() => {
                this.bigWildsNode.runAction(cc.fadeOut(0).easing(ease));
                this.battleNode.runAction(cc.fadeIn(0).easing(ease));
            })
            .delay(0)
            .call(() => {
                this.multiNodes.forEach((node, index) => {
                    const tween = cc.tween(node)
                        .delay(fadeTime * index)
                        .to(fadeTime, { opacity: 255 })
                        .to(scaleTime, { scale: 1.1 })
                        .to(scaleTime, { scale: 1 })
                    if (index === totalMultiNode - 1) {
                        tween.to(scaleTime, { scale: 1.1 }).to(scaleTime, { scale: 1 });
                    }
                    tween.start();
                })
            })
            .delay(fadeTime * totalMultiNode + scaleTime * 2)
            .delay(breakTime)
            .call(() => {
                this.battleNode.runAction(cc.fadeOut(fadeTime).easing(ease));
                this.bigWildsNode.runAction(cc.fadeIn(fadeTime).easing(ease));
            })
            .delay(fadeTime * 2)
            .union().repeatForever().start();
    },
    onDisable() {
        this.node.stopAllActions();
        this.bigWildsNode.stopAllActions();
        this.bigWildsNode.opacity = 255;
        this.battleNode.stopAllActions();
        this.battleNode.opacity = 0;
        this.multiNodes.forEach(node => {
            node.stopAllActions();
            node.scale = 1;
            node.opacity = 0;
        });
    },


});
