
cc.Class({
    extends: require('MainDirector9881'),

    properties: {
        totalBattle: cc.Node,
        stickyWild: cc.Node,
    },

    ready({ matrix, isResume, freeGameRemain }) {
        this.node.mainDirector.onIngameEvent("ENTER_GAME_MODE");
        this.node.soundPlayer && this.node.soundPlayer.playMainBGM('freeGame');
        this._updateSpinTimes([], isResume && freeGameRemain ? freeGameRemain : 10);
        if (matrix) {
            this.table.emit("CHANGE_MATRIX", { matrix });
            this.table.emit("CLEAR_PAYLINES");
        }
        if (isResume && this.stickyWild) {
            this.stickyWild.emit("PLAY_ANIM_RESUME")
        }
        this.scheduleOnce(() => {
            this.buttons.emit('SPIN_SHOW');
            this.buttons.emit('SPIN_ENABLE');
            this.buttons.emit('STOP_AUTO_SPIN_HIDE');
            this.runAction('SpinByTimes', 999999);
        }, 1.5);
    },
    _showTotalBattle(script, data) {
        this.totalBattle.active = true;
        this.totalBattle.emit('SHOW', data)
        this.executeNextScript(script);
    },
    _hideTotalBattle(script, data) {
        this.totalBattle.emit('FINISH', data, () => {
            this.executeNextScript(script);
        })
    },
    forceToExit(callback) {
        this._callbackForceExit = () => {
            callback && callback();
            this._callbackForceExit = null;
        };
        this.forceToExitMode = true;
        this.exitScript = [{ command: "_gameReset" },];
        if (this.isStopRunning) {
            this.executeNextScript(this.exitScript);
        } else {
            this.fastToResultClick();
        }
    },
    _gameReset() {
        this.unscheduleAllCallbacks();
        this._clearDelayTimeScript();
        this.skipAllEffects();
        this.stopAutoSpinClick();
        this._updateSpinTimes([], 0);
        this.node.resetCallbackWhenHide();
        this.table.emit("CLEAR_PAYLINES");
        this._callbackForceExit();
        this.fsm.reboot();
        this.fsm.gameStart();
        this.table.emit("FORCE_RESET_TABLE");
        this.forceToExitMode = false;
        this.node.exit();
    },
    fastToResultClick() {
        this._super();
        // const { winAmountPS } = this.node.gSlotDataStore.playSession;
        // this.winAmount.emit("FAST_UPDATE_WIN_AMOUNT", { value: winAmountPS, time: 0 });
        // this.winAmount.emit("HIDE_ANIM_WIN");
    }
});
