cc.Class({
    extends: require('SlotSymbolPaylinev2'),

    properties: {
        dimColor: cc.Color,
    },

    onLoad() {
        this.node.mainComponent = this;
        this.node.changeToSymbol = this.changeToSymbol.bind(this);
        this.node.playAnimation = this.playAnimation.bind(this);
        this.node.stopAnimation = this.stopAnimation.bind(this);
        this.node.blinkHighlight = this.blinkHighlight.bind(this);
        this.node.enableHighlight = this.enableHighlight.bind(this);
        this.node.disableHighlight = this.disableHighlight.bind(this);
        this.node.playAnimGoalIdle = this.playAnimGoalIdle.bind(this);
        this.node.playAnimNearWin = this.playAnimNearWin.bind(this);
        this.node.reset = this.reset.bind(this);
        
        this.node.on("CHANGE_TO_SYMBOL",this.changeToSymbol,this);
        this.node.on("PLAY_ANIMATION",this.playAnimation,this);
        this.node.on("STOP_ANIMATION",this.stopAnimation,this);
        this.node.on("BLINK_HIGHLIGHT",this.blinkHighlight,this);
        this.node.on("ENABLE_HIGHLIGHT",this.enableHighlight,this);
        this.node.on("DISABLE_HIGHLIGHT",this.disableHighlight,this);
        this.node.on("RESET",this.reset,this);
        this._animation = this.spineNode.getComponent(sp.Skeleton);
    },

    changeToSymbol(symbolName) {
        this.spineNode.active = false;
        this._havingAnim = false;
        this._isPlaying = false;
        this.node.symbol = symbolName;
        this._symbolName = symbolName;
        this.symbolAnim = this.findSymbolSpineData(symbolName);
        if (this.symbolAnim) {
            this._animation.skeletonData = this.symbolAnim.spine;
            this._havingAnim = true;
        }
        this.node.isSymbolAnimated = this._havingAnim;
    },

    playAnimation(isNormalPayline = false) {
        if (this._havingAnim && !this._isPlaying) {
            this._isPlaying = true;
            this.spineNode.active = true;
            if (this._symbolName === 'K') {
                if (isNormalPayline) {
                    this._animation.setAnimation(0, "Idle", true);
                } else {
                    this._animation.setAnimation(0, "Shoot", false);
                    this._animation.addAnimation(0, "Idle_Goal", true);
                }
            } else {
                this._animation.setAnimation(0, "animation", true);
            }
        }
    },

    playAnimGoalIdle() {
        if (this._havingAnim && !this._isPlaying) {
            this._isPlaying = true;
            this.spineNode.active = true;
            this._animation.setAnimation(0, "Idle_Goal", true);
        }
    },

    playAnimNearWin() {
        if (this._havingAnim) {
            this.spineNode.active = true;
            this._animation.setAnimation(0, "Idle", true);
        }
    },

    stopAnimation() {
        this.spineNode.active = false;
    },

    enableHighlight(){
        this.spineNode.color =  cc.Color.WHITE;
    },

    disableHighlight(){
        if(!this._isPlaying) {
            this.spineNode.active = false;
        } else {
            this.spineNode.color = this.dimColor;
        }
    },

    blinkHighlight() {

    },

    reset(){
        this.spineNode.color =  cc.Color.WHITE;
        this._isPlaying = false;
        this.spineNode.active = false;
    }
});