const LIGHT_STATE = {
    IDLE: 1,
    WIN: 2
};

cc.Class({
    extends: require('SlotGameDirector'),

    properties: {
        lighBG: sp.Skeleton,
    },

    ready(matrix) {
        const { freeGameRemain, freeGameTotal } = this.node.gSlotDataStore.playSession;
        this.buttons.active = true;
        if (matrix) {
            this.table.emit("CLEAR_PAYLINES");
            this.table.emit("CHANGE_MATRIX",{matrix});
        }
        if (freeGameRemain === freeGameTotal) {
            this.table.emit('INTRO_WILD');
        }
        // this.runAction('GameStart');
        //NEED TO CHECK WHEN RESUME??? OR RENAME IT TO INIT SETUP
        this.buttons.emit('SPIN_SHOW');
        this.buttons.emit('SPIN_ENABLE');
        this.buttons.emit('STOP_AUTO_SPIN_HIDE');

        //Turn on when enter
        const spinTimes = freeGameRemain;
        this.spinTimes.emit("UPDATE_SPINTIMES", spinTimes);
        this.spinTimes.active = true;

        this.scheduleOnce(() => {
            this.runAction('SpinByTimes', spinTimes);
        }, 0.5);
        this.node.mainDirector.onIngameEvent("ENTER_GAME_MODE");
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playMainBGM('freeGame');
        }
        this.lighBG.setAnimation(0, 'FreeG', true);
    },

    _showResult(script) {
        this.table.emit('STOP_SOUND_BIG_WILD');
        this._super(script);
    },

    _showNormalPayline(script) {
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        this.table.emit("SHOW_ALL_NORMAL_PAYLINES", ()=>{});
        this.executeNextScript(script);
    },

    _clearPaylines(script) {
        const { freeGameRemain, freeGameTotal } = this.node.gSlotDataStore.playSession;
        if (freeGameRemain === freeGameTotal) {
            this.table.emit("RESET_WILD");
        }
        this._super(script);
    },

    _spinClick(script) {
        this.table.emit("RESET_BIG_WILD");
        this.playAnimLight(LIGHT_STATE.IDLE);
        this._super(script);
    },

    _runAutoSpin(script) {
        const { payLines } = this.node.gSlotDataStore.playSession;
        if (!this.isAlwaysAutoSpin /*normal*/ && this.node.gSlotDataStore.isAutoSpin) {
            this.buttons.emit('STOP_AUTO_SPIN_SHOW');
        }
        this.node.mainDirector.gui.emit("HIDE_INTRO");
        this.table.emit("HIDE_ANIM_INTRO");
        this.skipAllEffects();
        this.buttons.emit('SPIN_DISABLE');
        this._showTrialButtons(null, false);
        this._callBackAutoSpin = () => {
            this.runAction('SpinClick');
            this.executeNextScript(script);
            this._callBackAutoSpin = null;
        };

        const havingPayline = payLines && payLines.length > 0;
        let delayTime = havingPayline ? 1 : 0.5;
        this.scheduleOnce(this._callBackAutoSpin, delayTime);
    },
    
    fastToResultClick() {
        this.table.emit("SKIP_ALL_EFFECT");
        this._super();
    },

    _gameExit(script) {
        this.buttons.active = false;
        this.table.emit("RESET_BIG_WILD");
        this._super(script);
    },

    _setupEuroBanner(script, format) {
        this.table.emit('SET_UP_EURO_BANNER', format);
        this.executeNextScript(script);
    },

    _showBigWild(script, data) {
        this.table.emit("SET_UP_BIG_WILD");
        this.table.emit("SHOW_BIG_WILD", data);
        this.executeNextScript(script);
    },

    _playAnimGirls(script) {
        this.node.mainDirector.gui.emit('PLAY_GIRL_DANCE');
        this.executeNextScript(script);
    },

    _openAllBanners(script) {
        this.table.emit('OPEN_ALL_BANNERS');
        this.openBannerCallback = () => {
            this.openBannerCallback = null;
            this.executeNextScript(script);
        };
        this.scheduleOnce(this.openBannerCallback, 0.5);
    },

    forceToExit(script) {
        this._super(script);
        this.fastToResultClick();
        if (this.openBannerCallback) {
            this.unschedule(this.openBannerCallback);
            this.openBannerCallback();
        }
    },

    _updateWinningAmount(script, { winAmount, time, isFTR, rate }) {
        if (!isFTR) this.playSoundWinLine(rate);
        this.winAmount.emit("UPDATE_WIN_AMOUNT",{value: winAmount, time});
        this.executeNextScript(script);
    },

    playSoundWinLine(rate) {
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playSoundWinLine(rate);
        }
    },

    _playLightWin(script) {
        this.playAnimLight(LIGHT_STATE.WIN);
        this.executeNextScript(script);
    },

    playAnimLight(updatedState) {
        if (this.lightState === updatedState) return;
        this.lightState = updatedState;

        if (this.lightState === LIGHT_STATE.IDLE) {
            this.lighBG.addAnimation(0, 'FreeG', true);
        } else  {
            this.lighBG.setAnimation(0, 'FreeG_Win', false);
            this.lighBG.addAnimation(0, 'FreeG', true);
        }
    },
});
