const { formatMoney } = require('utils');

cc.Class({
    extends: require('JackpotMultiply'),

    properties: {
        animWin: 'Win',
        jackpotMultiplySprites: [cc.SpriteFrame],
        jackpotMultiplyNode: cc.Node,
        star: cc.Node,
    },

    playAnimMultiply(multiply) {
        if (this.frameAnimation && !this.isRunningAppearAnim && !this.frameAnimation.opacity) {
            if (this.tweenFrame) this.tweenFrame.stop();
            this.frameAnimation.opacity = 255;
            this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animAppear, false);
            this.tweenFrame = cc.tween(this.frameAnimation)
                .delay(0.5)
                .call(() => {
                    this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animIde, true);
                })
                .start();

            if (this.jackpotUIAnims && this.jackpotUIAnims.length) {
                this.jackpotUIAnims.forEach((item) => {
                    cc.tween(item)
                        .to(0.5, { opacity: 255 })
                        .start();
                });

            }

        }

        if (!this.isRunningAppearAnim) this.isRunningAppearAnim = true;
        this.isRunningDisappearAnim = false;
    },

    playJackpotExplosion(data, callback) {
        if(!this.node.config.IS_SHOW_JACKPOT_EXPLOSION) return;
        let { jpId, amt, dn, lv} = data[0];
        let prefixLength = this.node.config.JP_PREFIX_EVENT.length;
        let jpIndex = jpId.charAt(prefixLength);
        let jpType = jpId.slice(prefixLength + 2);
        let jpAmount = 1 * amt;
        const { steps, currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        const betIndex = findKeyByValue(steps, currentBetData).toString();

        if (jpIndex === betIndex) {
            this._callbackNotiJp = callback;
            this.updateValueJP(jpAmount, 0);
            this.pauseRenderJP();
            if (lv && lv >= 1) {
                const jpLevel = jpIndex + this.transformJPName(jpType);
                this.currentJackpotData[jpLevel] = parseInt(jpAmount);
                this.showAnimNoticeWinJP(jpAmount, dn, lv, jpType);
            }
        }
    },

    stopAnimMultiply() {
        if (this.isRunningDisappearAnim) return;

        if (this.frameAnimation) {
            if (this.tweenFrame) this.tweenFrame.stop();
            this.frameAnimation.opacity = 0;
            this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animIde, true);

            if (this.jackpotUIAnims && this.jackpotUIAnims.length) {
                this.jackpotUIAnims.forEach((item) => {
                    cc.tween(item)
                        .to(0.5, { opacity: 0 })
                        .start();
                });
            }
        }

        this.isRunningAppearAnim = false;
        this.isRunningDisappearAnim = true;
    },

    showAnimNoticeWinJP(jpAmount, dn = '', lv = 1, jpType) {
        if (jpType === 'GRAND') {
            this.star.active = lv > 1;
            this.jackpotMultiplyNode.getComponent(cc.Sprite).spriteFrame = this.jackpotMultiplySprites[0];
        } else {
            this.star.active = false;
            this.jackpotMultiplyNode.getComponent(cc.Sprite).spriteFrame = this.jackpotMultiplySprites[1];
        }
        if (!this.jackpotNotice || !this.jackpotWinUsername || !this.jackpotWinAmount) return;
        if (this.jackpotWinUsername) this.jackpotWinUsername.getComponent(cc.Label).string = dn;
        if (this.jackpotWinAmount) this.jackpotWinAmount.getComponent(cc.Label).string = formatMoney(jpAmount);
        if (this.jackpotLabelHolder && this.jackpotLabelMask) {
            if (this.tweenjackpotNotice) this.jackpotNoticeTween.stop();
            if (this.tweenJackPotLabelHolder) this.tweenJackPotLabelHolder.stop();

            this.startX = 0;
            this.jackpotNoticeTween = cc.tween(this.jackpotNotice)
                .to(0.5, { opacity: 255})
                .call(() => {
                    if (this.frameAnimation && jpType === 'GRAND') {
                        this.tweenFrame = cc.tween(this.frameAnimation)
                            .call(() => {
                                this.frameAnimation.opacity = lv > 1 ? 255 : 0;
                                this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animWin, true);
                            })
                            .delay(2)
                            .call(() => {
                                this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animIde, true);
                            })
                            .start();
                    }

                    const labelHolderWidth = this.jackpotLabelHolder.children.reduce((acc, obj) => { return acc + obj.width; }, 0) + (5 * (this.jackpotLabelHolder.children.length - 1));
                    if (this.jackpotLabelMask.width < this.jackpotLabelHolder.width) {
                        this.startX = this.jackpotLabelHolder.x - (labelHolderWidth - this.jackpotLabelMask.width);
                        this.startX = parseInt(this.startX);
                    }
                    this.tweenJackPotLabelHolder = cc.tween(this.jackpotLabelHolder)
                        .to(2.5, { position: cc.v2(this.startX, this.jackpotLabelHolder.y) })
                        .to(3.5, { position: cc.v2(0, this.jackpotLabelHolder.y) })
                        .start();
                })
                .start();
        }
    },

    resetAnimNotice() {
        if (this.tweenFrame) this.tweenFrame.stop();
        if (this.tweenjackpotNotice) this.jackpotNoticeTween.stop();
        if (this.tweenJackPotLabelHolder) this.tweenJackPotLabelHolder.stop();

        if (this.jackpotNotice) {
            cc.tween(this.jackpotNotice)
                .to(0.5, { opacity: 0})
                .delay(0.5)
                .call(() => {
                    if (this.jackpotLabelHolder) this.jackpotLabelHolder.x = 0;
                })
                .start();
        }

        if (this.frameAnimation) {
            this.frameAnimation.getComponent(sp.Skeleton).setAnimation(0, this.animIde, true);
        }

        this.isRunningAppearAnim = false;
        this.isRunningDisappearAnim = false;
    },

});
