cc.Class({
    extends: require('SlotButtonV2'),
    properties: {
        promotionNode: cc.Node
    },
    onTouchMove(event){
        let touch  = event.touch;
        let hit = this.spinBtn._hitTest(touch.getLocation());
        if(hit){
            if(!this._isTouched){
                this.node.emit('ON_TOUCH_START');
                this._isTouched = true;
            }
        }else{
            this.cancelAutoSpinPanel();
            if (this.selectionAutoSpin) return;
            this.node.emit('ON_TOUCH_CANCEL');
            this.spinBtn.emit("ON_MOUSE_LEAVE");
            this._isTouched = false;
        }
    },

    showAllPromotionButton() {
        this._super();
        this.promotionNode.active = true;
    },
    hideAllPromotionButton() {
        this._super();
        this.promotionNode.active = false;
    },
    onMouseLeave() {
        this.spinBtn.emit("ON_MOUSE_LEAVE");
        this._super();
    },
});