function getAnimationsName(spine) {
    return Object.keys(spine.skeletonData._skeletonJson.animations);
}
function getSkinsName(spine) {
    return Object.keys(spine.skeletonData._skeletonJson.skins);
}

cc.Class({
    extends: cc.Component,

    properties: {
        panel: cc.Node,
        isShowAddAnim: false,
        labelTime: cc.Label
    },

    onLoad() {
        this._spines = this.node.getComponentsInChildren(sp.Skeleton);
        this._spines.forEach(spine => {
            spine.loop = false;
            spine.node.on(cc.Node.EventType.MOUSE_DOWN, () => {
                this.focus(spine);
            });
        });
    },
    focus(spine) {
        cc.log("%c SPINE", "color: red;", spine);
        this._isLoop = false;
        this.panel.removeAllChildren();
        this.addButtonReset(spine);
        this._creatButton(`__ setAnimation __`, cc.Color.ORANGE);
        this.showAnimListToSet(spine);
        this.showSkin(spine);
        if (this.isShowAddAnim) {
            this._creatButton(`__ addAnimation __`, cc.Color.ORANGE);
            this.showAnimListToAdd(spine);
        }
    },
    addButtonReset(spine) {
        const buttonOn = this._creatButton("ACTIVE");
        buttonOn.color = cc.Color.RED;
        buttonOn.on(cc.Node.EventType.MOUSE_DOWN, () => {
            spine.node.active = true;
        });

        const buttonOff = this._creatButton("DEACTIVATE");
        buttonOff.color = cc.Color.GRAY;
        buttonOff.on(cc.Node.EventType.MOUSE_DOWN, () => {
            spine.node.active = false;
        });

        const buttonLoop = this._creatButton(this._isLoop ? "UNLOOP" : "LOOP");
        buttonLoop.color = cc.Color.BLUE;
        buttonLoop.on(cc.Node.EventType.MOUSE_DOWN, () => {
            this._isLoop = !this._isLoop;
            buttonLoop.getComponent(cc.Label).string = this._isLoop ? "UNLOOP" : "LOOP";
            const anim = spine.animation;
            spine.setAnimation(0, anim, this._isLoop);
        });
    },
    showAnimListToSet(spine) {
        const animations = getAnimationsName(spine);
        for (let i = 0; i < animations.length; i++) {
            const animName = animations[i];
            const btn = this._creatButton(animName);
            btn.on(cc.Node.EventType.MOUSE_DOWN, () => {
                let currentTime = Date.now();
                spine.setAnimation(0, animName, this._isLoop);
                spine.setCompleteListener(() => {
                    cc.log("%c anims queue: " + spine._animationQueue.length, "color: red;", JSON.stringify(spine._animationQueue, null, '\t'));
                    cc.log('duration', (Date.now() - currentTime));
                });
                const duration = spine.findAnimation(animName).duration;
                this.labelTime.string = 'Time: ' + duration;
            });
        }
    },
    showSkin(spine) {
        const skins = getSkinsName(spine);
        cc.log("%c skins: ", "color:red", skins);
        if (!skins || skins.length === 0) return;
        this._creatButton(`__ setSkin __`, cc.Color.ORANGE);
        for (let i = 0; i < skins.length; i++) {
            const skinName = skins[i];
            const btn = this._creatButton(skinName);
            btn.on(cc.Node.EventType.MOUSE_DOWN, () => {
                spine.setSkin(skinName);
            });
        }
    },
    showAnimListToAdd(spine) {
        const animations = getAnimationsName(spine);
        for (let i = 0; i < animations.length; i++) {
            const animName = animations[i];
            const btn = this._creatButton(animName);
            btn.on(cc.Node.EventType.MOUSE_DOWN, () => {
                if (this._isLoop) {
                    alert("can not add animation when spine is looping");
                }
                spine.addAnimation(0, animName, false);
                cc.log("animation queue: ", spine._animationQueue);
            });
        }
    },
    _creatButton(buttonName = "", color = cc.Color.BLACK) {
        const btn = new cc.Node();
        btn.name = "btn " + buttonName;
        btn.color = color;
        this.panel.addChild(btn);
        const label = btn.addComponent(cc.Label);
        label.string = buttonName;
        return btn;
    }
});
