cc.Class({
    extends: require('SlotGameDirector'),

    properties: {
        lighBG: sp.Skeleton,
    },

    ready(matrix) {
        this.lighBG.setAnimation(0, 'MainG', true);
        this._super(matrix);
    },

    switchToTrial() {
        this._resetGameMode();
        this._super();
    },
    switchToReal(){
        this._resetGameMode();
        this._super();
    },

    _resetGameMode() {
        this.skipAllEffects();
        if (this.openBannerCallback) {
            this.unschedule(this.openBannerCallback);
            this.openBannerCallback();
        }
        this.winAmount.emit("FADE_OUT_NUMBER", 0.05);
    },

    _spinClick(script) {
        this._super(script);
        this.table.emit('PLAY_SPOT_LIGHT_IDLE');
    },

    extendInit(){
        this.listScriptAsync = [];
    },

    skipAllEffects() {
        this.resetAsyncScript();
        this._super();
    },

    _setupEuroBanner(script, format) {
        this.table.emit('SET_UP_EURO_BANNER', format);
        this.executeNextScript(script);
    },

    _showResult(script) {
        this.table.emit('PLAY_ANIM_STOP_SURE_WIN');
        this.table.emit('PLAY_LIGHT_IDLE');
        this._super(script);
    },

    _resumeGameMode(script, {name, data}) {
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playMainBGM('normalGame');
        }
        this._super(script, {name, data});
    },

    _showWildPayLine(script) {
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playSfxWinWild();
        }
        this.table.emit('PLAY_SPOT_LIGHT_WIN');
        this.table.emit("SHOW_WILD_PAYLINE",() => {
            this.executeNextScript(script);
        });
    },

    _blinkAllPaylines(script) {
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        if (this.canStoreAsyncScript()) {
            const callback = () => { 
                this.table.emit("BLINK_ALL_NORMAL_PAYLINES", this.runAsyncScript.bind(this));
            };
            this.storeAsyncScript(script, {callback, name: "_blinkAllPaylines", isSkippable: true});
        } else {
            this.table.emit("BLINK_ALL_NORMAL_PAYLINES", this.executeNextScript.bind(this, script));
        }
    },

    _blinkAllPaylineSync(script) {
        this.table.emit("BLINK_ALL_NORMAL_PAYLINES", this.executeNextScript.bind(this, script));
    },

    _blinkAllPaylineSync_2(script) {
        this.executeNextScript(script);
    },

    _showNormalPayline(script) {
        if (!this.hasPayline) {
            this.executeNextScript(script);
            return;
        }
        if (this.canStoreAsyncScript()) {
            const callback = () => { 
                this.node.mainDirector.onIngameEvent("SHOW_NORMAL_PAYLINE");
                this.table.emit("SHOW_ALL_NORMAL_PAYLINES");
                this.runAsyncScript();
            };
            this.storeAsyncScript(script, {callback, name: "_showNormalPayline", isSkippable: true});
        } else {
            this.table.emit("SHOW_ALL_NORMAL_PAYLINES");
            this.executeNextScript(script);
        }
    },

    _updateTrialWallet(script, data) {
        const { state } = this.node.gSlotDataStore.playSession;
        if (this.canStoreAsyncScript() && state !== 2) {
            const callback = () => { 
                if (this.node.mainDirector) {
                    this.node.mainDirector.updateTrialWallet(data);
                    this.runAsyncScript();
                }
            };
            this.storeAsyncScript(script, {callback, name: "_updateTrialWallet", isSkippable: false});
        } else {
            this._super(script, data);
        }
    },

    _updateWinningAmount(script, { winAmount, time, isFTR, rate }) {
        if (this.canStoreAsyncScript()) {
            const callback = () => {
                if (!isFTR) this.playSoundWinLine(rate);
                this.winAmount.emit("UPDATE_WIN_AMOUNT",{value: winAmount, time});
                this.runAsyncScript();
            };
            this.storeAsyncScript(script, { callback, name: "_updateWinningAmount", isSkippable: false });
        } else {
            if (!isFTR) this.playSoundWinLine(rate);
            this.winAmount.emit("UPDATE_WIN_AMOUNT",{value: winAmount, time});
            this.executeNextScript(script);
        }
    },

    playSoundWinLine(rate) {
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playSoundWinLine(rate);
        }
    },

    _clearWinAmount(script) {
        this.winAmount.emit("FADE_OUT_NUMBER", this.node.gSlotDataStore.modeTurbo ? 0.4 : 0.8);
        this.executeNextScript(script);
    },

    _clearWinAmount_2(script) {
        this.winAmount.emit("FADE_OUT_NUMBER", 0.2);
        this.executeNextScript(script);
    },

    _pauseWallet(script) {
        if (this.canStoreAsyncScript()) {
            this.node.gSlotDataStore.isUpdateWinAmount = true;
        }
        this.executeNextScript(script);
    },

    _resumeWallet(script) {
        if (this.canStoreAsyncScript()) {
            const callback = () => {
                this.node.gSlotDataStore.isUpdateWinAmount = false;
                this.runAsyncScript();
            };
            this.storeAsyncScript(script, { callback, name: "_resumeWallet", isSkippable: false });
        } else {
            this.node.gSlotDataStore.isUpdateWinAmount = false;
            this.executeNextScript(script);
        }
    },

    canStoreAsyncScript() {
        const { isFinished } = this.node.gSlotDataStore.playSession;
        const { isAutoSpin } = this.node.gSlotDataStore;
        const isValid = isFinished === true && !isAutoSpin;
        return isValid;
    },

    storeAsyncScript(script, data) {
        this.listScriptAsync.push(data);
        this.executeNextScript(script);
    },

    _runAsyncScript(script) {
        this.executeNextScript(script);
        this.runAsyncScript();
    },

    runAsyncScript(){
        if (this.isResetAsyncScript) return;
        const command = this.listScriptAsync.shift();
        if (command) {
            const {callback, name} = command;
            if (name) cc.log(this.name +' run AsyncScript: ', name);
            callback && callback();
        }
    },

    resetAsyncScript() {
        if (!this.listScriptAsync || !this.listScriptAsync.length) return;
        this.isResetAsyncScript = true;
        while (this.listScriptAsync.length > 0) {
            const command = this.listScriptAsync.shift();
            if (command) {
                const {callback, isSkippable, name} = command;
                if (!isSkippable) {
                    if (name) cc.log(this.name +' run resetAsyncScript: ', name);
                    callback && callback(true);
                }
            }
        }
        this.isResetAsyncScript = false;
    },

    _updateValueJP(script, data) {
        this.node.mainDirector.jackpot.emit('UPDATE_VALUE_JACKPOT', data.jpValue, data.jpType);
        this.executeNextScript(script);
    },

    _playAnimSureWin(script) {
        this.table.emit('PLAY_SPOT_LIGHT_WIN');
        this.table.emit('SET_UP_SURE_WIN');
        this.executeNextScript(script);
    },

    _playAnimGirls(script) {
        this.node.mainDirector.gui.emit('PLAY_GIRL_DANCE');
        this.executeNextScript(script);
    },

    _scaleSubSymbol(script) {
        this.table.emit('SCALE_SUB_SYMBOL');
        this.executeNextScript(script);
    },

    fastToResultClick() {
        this._super();
        this.resetAsyncScript();
    },

    _showCutscene(script, { name, content }) {
        if (this.canStoreAsyncScript() && name !== 'DialogMessage') {
            const callback = () => {
                if (this.node.mainDirector) {
                    this.node.mainDirector.showCutscene(name, content, () => {
                        this.runAsyncScript();
                    });
                } else {
                    cc.error('There is no main Director to play cutscenes');
                    this.runAsyncScript();
                }
            };
            this.storeAsyncScript(script, { callback, name: "_showCutscene", isSkippable: true });
        } else {
            this._super(script, { name, content });
        }
    },
    
    _openAllBanners(script) {
        if (this.canStoreAsyncScript()) {
            const callback = (isSkip) => {
                const {winAmountPS} = this.node.gSlotDataStore.playSession;
                if (isSkip && winAmountPS > 0) {
                    this.winAmount.emit("UPDATE_WIN_AMOUNT",{value: winAmountPS, time: 10});
                }
                this.table.emit('OPEN_ALL_BANNERS');
                this.openBannerCallback = () => {
                    this.openBannerCallback = null;
                    this.runAsyncScript();
                };
                this.scheduleOnce(this.openBannerCallback, 0.5);
            };
            this.storeAsyncScript(script, { callback, name: "_openAllBanners", isSkippable: false });
        } else {
            this.table.emit('OPEN_ALL_BANNERS');
            this.openBannerCallback = () => {
                this.openBannerCallback = null;
                this.executeNextScript(script);
            };
            this.scheduleOnce(this.openBannerCallback, 0.5);
        }
    },

    _openAllBannersSync(script) {
        this.table.emit('OPEN_ALL_BANNERS');
        this.openBannerCallback = () => {
            this.openBannerCallback = null;
            this.executeNextScript(script);
        };
        this.scheduleOnce(this.openBannerCallback, 0.5);
    },

    _showIntroFreeGame(script, {name,content}) {
        this.node.mainDirector.onIngameEvent("ON_FINISH_WILD_PAYLINE");
        if (this._checkPauseTutorial('pauseIntroFreeGame')) {
            this.storeCurrentScripts = '_showUnskippedCutscene';
            this.storeNextScripts = {
                script,
                data: {name,content}
            };
            return;
        } else {
            if (this.node.mainDirector) {
                this.node.mainDirector.showCutscene(name,content,() => {
                    this.executeNextScript(script);
                });
            }
        }
    },

    _runAutoSpin(script) {
        const { payLines } = this.node.gSlotDataStore.playSession;
        if (!this.isAlwaysAutoSpin /*normal*/ && this.node.gSlotDataStore.isAutoSpin) {
            this.buttons.emit('STOP_AUTO_SPIN_SHOW');
        }
        this.node.mainDirector.gui.emit("HIDE_INTRO");
        this.table.emit("HIDE_ANIM_INTRO");
        this.skipAllEffects();
        this.buttons.emit('SPIN_DISABLE');
        this._showTrialButtons(null, false);
        this._callBackAutoSpin = () => {
            this.runAction('SpinClick');
            this.executeNextScript(script);
            this._callBackAutoSpin = null;
        };

        const havingPayline = payLines && payLines.length > 0;
        let delayTime = havingPayline ? 1 : 0.5;
        if(this.node.gSlotDataStore.firstAutoSpin){
            this.node.gSlotDataStore.firstAutoSpin = false;
            delayTime = 1;
        }
        this.scheduleOnce(this._callBackAutoSpin, delayTime);
    },

    _showIntroFreeGame_2(script) {
        this.executeNextScript(script);
    },

    forceToExit(script) {
        this._super(script);
        this.fastToResultClick();
    },

    _playLightWin(script) {
        this.table.emit('PLAY_SPOT_LIGHT_WIN');
        this.executeNextScript(script);
    },

    _showIntroJackpot(script, subSymbol) {
        this.node.mainDirector.onIngameEvent("ON_FINISH_INTRO_JACKPOT");
        if (this._checkPauseTutorial('pauseIntroJackpot')) {
            this.storeCurrentScripts = '_showIntroJackpot';
            this.storeNextScripts = {
                script,
                data: subSymbol
            };
            return;
        } else {
            this.table.emit('SHOW_INTRO_JACKPOT', subSymbol, () => {
                this.executeNextScript(script);
            });
        }
    },

    _showAllSubSymbol(script) {
        this.table.emit('SHOW_SUB_SYMBOL_PAYLINE');
        this.executeNextScript(script);
    },

    runPromotionSpin() {
        this._super();
        this.spinTimes.opacity = 255;
    },
});