cc.Class({
    extends: require('SlotSymbol'),

    properties: {
        dimColor: cc.Color,
    },

    changeToSymbol(symbolName) {
        this._super(symbolName);
        this.staticSymbol.active = true;
    },
    blinkHighlight() { },
    disableHighlight() {
        this.staticSymbol.color = this.dimColor;
    },
    enableHighlight() {
        this.staticSymbol.color = cc.Color.WHITE;
    },
    reset() {
        this.staticSymbol.stopAllActions();
        this.staticSymbol.color = cc.Color.WHITE;
        this.bigSymbol = false;
        this.node.opacity = 255;
    },
});