const serviceRest = require('serviceRest');
const globalNetwork = require('globalNetwork');
const loadConfigAsync = require('loadConfigAsync');
const {setDeviceOrientation} = require('utils');
const { ccclass, property } = cc._decorator;

@ccclass
export class Login extends cc.Component {

    @property(cc.EditBox)
    tokenInput: cc.EditBox = null;

    @property(cc.EditBox)
    userInput: cc.EditBox = null;

    @property(cc.EditBox)
    passwordInput: cc.EditBox = null;

    @property({ type: cc.Label })
    errorMsg: cc.Label = null;

    @property({ type: cc.Label })
    version: cc.Label = null;

    @property({ type: cc.JsonAsset })
    versionFile: cc.JsonAsset = null;

    @property({ type: cc.Toggle })
    toggleStaging: cc.Toggle = null;

    @property({ type: cc.Toggle })
    toggleExStaging: cc.Toggle = null;

    @property({ type: cc.Toggle })
    toggleAccount: cc.Toggle = null;

    @property({ type: cc.Toggle })
    toggleLoadTest: cc.Toggle = null;

    @property({ type: cc.Toggle })
    toggleLoadTest_2: cc.Toggle = null;

    @property({ type: cc.Node })
    passwordHolder = null;

    @property({ type: cc.Node })
    tokenHolder = null;

    @property({ type: cc.Button })
    btnGetToken: cc.Button = null;

    @property({ type: cc.Prefab })
    stateMonitorPrefab: cc.Prefab = null;

    @property({ type: cc.Prefab })
    stateMonitorPrefabPortrait: cc.Prefab = null;

    envInfo = {
        stg: {
            userId: '',
            token: '',
        },
        exstg: {
            userId: '',
            token: '',
        },
        loadtest: {
            userId: '',
            token: '',
        },
        loadtest2: {
            userId: '',
            token: '',
        },
        account: {
            userId: '',
            password: '',
        },
        current: 'stg'
    };

    onLoad() {
        // console.log('Loaded scene login v341');
        this.getConfigRemote();
        this.version.string = this.versionFile.json['v'];
        if (cc.sys.isNative) {
            let versionFilePath = jsb.fileUtils.getWritablePath() + 'eno-hotupdate/' + 'project.manifest';
            if (jsb.fileUtils.isFileExist(versionFilePath)) {
                cc.log('Found hotupdate, version path ' + versionFilePath);
                let str = jsb.fileUtils.getStringFromFile(versionFilePath);
                if (str) {
                    let obj = JSON.parse(str);
                    this.version.string = obj.version;
                    cc.log('Update lobby version ' + obj.version);
                }
                else {
                    cc.log('Cant get string from version path');
                }
            }
        }

        if (this.stateMonitorPrefab) {
            let monitorPrefab = this.stateMonitorPrefab;
            const screenWidth = window ? window.innerWidth : cc.winSize.width;
            const screenHeight = window ? window.innerHeight : cc.winSize.height;
            if (screenWidth < screenHeight) {
                monitorPrefab = this.stateMonitorPrefabPortrait;
            }
            let monitor = cc.instantiate(monitorPrefab);
            cc.game.addPersistRootNode(monitor);
        }
        else {
            cc.log("No StateMonitor");
        }
    }

    protected start(): void {
        if (cc.sys.isNative) {
            setDeviceOrientation(false);
        }
    }

    getConfigRemote() {
        const { IS_FINISHED_REMOTE } = loadConfigAsync.getConfig();
        if (!IS_FINISHED_REMOTE) {
            setTimeout(() => {
                this.getConfigRemote();
            }, 100);
            return;
        }
        let cachedInfo = cc.sys.localStorage.getItem('envInfo');
        if (cachedInfo) {
            this.envInfo = JSON.parse(cachedInfo);
        }

        if (this.envInfo && this.envInfo.current == "stg") {
            this.toggleStaging.isChecked = true;
            this.switchStaging();
        }
        else if (this.envInfo && this.envInfo.current == "exstg") {
            this.toggleExStaging.isChecked = true;
            this.switchExStaging();
        }
        else if (this.envInfo && this.envInfo.current == "loadtest") {
            this.toggleLoadTest.isChecked = true;
            this.switchLoadTest();
        }
        else if (this.envInfo && this.envInfo.current == "loadtest2") {
            this.toggleLoadTest_2.isChecked = true;
            this.switchLoadTest2();
        }
        else {
            this.toggleAccount.isChecked = true;
            this.switchAccount();
        }
    }

    replaceConfig(config: any) {
        const dataUpdate = loadConfigAsync.getConfig();
        Object.keys(config).forEach(it => {
            dataUpdate[it] = config[it];
        });
    }

    switchStaging() {
        if (!this.toggleStaging.isChecked) return;
        this.envInfo.current = 'stg';
        let config = {
            API_URL: "https://api.staging.enostd.gay/",
            NETWORK_V3: true,
            USER_TOKEN: "user_token",
            SOCKET_URL: "wss://sock.staging.enostd.gay",
            LOBBY_SCENE_NAME: "Lobby",
            LOGIN_SCENE_NAME: "Login",
            IPMaster12: "wss://staging.fish.enostd.gay/lobby-1985/",
        }
        this.btnGetToken.interactable = true;
        this.tokenHolder.active = true;
        this.passwordHolder.active = false;
        this.replaceConfig(config);
        cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
        this.loadUserInfo();
    }

    switchExStaging() {
        if (!this.toggleExStaging.isChecked) return;
        this.envInfo.current = 'ex-stg';
        let config = {
            API_URL: "https://api.ex-staging.enostd.gay/",
            NETWORK_V3: true,
            USER_TOKEN: "user_token",
            SOCKET_URL: "wss://sock.ex-staging.enostd.gay",
            LOBBY_SCENE_NAME: "Lobby",
            LOGIN_SCENE_NAME: "Login",
            IPMaster12: "wss://staging.fish.enostd.gay/lobby-1985/",
        }
        this.btnGetToken.interactable = true;
        this.tokenHolder.active = true;
        this.passwordHolder.active = false;
        this.replaceConfig(config);
        cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
        this.loadUserInfo();
    }

    switchAccount() {
        if (!this.toggleAccount.isChecked) return;
        this.envInfo.current = 'account';
        let config = {
            API_URL: "https://api.staging.enostd.gay/",
            NETWORK_V3: true,
            USER_TOKEN: "user_token",
            SOCKET_URL: "wss://sock.staging.enostd.gay",
            LOBBY_SCENE_NAME: "Lobby",
            LOGIN_SCENE_NAME: "Login",
            IPMaster12: "wss://staging.fish.enostd.gay/lobby-1985/",
        }
        this.btnGetToken.interactable = false;
        this.tokenHolder.active = false;
        this.passwordHolder.active = true;
        this.replaceConfig(config);
        cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
        this.loadUserInfo();
    }

    switchLoadTest(){
        if (!this.toggleLoadTest.isChecked) return;
        this.envInfo.current = 'loadtest';
        let config = {
            API_URL: "https://api.loadtest.enostd.gay/",
            NETWORK_V3: true,
            USER_TOKEN: "user_token",
            SOCKET_URL: "wss://sock.loadtest.enostd.gay",
            LOBBY_SCENE_NAME: "Lobby",
            LOGIN_SCENE_NAME: "Login",
            IPMaster12: "wss://staging.fish.enostd.gay/lobby-1985/",
        }
        this.btnGetToken.interactable = true;
        this.tokenHolder.active = true;
        this.passwordHolder.active = false;
        this.replaceConfig(config);
        cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
        this.loadUserInfo();
    }

    switchLoadTest2(){
        if (!this.toggleLoadTest_2.isChecked) return;
        this.envInfo.current = 'loadtest2';
        let config = {
            API_URL: "https://api2.loadtest.enostd.gay/",
            NETWORK_V3: true,
            USER_TOKEN: "user_token",
            SOCKET_URL: "wss://sock2.loadtest.enostd.gay",
            LOBBY_SCENE_NAME: "Lobby",
            LOGIN_SCENE_NAME: "Login",
            IPMaster12: "wss://staging.fish.enostd.gay/lobby-1985/",
        }
        this.btnGetToken.interactable = true;
        this.tokenHolder.active = true;
        this.passwordHolder.active = false;
        this.replaceConfig(config);
        cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
        this.loadUserInfo();
    }

    loadUserInfo() {
        let currentEnv = this.envInfo.current;
        let userInfo = this.envInfo[currentEnv] || {};

        if (!userInfo) return;

        if (userInfo.token)
            this.tokenInput.string = userInfo.token;
        else {
            this.tokenInput.string = '';
        }
        if (userInfo.password)
            this.passwordInput.string = userInfo.password;
        else {
            this.passwordInput.string = '';
        }
        this.userInput.string = userInfo.userId || '';
    }

    getToken() {
        this.errorMsg.string = "";
        let userName = this.userInput.string.replace(/xxxxx_|game_|tek_/g, '');
        let url = 'internal-support-tool/token/generate/' + userName;
        serviceRest.post({
            url, params: null,
            callback: (data) => {
                if (data.status == 200 && data.data && this.tokenInput) {
                    this.tokenInput.string = data.data.data.token;
                    this.errorMsg.string = "";
                } else {
                    this.errorMsg.string = "Khng th to Token";
                }
            },
            callbackErr: () => {
                this.errorMsg.string = "Khng th to Token";
            }
        })
    }

    onLoginPress() {
        if (this.envInfo.current == 'account') {
            this.loginAccount();
        }
        else {
            this.loginToken();
        }
    }

    loginAccount() {
        this.errorMsg.string = "";
        const { LOBBY_SCENE_NAME, USER_TOKEN, API_URL } = loadConfigAsync.getConfig();
        const dataPost = {
            username: this.userInput.string,
            password: this.passwordInput.string
        };

        serviceRest.post({
            apiUrl: API_URL,
            url: 'client2-tool/login',
            data: dataPost,
            callback: (res) => {
                const { data: { data, code } } = res;
                if (code === 200 && data) {
                    this.errorMsg.string = "";
                    const { token, refreshToken } = data;
                    if (!this.envInfo[this.envInfo.current])
                        this.envInfo[this.envInfo.current] = {};
                    this.envInfo[this.envInfo.current].userId = this.userInput.string;
                    this.envInfo[this.envInfo.current].password = this.passwordInput.string;
                    cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
                    globalNetwork.init(token, refreshToken);
                    cc.sys.localStorage.setItem(USER_TOKEN, token);
                    cc.director.loadScene(LOBBY_SCENE_NAME);
                } else {
                    this.errorMsg.string = "Khng th ng nhp";
                }
            },
            callbackErr: () => {
                this.errorMsg.string = "Khng th ng nhp";
            }
        });
    }

    loginToken() {
        this.errorMsg.string = "";
        const { LOBBY_SCENE_NAME, USER_TOKEN, API_URL } = loadConfigAsync.getConfig();
        const userToken = this.tokenInput.string;
        if (!userToken) return;
        const dataPost = {
            token: userToken,
        };
        // console.log('Trying login user token ' + userToken);
        serviceRest.post({
            url: 'auth/token/login', data: dataPost, callback: (res) => {
                // console.log('login res ' + JSON.stringify(res));
                const { data: { data, error } } = res;
                if (data) {
                    const { token, userId } = data;
                    if (token && token != "") {
                        if( !this.envInfo[this.envInfo.current] ){
                            this.envInfo[this.envInfo.current] = {
                                token: '',
                                userId: ''
                            }
                        }
                        this.envInfo[this.envInfo.current].token = token;
                        this.envInfo[this.envInfo.current].userId = userId.replace(/xxxxx_|tek_/g, ''),
                            cc.sys.localStorage.setItem('envInfo', JSON.stringify(this.envInfo));
                        cc.sys.localStorage.setItem(USER_TOKEN, token);
                        cc.director.loadScene(LOBBY_SCENE_NAME);
                        this.errorMsg.string = "";
                        loadConfigAsync.setToken(token);
                        globalNetwork.init(token, null, 'all', 'all');
                    }
                    else {
                        this.errorMsg.string = "Khng th ng nhp";
                    }
                } else if (error || res.data.errors) {
                    switch (res.data.errors[0]) {
                        case "ERROR_TOKEN_EXPRIED":
                            this.errorMsg.string = "Token  expired";
                            break;
                        default:
                            this.errorMsg.string = "Khng th ng nhp";
                            break;
                    }
                }
            }
        })
    }

}

