cc.Class({
    extends: require('CutsceneMode'),

    properties: {
        introContent: sp.Skeleton,
        transitionAnim: sp.Skeleton,
        spinTimes: cc.Node,
    },

    enter() {
        const { isResume } = this.content;
        this.transitionAnim.node.active = false;
        this.introContent.node.active = false;
        this.spinTimes.active = false;
        if (!isResume) {
            this.showFreeGamePopUp();
        } else {
            this.runAnimTransition();
        }
    },

    showFreeGamePopUp() {
        if (this.node.soundPlayer) {
            this.node.soundPlayer.playTransitionFree();
        }
        this.introContent.node.active = true;
        this.introContent.timeScale = 1;
        this.introContent.setAnimation(0, 'Begin', false);
        this.introContent.addAnimation(0, 'Idle', true);
        this.spinTimes.opacity = 0;
        this.spinTimes.scale = 1;
        this.node.runAction(cc.sequence(
            cc.delayTime(0.6),
            cc.callFunc(() => {
                this.spinTimes.active = true;
                this.spinTimes.runAction(cc.fadeIn(0.4));
            }),
            cc.delayTime(1.5),
            cc.callFunc(() => {
                this.runAnimTransition(true);
            })
        ));
    },

    runAnimTransition() {
        this.transitionAnim.node.active = true;
        this.transitionAnim.setAnimation(0, 'animation', false);
        this.transitionAnim.setCompleteListener(() => {
            this.transitionAnim.node.active = false;
            this.node.active = false;
            this.transitionAnim.setCompleteListener(() => {});
        });
        this.node.runAction(cc.sequence(
            cc.delayTime(0.66),
            cc.callFunc(() => {
                this.introContent.node.active = false;
                this.spinTimes.active = false;
                this.exit();
            })
        ));
    },

    exit() {
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopTransitionFree();
        }
        if (this.callback && typeof this.callback == "function") {
            if(this.node.mainDirector){
                this.node.mainDirector.onIngameEvent("ON_CUTSCENE_CLOSE", this.node.name);
            }
            this.node.emit("STOP");
            this.callback();
        }
    }
});
