const {formatMoney} = require('utils');
const {reverseEasing} = require('globalAnimationLibrary');

cc.Class({
    extends: require('WinEffectv2'),

    properties: {
        spineBigWin: cc.Node,
        fastEffect: cc.Node,
        normalEffect: cc.Node,
    },

    onLoad() {
        this._super();
        this.bigWinSkeleton = this.spineBigWin.getComponent(sp.Skeleton);
        this.spineBigWin.active = false;
    },

    showFastEffectWin() {  // turbo
        if (this.isShowFastEffect) {
            this.callback && this.callback();
            this.callback = null;
            return;
        }
        this.isShowFastEffect = true;
        this.winInfo.opacity = 0;
        if (this.overlayNode) {
            this.overlayNode.active = false;
        }
        const listParticle = this.fastEffect.getComponentsInChildren(cc.ParticleSystem);
        listParticle.forEach(item => {
            item.resetSystem();
        });
        this.spineBigWin.active = false;
        this.fastEffect.opacity = 0;
        this.fastEffect.stopAllActions();
        this.fastEffect.runAction(cc.sequence(
            cc.delayTime(0.1),
            cc.fadeIn(0.1),
        ));
        this.callback && this.callback();
        this.callback = null;
        if( this._fastEffectAction){
            this.node.stopAction(this._fastEffectAction);
        }
        this._fastEffectAction = cc.sequence(
            cc.delayTime(1),
            cc.callFunc(()=>{
                listParticle.forEach(item => {
                    item.stopSystem();
                });
            }),
            cc.delayTime(2),
            cc.callFunc(()=>{
                this.exit();
            }),
        );
        this.node.runAction(this._fastEffectAction);
    },

    showEffectWin() {
        this.isShowNormalEffect = true;
        if(this.overlayNode){
            this.overlayNode.active = true;
        }
        this.playSoundStart();
        this.winInfo.opacity = 255;
        this.spineBigWin.active = true;
        this.bigWinSkeleton.setSkin('Thanglon');
        this.bigWinSkeleton.setSlotsToSetupPose();
        this.bigWinSkeleton.setAnimation(0, 'Appear', false);
        this.bigWinSkeleton.addAnimation(0, 'Idle', true);
        this.scheduleOnce(() => {
            const scaleTime = 0.2;
            this.winAmount.stopAllActions();
            this.winAmount.runAction(cc.repeatForever(cc.sequence(
                cc.scaleTo(scaleTime, 1.2),
                cc.scaleTo(scaleTime, 1),
            )));
            this.initValue();
            this.startParticle();
            this.startUpdateWinAmount();
        }, 1);
        this.label.string = '';
    },

    initValue() {
        this.winInfo.scale = 1;
        this.currentValue = 0;
        this.currentTitle = 0;
        this.label.string = '';
        this.megaWinAmount = this.content.currentBetData * this.megaRate;
        this.superWinAmount = this.content.currentBetData * this.superRate;
        this.isUpdating = true;
        this.speedUp = false;
        this.bindQuickShow();
    },

    changeTitle(index) {
        this.currentTitle = index;
        this.bigWinSkeleton.setSkin(index === 1 ? 'Thangcuclon' : 'Thangsieulon');
        this.bigWinSkeleton.setSlotsToSetupPose();
    },

    startParticle() {
        const listParticle = this.normalEffect.getComponentsInChildren(cc.ParticleSystem);
        listParticle.forEach(item => {
            item.resetSystem();
        });
    },

    stopParticle() {
        const listParticle = this.normalEffect.getComponentsInChildren(cc.ParticleSystem);
        listParticle.forEach(item => {
            item.stopSystem();
        });
    },

    startUpdateWinAmount() {
        const speedUpEasing = t => t*t; // constant accelerated
        const slowDownEasing = reverseEasing(t => t*t);
        const halfAmount = 0.5 * this.content.winAmount;
        this.currentTween = cc.tween(this);
        this.currentTween
            .to(0.5*this.animDuration, {currentValue: halfAmount}, {easing: speedUpEasing})
            .to(0.5*this.animDuration, {currentValue: this.content.winAmount}, {easing: slowDownEasing})
            .call(() => {
                this.skippable = false;
                this.currentTween = null;
                this.playSoundEnd();
                this.finish();
            });
        this.currentTween.start();
    },

    playSoundStart(){
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playWinLoop();
        }
    },

    playSoundEnd(){
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopWinLoop();
            this.node.soundPlayer.playWinEnd();
        }
    },

    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmount.stopAllActions();
        this.stopParticle();
        this.winInfo.stopAllActions();
        this.winInfo.runAction(cc.sequence(
            cc.delayTime(this.delayShowTime + this.hideTime),
            cc.callFunc(()=>{
                if (this.node.soundPlayer) {
                    this.node.soundPlayer.stopAllAudio();
                    this.node.soundPlayer.playMainBGM();
                }
                this.label.string = '';
                this.winInfo.opacity = 0;
                this.spineBigWin.active = false;
                this.exit();
            })
        ));
    },
});