const { createLabel } = require("testUtils");
cc.Class({
    extends: require('SlotReelv2'),
    properties: {
        symbolPrefab: cc.Node,
        specialSymbolPrefab: cc.Node
    },
    onLoad() {
        window.test = this;
        this._super();
        this._symbols = [];
        this.initTest();    
    },
    initTest(showNumber = 5, gameConfig = null, col = 0, symbolPrefab = null, isFreeMode = false) {
        this.col = col;
        this.config = gameConfig || this.node.config;
        if (isFreeMode) {
            this.symbolList = this.config.SYMBOL_NAME_LIST_FREE[col];
        } else {
            this.symbolList = this.config.SYMBOL_NAME_LIST[col];
        }
        // this.symbolPrefab = symbolPrefab;
        this.showNumber = showNumber;
        this.showSymbols = [];
        this.totalNumber = this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP + this.config.TABLE_SYMBOL_BUFFER.BOT;
        this.isFreeMode = isFreeMode;
        this.symbolStartShowY = (this.config.TABLE_FORMAT[col] / 2 - 0.5) * this.config.SYMBOL_HEIGHT;

        this.symbolStartY = - (this.config.TABLE_FORMAT[col] / 2 + this.config.TABLE_SYMBOL_BUFFER.BOT - 0.5) * this.config.SYMBOL_HEIGHT;
        for (let i = 0; i < this.totalNumber; ++i) {
            const symbol = cc.instantiate(this.symbolPrefab);
            symbol.name = "Symbol_" + i;
            symbol.parent = this.reel;
            symbol.setPosition(0, this.symbolStartY + i * this.config.SYMBOL_HEIGHT);
            symbol.changeToSymbol(this.getRandomSymbolName());
            this._symbols.push(symbol);
            if (i === 0) {
                this._bottomSymbol = symbol;
            } if (i >= this.config.TABLE_SYMBOL_BUFFER.BOT && this.showSymbols.length < this.showNumber) {
                this.showSymbols.unshift(symbol);
            } else if (i === this.totalNumber - 1) {
                this._topSymbol = symbol;
            }
        }

        this.mode = 'FAST';
        this.curentConfig = this.config.STATS[this.mode];
        this.index = 0;
        this.reset();
    },
    startSpinningWithDelay(evt, delay = 0) {
        this._super(delay);
        this.scheduleOnce(() => {
            this.stopSpinningWithDelay(
                0,
                ["2", "3", "4", "5", "6",].reverse(), // fake data result
                function () { }
            );
        }, 0.2) // time to get response from server: about 200ms
    },
    setMode(evt, mode) {
        this.mode = mode;
    },

    startRespinWithDelay(evt, delay = 0.5, rows) {
        this._tick++;
        this.processRespinData(rows);
        this.fadeOutWinSymbols(delay);
        for (let index = 0; index < this.fallingSymbols.length; index++) {
            const symbol = this.fallingSymbols[index];
            symbol._showY = this._getSymbolShowY(index);
            let time = this._getFallingTime(symbol._hideY - symbol._showY);
            symbol.runAction(cc.sequence(
                cc.delayTime(delay),
                cc.callFunc(() => {
                    symbol.opacity = 255;
                }),
                cc.moveTo(time, symbol.x, symbol._showY).easing(cc.easeQuadraticActionIn()),
                cc.moveTo( this.curentConfig.TIME / 2, symbol.x, symbol._showY + 15).easing(cc.easeQuadraticActionIn()),
                cc.moveTo( this.curentConfig.TIME / 2, symbol.x, symbol._showY).easing(cc.easeQuadraticActionIn()),
                cc.callFunc(()=>{
                    if(index === 0){
                        this._arrangeSymbols();
                    }
                })
            ));
        }
    },
    fadeOutWinSymbols(time) {
        this._toHideSymbols.forEach(sb => sb.runAction(cc.sequence(
            cc.spawn(
                cc.fadeOut(time),
                cc.scaleTo(time, 0.5),
            ),
            cc.callFunc(() => {
                sb.y = sb._hideY;
                // sb.opacity = 255;
                sb.scale = 1;
                sb.changeToSymbol(this.getRandomSymbolName());
            })
        )))
    },
    _randomRows(){
        let rows = []
        for(let i = 0; i < 5; i++){
            let isWin = Math.random() > 0.5;
            if(isWin)rows.push(i);
        }
        if(rows.length === 0){
            rows = [1,3];
        }
        return rows;
    },
    processRespinData(rows) {
        rows = this._randomRows();
        this._maxIndex = Math.max.apply(null, rows);
        let _toHideSymbols = [],
            _toFallSymbols = [],
            _toStaySymbols = [];

        let count = 0;
        for (let index = 0; index < this.showSymbols.length; index++) {
            const symbol = this.showSymbols[index];
            // symbol._currentRow = index;
            if (rows.indexOf(index) > - 1) {
                count++;
                symbol._hideY = this.symbolStartShowY + count * (this.config.SYMBOL_HEIGHT + 100);
                _toHideSymbols.unshift(symbol);
            } else if (index < this._maxIndex) {
                _toFallSymbols.push(symbol);
                symbol._hideY = symbol.y;
            } else {
                _toStaySymbols.push(symbol);
            }
        }
        this._toHideSymbols = _toHideSymbols;
        this.showSymbols = _toHideSymbols.concat(_toFallSymbols).concat(_toStaySymbols);
        this.fallingSymbols = _toHideSymbols.concat(_toFallSymbols);
    },
    _getSymbolShowY(row) {
        return this.symbolStartShowY - row * this.config.SYMBOL_HEIGHT;
    },
    _getFallingTime(height) {
        return (height / this.config.SYMBOL_HEIGHT) * this.curentConfig.TIME * 2;
    },
    _arrangeSymbols() {
        this._symbols.sort(function(a,b){return a.y - b.y});
        this.index = 0;
    },
    circularSymbols() {
        const lastSymbol = this._symbols[this.index % (this.totalNumber)];
        if (!this.showResult) {
            lastSymbol.changeToBlurSymbol(this.getRandomSymbolName());
        } else if (this.stop < this.totalNumber) {
            let isRealSymbol = this.stop >= this.config.TABLE_SYMBOL_BUFFER.TOP && this.stop < this.showNumber + this.config.TABLE_SYMBOL_BUFFER.TOP;
            let symbolValue = this.matrix[this.stop];
            this.step = this.totalNumber + this.showNumber - (this.stop + this.config.TABLE_SYMBOL_BUFFER.BOT);
            if (isRealSymbol) {
                lastSymbol.changeToSymbol(symbolValue);
                this.usingMotionBlur && lastSymbol.stopBlur();
                this.showSymbols.unshift(lastSymbol);
            } else {
                if (this.stop === 0) {
                    this._bottomSymbol = lastSymbol;
                } else if (this.stop === this.totalNumber - 1) {
                    this._topSymbol = lastSymbol;
                }
                lastSymbol.changeToBlurSymbol(symbolValue);
            }
            this.stop++;
        }
        lastSymbol.y = lastSymbol.y + this.config.SYMBOL_HEIGHT * (this.totalNumber);
        this.index++;
    },
});