cc.Class({
    extends: require('SlotSoundPlayer'),

    properties: {
        bgmFree: { default: null, type: cc.AudioClip },

        /**@CutScene */
        jackpotLoop: { default: null, type: cc.AudioClip },
        bigWinLoop: { default: null, type: cc.AudioClip },
        bigWinEnd: { default: null, type: cc.AudioClip },
        totalWin: { default: null, type: cc.AudioClip },
        transitionFree: { default: null, type: cc.AudioClip },
        
        /**@table*/
        sureWin: { default: null, type: cc.AudioClip },
        sfxWinWilds: { default: null, type: cc.AudioClip },

        sfxOpenFlag: { default: null, type: cc.AudioClip },
        sfxKickBall: { default: null, type: cc.AudioClip },
        sfxExpandWild: { default: null, type: cc.AudioClip },
        sfxMultiply: { default: null, type: cc.AudioClip },
        sfxWinBigWild: { default: null, type: cc.AudioClip },

        sfxWinLines: {
            default: [],
            type: cc.AudioClip,
        },
    },

    playMainBGM(gameMode) {
        const currentGameMode = gameMode || this.node.gSlotDataStore.currentGameMode;
        let sound = this.bgmMain;
        let musicVolume = this.MUSIC_VOLUME;

        if (currentGameMode === "freeGame") {
            if (this.bgmFree) sound = this.bgmFree;
        }
        this.playMusic(sound, true, musicVolume);
    },

    playWinLoop(isJackpot = false) {
        if (this.bigWinLoop && this.jackpotLoop) {
            this._audioWinLoopValue && this.stopSound(this._audioWinLoopValue);
            this._audioWinLoopValue = this.playSound(isJackpot ? this.jackpotLoop : this.bigWinLoop, true);
        }
    },

    stopWinLoop() {
        this._audioWinLoopValue && this.stopSound(this._audioWinLoopValue);
    },

    playWinEnd() {
        if (this.bigWinEnd) {
            this._audioWinEndValue && this.stopSound(this._audioWinEndValue);
            this._audioWinEndValue = this.playSound(this.bigWinEnd, false, 1);
        }
    },

    stopWinEnd() {
        this._audioWinEndValue && this.stopSound(this._audioWinEndValue);
    },

    playTotalWin() {
        this._audioTotalWin = this.playSFX(this.totalWin);
    },
    
    stopTotalWin() {
        this._audioTotalWin && this.stopSound(this._audioTotalWin);
    },

    playSfxOpenFlag() {
        if (!this.sfxOpenFlag) return;
        this._audioOpenFlag && this.stopSound(this._audioOpenFlag);
        this._audioOpenFlag = this.playSFX(this.sfxOpenFlag);
    },

    playSfxPlayerKick() {
        if (!this.sfxKickBall) return;
        this._audioPlayerKick = this.playSFX(this.sfxKickBall);
    },

    playSfxExpandWild() {
        if (!this.sfxExpandWild) return;
        this._audioExpandWild && this.stopSound(this._audioExpandWild);
        this._audioExpandWild = this.playSFX(this.sfxExpandWild);
    },

    playSfxMultiply() {
        if (!this.sfxMultiply) return;
        this._audioSfxMultiply && this.stopSound(this._audioSfxMultiply);
        this._audioSfxMultiply = this.playSFX(this.sfxMultiply);
    },

    playSfxWinBigWild() {
        if (!this.sfxWinBigWild) return;
        this._audioSfxWinBigWild && this.stopSound(this._audioSfxWinBigWild);
        this._audioSfxWinBigWild = this.playSFX(this.sfxWinBigWild);
    },

    stopSfxWinBigWild() {
        this._audioSfxWinBigWild && this.stopSound(this._audioSfxWinBigWild);
    },

    playSfxWinWild() {
        if (!this.sfxWinWilds) return;
        this._audioWinWild = this.playSFX(this.sfxWinWilds);
    },

    playSfxSureWin() {
        if (!this.sureWin) return;
        this._audioSureWin = this.playSFX(this.sureWin);
    },

    playSoundWinLine(rate) {
        let audio = this.sfxWinLines[rate];
        if (audio) {
            this._audioWinLine && this.stopSound(this._audioWinLine);
            this._audioWinLine = this.playSFX(audio);
        }
    },

    stopSoundWinLine() {
        this._audioWinLine && this.stopSound(this._audioWinLine);
    },

    playTransitionFree() {
        if (this.transitionFree) {
            this._audioTransitionFree && this.stopSound(this._audioTransitionFree);
            this._audioTransitionFree = this.playSFX(this.transitionFree);
        }
    },

    stopTransitionFree() {
        this._audioTransitionFree && this.stopSound(this._audioTransitionFree);
    },
});