
cc.Class({
    extends: require('TrialButtonv2'),

    onLoad() {
        this._super();
        this.node.on("ON_CLICK_REAL_BUTTON", this.onClickRealMode, this);
    },
    onPlayRealButtonClicked() {
        const cutSceneMgr = this.node.mainDirector.director.cutscene.getComponent("CutsceneControl");
        if (cutSceneMgr.isDisplayCutscene()) return;
        this._super();
    },

    onClickRealMode() {
        if (this.playTrialButton) {
            this.playTrialButton.active = true;
        }
        if (this.playRealButton) {
            this.playRealButton.active = false;
        }
        if (this.displayRootNode) {
            this.displayRootNode.active = false;
        }
        if (this.playTrialButton) {
            this.playTrialButton.getComponent(cc.Button).interactable = false;
            this.node.runAction(cc.sequence(
                cc.delayTime(2.0),
                cc.callFunc(() => {
                    this.playTrialButton.getComponent(cc.Button).interactable = true;
                })
            ));
        }
        this.playAnimRealButton();
        this.completeTrialSessionCount++;
    },
    playAnimTrialButton() {
        if (this.animationBtn) this.animationBtn.node.active = true;
        this._super();
    },
    playAnimRealButton() {
        if (this.animationBtn) this.animationBtn.node.active = false;
    },
    onEnableButtons(isOn) {
        if (this.playingDemo) isOn = false;
        cc.log('_showTrialButtons', isOn);
        this.playRealButton.getComponent(cc.Button).interactable = isOn;
        this.playTrialButton.getComponent(cc.Button).interactable = isOn;
    },
});
