
cc.Class({
    extends: cc.Component,

    properties: {

    },

    onLoad() {
        this.node.on("MOVE_PARTICLES", this.moveParticle, this);
        this.node.on('CLEAR_PARTICLES', this.clearParticles, this);

        this.poolFactory = this.node.poolFactory;
    },

    moveParticle(data, callback) {
        const { startNode, endNode, movingTime, mul, particleType } = data;
        const worldStartPos = startNode.convertToWorldSpaceAR(cc.v2(0, 0));
        const newStartPos = this.node.convertToNodeSpaceAR(worldStartPos);

        const worldEndPos = endNode.convertToWorldSpaceAR(cc.v2(0, 0));
        const newEndPos = this.node.convertToNodeSpaceAR(worldEndPos);


        const movingParticle = this.poolFactory.getObject("MovingParticle9877");
        if (!movingParticle) return;
        movingParticle.parent = this.node;
        movingParticle.active = true;
        movingParticle.emit("RESET")
        movingParticle.emit("INIT", { mul, particleType })
        movingParticle.setPosition(newStartPos.x, newStartPos.y);
        movingParticle["tweenMoveParticle"] && movingParticle["tweenMoveParticle"].stop();
        movingParticle["tweenMoveParticle"] = cc.tween(movingParticle)
            .to(movingTime, { position: newEndPos })
            .call(() => {
                movingParticle.emit("IMPACT");
                callback && callback();
            })
            .delay(0.5)
            .call(() => {
                movingParticle.active = false;
                this.poolFactory.removeObject(movingParticle);
            });
        movingParticle["tweenMoveParticle"].start();
    },
    clearParticles() {
        const paricles = this.node.children;
        for (let i = 0; i < paricles.length; i++) {
            const particle = paricles[i];
            if (particle) {
                particle.emit("RESET")
                particle["tweenMoveParticle"] && particle["tweenMoveParticle"].stop();
                particle["tweenMoveParticle"] = null;
                this.poolFactory.removeObject(particle);
            }
        }
        this.node.removeAllChildren();
    },
});
