
cc.Class({
    extends: cc.Component,

    properties: {
        rabbitNode: cc.Node,
        swallowNode: cc.Node,
        mascotJumHolder: cc.Node,
    },

    onLoad() {
        this.node.on("PLAY_ANIM_JUMP", this.playAnimJump, this);
        this.node.on("PLAY_ANIM_WIN", this.playAnimWin, this);
        this.node.on("HIDE_ALL_MASCOT", this.hideAllMascot, this);
        this.node.on("RESET", this.reset, this);

        this.animRabbit = this.rabbitNode.getComponent(sp.Skeleton);
        this.animRabbit.setAnimation(0, 'Idle', true);

        this.animSwallow = this.swallowNode.getComponent(sp.Skeleton);
        this.animSwallow.setAnimation(0, 'Idle', true);
    },

    playAnimWin() {
        // if (!this.animRabbit.isJump) {
        //     this.animRabbit.setAnimation(0, 'Win', false);
        //     this.animRabbit.addAnimation(0, 'Idle', true);
        // }

        // if (!this.animSwallow.isJump) {
        //     this.animSwallow.setAnimation(0, 'Win', false);
        //     this.animSwallow.addAnimation(0, 'Idle', true);
        // }
    },

    playAnimJump(isRabbit) {
        const _animNode = isRabbit ? this.rabbitNode : this.swallowNode;
        const _anim = isRabbit ? this.animRabbit : this.animSwallow;
        _anim.isJump = true;
        _anim.setAnimation(0, 'Jump', false)
        _anim.setCompleteListener(() => {
            _animNode.active = false;
            _anim.setCompleteListener(() => { });
        });
    },
    showMascot() {
        if (this.animRabbit.isJump) {
            this.animRabbit.isJump = false;
            this.rabbitNode.active = true;
            this.rabbitNode.parent = this.mascotJumHolder;
            this.scheduleOnce(() => {
                this.rabbitNode.parent = this.node;
            }, 0.2)
            this.animRabbit.setCompleteListener(() => { });
            this.animRabbit.setAnimation(0, 'Appear', false);
            this.animRabbit.addAnimation(0, 'Idle', true);
        }
        if (this.animSwallow.isJump) {
            this.animSwallow.isJump = false;
            this.swallowNode.active = true;
            this.swallowNode.parent = this.mascotJumHolder;
            this.scheduleOnce(() => {
                this.swallowNode.parent = this.node;
            }, 0.2)
            this.animSwallow.setCompleteListener(() => { });
            this.animSwallow.setAnimation(0, 'Appear', false);
            this.animSwallow.addAnimation(0, 'Idle', true);
        }
    },
    hideAllMascot() {
        this.rabbitNode.active = false;
        this.animRabbit.isJump = true;
        this.swallowNode.active = false;
        this.animSwallow.isJump = true;
    },
    reset() {
        this.showMascot();
    },

});
