const {formatMoney} = require('utils');
cc.Class({
    extends: require('JackpotWinv2'),

    properties: {
        overlay: cc.Node,
        spinJackpot: cc.Node,
        fonts: [cc.Font]
    },

    onLoad() {
        this._super();
        this.jackpotSpineSkeleton = this.spinJackpot.getComponent(sp.Skeleton);
        this.spinJackpot.active = false;
    },

    enter() {
        this.overlay.opacity = 0;
        const { jackpotType } = this.content;
        this.playSoundStart();
        this.spinJackpot.active = true;
        this.jackpotSpineSkeleton.setSkin(jackpotType === 0 ? 'Final' : 'Semi');
        this.jackpotSpineSkeleton.setSlotsToSetupPose();
        this.label.font = this.fonts[jackpotType];
        this.jackpotSpineSkeleton.setAnimation(0, 'Appear', false);
        this.jackpotSpineSkeleton.addAnimation(0, 'Idle', true);
        this.scheduleOnce(() => {
            const scaleTime = 0.2;
            this.winAmount.runAction(cc.repeatForever(cc.sequence(
                cc.scaleTo(scaleTime, 1.2),
                cc.scaleTo(scaleTime, 1),
            )));
            this.overlay.runAction(cc.fadeTo(0.3, 200));
            this.initValue();
            this.startParticle();
            this.startUpdateWinAmount();
        }, 1.3);
        this.label.string = '';
    },

    playSoundStart(){
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopAllAudio();
            this.node.soundPlayer.playWinLoop(true);
        }
    },

    playSoundEnd(){
        if (this.node.soundPlayer) {
            this.node.soundPlayer.stopWinLoop();
            this.node.soundPlayer.playWinEnd();
        }
    },

    startParticle() {
    },

    stopParticle() {
    },

    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmount.stopAllActions();
        this.stopParticle();
        this.winInfo.runAction(cc.sequence(
            cc.delayTime(this.delayShowTime + this.hideTime),
            cc.callFunc(()=>{
                if (this.node.soundPlayer) {
                    this.node.soundPlayer.stopAllAudio();
                    this.node.soundPlayer.playMainBGM();
                }
                this.label.string = '';
                this.spinJackpot.active = false;
                this.exit(); 
            })
        ));
    },
});