const { formatMoney } = require("utils");

const ANIMATION = {
    SWALLOW_RESULT: 'swallow_Result',
    RABBIT_RESULT: 'rabbit_Result',
}
cc.Class({
    extends: cc.Component,

    properties: {
        overlay: cc.Node,
        bigWildTable: cc.Node,
        stickyWild: cc.Node,
        battleAnim: sp.Skeleton,
        animMultiply: sp.Skeleton,
        labelWinAmt: cc.Label,

        _currentValue: 0,
        currentValue: {
            get() {
                return this._currentValue;
            },
            set(value) {
                if (this._currentValue !== value) {
                    this._currentValue = value;
                    this._updateLabelWinAmount();
                }
            },
            visible: false
        }
    },

    onLoad() {
        this.node.on("SHOW", this.show, this);
        this.node.on("FINISH", this.finish, this);
        this.overlay.active = false;
    },
    show(data) {
        const { freeBattleData, isBigWin, winAmount } = data;
        const endStepData = freeBattleData[freeBattleData.length - 1];
        const { multiply, isRabbit } = endStepData;
        const winAmtBeforeUpdate = winAmount / multiply;
        if (multiply <= 1) return;
        this.node.opacity = 255;
        this.overlay.active = true;
        this.bigWildTable.active = false;
        this.stickyWild.active = false;
        this.animMultiply.node.active = false;
        this.battleAnim.node.active = true;
        this.currentValue = winAmtBeforeUpdate;
        this.node.soundPlayer && this.node.soundPlayer.playSfxByID('9881_sfx_applause_4_finish');
        this.playAnimShowMultiply(multiply);
        this._updateLabelWinAmount();
        this.battleAnim.setSkin('default');
        this.battleAnim.setAnimation(0, isRabbit ? ANIMATION.RABBIT_RESULT : ANIMATION.SWALLOW_RESULT, true);
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = cc.tween(this.node)
            .delay(0.2)
            .call(() => {
                this.node.soundPlayer.playSfxByID('9881_sfx_match_end');
            })
            .delay(1.5)
            .call(() => {
                this.node.soundPlayer.setMusicVolume(0);
            })
            .delay(1.5)
            .call(() => {
                this.node.soundPlayer.playSfxByID('9881_sfx_multiply_hit');
            })
            .delay(0.3)
            .call(() => {
                this.updateWinAmt(winAmount, 0.8);
            })
            .delay(isBigWin ? 0.5 : 1.5)
            .call(() => {
                this.hide();
                this.tweenShow = null;
            })
            .start();

    },
    playAnimShowMultiply(mul) {
        if (mul == 1) return;
        const animSkin = 'x' + mul;
        this.animMultiply.node.active = true;
        this.animMultiply.setSkin(animSkin);
        this.animMultiply.setAnimation(0, 'Result', false)
    },
    updateWinAmt(winAmount, time) {
        this._tweenValue && this._tweenValue.stop();
        this._tweenValue = cc.tween(this)
            .to(time, { currentValue: winAmount }, { easing: 'sineInOut' })
            .start();
    },
    finish(data, callBack) {
        this.callBack = callBack;
        const { freeBattleData } = data;
        const endStepData = freeBattleData[freeBattleData.length - 1];
        const { multiply } = endStepData;
        if (multiply <= 1) {
            this.hide();
        }
    },
    hide() {
        this.bigWildTable.active = true;
        this.stickyWild.active = true;
        if (this.node.soundPlayer) {
            this.node.soundPlayer.resumeMusic();
            this.node.soundPlayer.playMainBGM('freeGame');
        }
        this._tweenValue && this._tweenValue.stop();
        this._tweenValue = null;
        this.tweenShow && this.tweenShow.stop();
        this.tweenShow = null;
        this.labelWinAmt.string = ''
        this.node.opacity = 0;
        this.node.active = false;

        this.callBack && this.callBack();
        this.callBack = null;
    },
    _updateLabelWinAmount() {
        this.labelWinAmt.string = this.currentValue > 0 ? formatMoney(this._currentValue) : "";
    },
    _getSoundMul(multiply) {
        if (multiply == 1) return '9881_sfx_multiply_1';
        if (multiply == 2) return '9881_sfx_multiply_2';
        if (multiply == 4) return '9881_sfx_multiply_3';
        if (multiply == 8) return '9881_sfx_multiply_4';
        return '9881_sfx_multiply_4';
    }
});
