
cc.Class({
    extends: require('TutorialMgr'),
    startTutorial() {
        if (this.waitForStart) return;

        if (!this.inited) {
            this.init();
        }
        this.node.active = true;
        this.node.opacity = 255;
        this.endTutorialData = false;
        this._isSpinning = false;
        this.initJackpot();
        this.slotButtons.forEach(sb => {
            sb.node.emit("ENABLE_SPIN_KEY", false);
            sb.node.emit("DISABLE_ALL_BUTTONS");
        });
        this._lockTouch();
        this.waitForStart = true;
        this.node.stopAllActions();
        if (this.mainDirector && this.mainDirector.node) {
            this.mainDirector.node.emit('DISABLE_BUTTON_CONTROL');
            this.mainDirector.currentGameMode.emit('SPIN_DISABLE');
            this.mainDirector.disableBet(true);
        }
        this.node.runAction(
            cc.sequence(
                cc.delayTime(this.timeDelayStart),
                cc.callFunc(() => {
                    this.finished = false;
                    this.reset();
                    if (this.forceSkipTutorial) {
                        this.skipTutorial();
                    } else {
                        this.run();
                    }
                    if (this.mainDirector && this.mainDirector.node) {
                        this.mainDirector.currentGameMode.emit('START_AUTO_SPIN_TRIAL');
                        this.slotButtons.forEach(sb => {
                            sb.node.emit("STOP_AUTO_SPIN_HIDE");
                        });
                    }
                    this.waitForStart = false;
                }),
                cc.delayTime(1),
                cc.callFunc(() => {
                    this._unlockTouch();
                }),
            ));
    },
});
