
cc.Class({
    extends: require('MainWriter9881'),

    makeScriptResultReceive() {
        const { matrix0, matrix, jackpot, bigWild } = this.node.gSlotDataStore.playSession;
        let listScript = [];
        if (jackpot) {
            const jpInfo = jackpot[jackpot.length - 1].split(';');
            listScript.push({
                command: "_updateValueJP",
                data: {
                    jpValue: Number(jpInfo[1]),
                    jpType: 0,
                }
            });
            listScript.push({
                command: "_pauseUpdateJP"
            });
        }
        listScript.push({
            command: "_setupBigWild",
            data: { isResume: false, matrix, matrix0, bigWild },
        });
        listScript.push({
            command: "_resultReceive",
            data: { matrix0, matrix },
        });
        listScript.push({
            command: "_showResult",
            data: matrix0,
        });
        return listScript;
    },

    makeScriptShowResults() {
        const { matrix, winAmount, payLines, freeBattleData,
            winAmountPS, freeGameRemain, winJackpotAmount, bigWild } = this.node.gSlotDataStore.playSession;
        const { currentBetData } = this.node.gSlotDataStore.slotBetDataStore.data;
        const totalBet = this._getTotalBetAmount();
        const isJackpotWin = winJackpotAmount && winJackpotAmount > 0;
        const isBigWin = winAmount && winAmount >= currentBetData * 20;
        const { isAutoSpin, modeTurbo } = this.node.gSlotDataStore;
        const rate = winAmount / totalBet;
        const isFTR = this.node.gSlotDataStore.gameSpeed === this.node.config.GAME_SPEED.INSTANTLY;
        const timeUpdateWinAmt = isFTR || isJackpotWin ? 300 : this._getTimeUpdateWinAmount(rate) * 1000;
        const listScript = [];

        listScript.push({
            command: "_setUpPaylines",
            data: { matrix, payLines, rate, isAutoSpin },
        });
        if (isJackpotWin) {
            if (bigWild && bigWild.length) {
                listScript.push({
                    command: "_delayTimeScript",
                    data: 1
                });
            }
            listScript.push({
                command: "_showJackpotPayLine",
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "JackpotWin",
                    content: {
                        winAmount: winJackpotAmount,
                        currentBetData
                    }
                }
            });
            listScript.push({
                command: '_updateWinningAmount',
                data: { winAmount: winAmountPS - winAmount, time: 300 }
            });
            listScript.push({
                command: "_resumeUpdateJP",
            });
        }
        if (freeBattleData) {
            listScript.push({
                command: "_delayTimeScript",
                data: 0.5
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "FreeGameBattle",
                    content: {
                        freeBattleData
                    }
                }
            });
            listScript.push({
                command: "_showTotalBattle",
                data: {
                    freeBattleData,
                    winAmount,
                    isBigWin
                }
            });
            listScript.push({
                command: "_blinkAllPaylines",
            });
            listScript.push({
                command: "_hideTotalBattle",
                data: {
                    freeBattleData,
                    winAmount,
                    isBigWin
                }
            });
            if (isBigWin) {
                // listScript.push({
                //     command: "_delayTimeScript",
                //     data: 0.2
                // });
                listScript.push({
                    command: "_showUnskippedCutscene",
                    data: {
                        name: "WinEffect",
                        content: {
                            winAmount,
                            currentBetData,
                            matrix,
                            freeBattleData,
                            rate,
                        }
                    }
                });
            }
            listScript.push({
                command: "_playSoundWinLine",
                data: rate
            });
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
            });
        } else {
            if (isBigWin) {
                if (modeTurbo || isFTR) {
                    listScript.push({
                        command: "_updateWinningAmount",
                        data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
                    });
                }
                if (bigWild && bigWild.length) {
                    listScript.push({
                        command: "_delayTimeScript",
                        data: 1
                    });
                }
                listScript.push({
                    command: "_blinkAllPaylines",
                });
                listScript.push({
                    command: "_showUnskippedCutscene",
                    data: {
                        name: "WinEffect",
                        content: {
                            winAmount,
                            currentBetData,
                            matrix,
                            freeBattleData,
                            rate,
                        }
                    }
                });
                listScript.push({
                    command: "_updateWinningAmount",
                    data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
                });
            }
        }

        if (!isBigWin && payLines) {
            listScript.push({
                command: "_playSoundWinLine",
                data: rate
            });
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: timeUpdateWinAmt, rate }
            });
            if (bigWild && bigWild.length) {
                listScript.push({
                    command: "_delayTimeScript",
                    data: 1
                });
            }
            listScript.push({
                command: "_blinkAllPaylines",
            });
            listScript.push({
                command: "_showNormalPayline",
            });
        }
        if (!freeGameRemain || freeGameRemain <= 0) {
            const rate2 = winAmountPS / totalBet;
            listScript.push({
                command: "_delayTimeScript",
                data: 1
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: "TotalWinPanel",
                    content: {
                        winAmount: winAmountPS,
                        rate: rate2,
                    }
                }
            });
            listScript.push({
                command: "_gameExit",
            });
        } else {
            if (freeGameRemain <= this.node.gSlotDataStore.spinTimes) {
                this.node.gSlotDataStore.spinTimes = freeGameRemain;
                listScript.push({
                    command: "_updateSpinTimes",
                    data: freeGameRemain
                });
            }
            listScript.push({
                command: "_gameRestart"
            });
        }
        return listScript;
    },


});
